import React, { FC, ReactElement } from "react";
import { ImRadioChecked, ImRadioUnchecked } from "react-icons/im";
import { MdAdd, MdDelete, MdExpandMore } from "react-icons/md";
import { ContractItemList } from "./Contract";

// props for the component
interface AppProps {
  title: string;
  removeData: (id: string) => void;
  addItem: () => void;
  data: ContractItemList[];
  strict: boolean;
  changeStrict: (option: boolean) => void;
  expandClick?: () => void;
  loading?: boolean;
}

const ContractItem: FC<AppProps> = ({
  title,
  strict,
  removeData,
  addItem,
  changeStrict,
  data,
  expandClick,
  loading,
}): ReactElement => {
  // const [open, setOpen] = useState(false);
  const removeDataHandler = (element: ContractItemList) => {
    if (
      window.confirm(
        `Are you sure you want to remove ${element.title} from ${title} list?`
      ) === true
    )
      removeData(element.id);
  };

  return (
    <div className="bg-white border-b p-3 ">
      <div
        onClick={() => {
          // setOpen(!open);
          if (expandClick) expandClick();
        }}
        className="flex flex-row items-center justify-between cursor-pointer hover:text-blue-600"
      >
        <div className="flex flex-row items-center gap-3 group cursor-pointer">
          <div>
            {/* <div
              className={`bg-blue-50 text-blue-600  rounded-full flex items-center justify-center h-10 w-10  cursor-pointer group-hover:bg-blue-100 group-hover:text-blue-600`}
            >
              <MdExpandMore className={`text-5xl transition transform `} />
            </div> */}
          </div>
          <span
            className={`text-gray-900 font-extrabold text-xl group-hover:text-blue-600`}
          >
            {title}
          </span>
        </div>
        <div className="text-right bg-gray-50 text-gray-800 rounded-xl px-3 py-1 flex flex-row items-center">
          {loading === undefined ? (
            <span className="text-sm ">
              {strict === false && "All " + title + "(s), with "}
              <b>{data.length}</b> Priorities
            </span>
          ) : loading === false ? (
            <span className="text-sm ">
              {strict === false && "All " + title + "(s), with "}
              <b>{data.length}</b> Priorities
            </span>
          ) : null}
        </div>
      </div>
      {/* {open === true && ( */}
      <div className="my-2 rounded bg-white px-3 py-2 animate__animated animate__fadeIn">
        <div>
          <span className="">Select priority</span>
        </div>
        <div className="bg-gray-50 border rounded-md w-full grid grid-cols-12">
          <div className="col-span-10 p-1">
            {loading !== undefined && loading === true && (
              <div className="animate__animated animate__fadeIn animate__infinite inline-block text-gray-600">
                Loading...
              </div>
            )}

            {data.length <= 0 ? (
              <span className=" inline-block w-auto  rounded-full text-md bg-gray-500  m-1 text-white">
                <div className="flex items-center">
                  <span className="flex-1 p-0.5 px-2">
                    All {title}
                    {`(s)`}
                  </span>
                </div>
              </span>
            ) : (
              data.map((itm) => (
                <span
                  key={itm.id}
                  className=" inline-block w-auto  rounded-full text-md bg-gray-500  m-1 text-white"
                >
                  <div className="flex items-center">
                    <span className="flex-1 p-0.5 px-2">{itm.title}</span>
                    <button
                      className="bg-red-400 text-xl p-1 rounded-full"
                      onClick={removeDataHandler.bind(this, itm)}
                    >
                      <MdDelete />
                    </button>
                  </div>
                </span>
              ))
            )}

            {strict === false && data.length > 0 && (
              <span className=" inline-block w-auto  rounded-full text-md bg-gray-500  m-1 text-white">
                <div className="flex items-center">
                  <span className="flex-1 p-0.5 px-2">
                    and others {title}
                    {`(s)`}
                  </span>
                </div>
              </span>
            )}
          </div>
          <div className="col-span-2 flex flex-row items-center- justify-end">
            <button
              onClick={addItem.bind(this)}
              className="bg-blue-200 text-blue-900 hover:bg-blue-700 px-3 py-3 h-max rounded-r-md text-center w-max hover:text-white font-bold text-base flex flex-row items-center cursor-pointer"
            >
              {/* <MdAdd className="text-3xl" /> */}
              <span>Select location</span>
            </button>
          </div>
        </div>

        {data.length >= 1 && (
          <div className="mt-2">
            <div>
              <span className="text-gray-700">
                Can we place you in another {title} when no places available for
                selected {title}?
              </span>
            </div>
            <div className="flex flex-row items-center gap-10 mt-2">
              <div
                title="If there are no vacant place, the system will place you in another available places"
                onClick={() => changeStrict(false)}
                className={`flex flex-row items-center gap-2 ${
                  strict === false ? "text-blue-600" : "text-gray-500"
                } cursor-pointer`}
              >
                {strict === false ? (
                  <>
                    <ImRadioChecked className="text-3xl" />
                    <span className="font-extrabold">Yes</span>
                  </>
                ) : (
                  <>
                    <ImRadioUnchecked className="text-3xl" />
                    <span className="font-extrabold">Yes</span>
                  </>
                )}
              </div>
              <div
                title="The system will place you only if there are available vacant places for the selected location"
                onClick={() => changeStrict(true)}
                className={`flex flex-row items-center gap-2 ${
                  strict === true ? "text-blue-600" : "text-gray-500"
                } cursor-pointer`}
              >
                {strict === true ? (
                  <>
                    <ImRadioChecked className="text-3xl" />
                    <span className="font-extrabold">No</span>
                  </>
                ) : (
                  <>
                    <ImRadioUnchecked className="text-3xl" />
                    <span className="font-extrabold">No</span>
                  </>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
      {/* // )} */}
    </div>
  );
};
export default ContractItem;
