declare global {
  interface Window {
    Peer?: any;
  }
}

let myPeer: any;

/**
 * Initialize the new Peer
 * @getPeerId => send the peer id
 * @addVideoStream => when the user call us it return their stream
 * @stream my local stream
 * */
export const connectWithMyPeer = (
  getPeerId: (id: string) => void,
  addVideoStream: (stream: any) => void,
  localStream: any
) => {
  myPeer = new window.Peer(undefined, {
    path: "/peerjs",
    host: "/",
    port: "3001",
  });

  myPeer.on("open", (id: any) => {
    console.log("succesfully connected with peer server");
    getPeerId(id);
  });

  myPeer.on("call", (call: any) => {
    call.answer(localStream);
    call.on("stream", (incomingStream: any) => {
      addVideoStream(incomingStream);
      // }
    });
  });
};

export const connectToNewUser = (
  peer_id: string,
  localStream: any,
  addVideoStream: (incoming_stream: any) => void
) => {
  const call = myPeer.call(peer_id, localStream);
  call.on("stream", (incomingStream: any) => {
    addVideoStream(incomingStream);
  });
};

export const clearGroupData = () => {
  myPeer.destroy();
};

export const removeInactiveStream = (data: any) => {};
