import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Proctoring,
  Socket,
  FC_SaveEvent,
  FT_AddRules,
  FT_SaveEvent,
  FC_AddRules,
} from "../../actions";
import {
  EventInterface,
  Events,
  EventsLabels,
  LiveServer__Candidate_Details,
  LiveServer__Room,
} from "../../interfaces/events";
import { StoreState } from "../../reducers";
import { ProctoringCandidateOnline } from "../ProctoringCandidateOnline/ProctoringCandidateOnline";
import { CountDown } from "../../components/CountDown/CountDown";
// import Alert, { AlertInterface } from "../../components/Alert/Alert";

// props for the component
interface AppProps {
  auth: Auth;
  proctoring: Proctoring;
  socket: Socket;
  FC_SaveEvent: FT_SaveEvent;
  FC_AddRules: FT_AddRules;
}

interface AppState {
  start_exam: boolean;
  start_time: string;
  duration: number;
  added_minutes: number;
  group_id: string;
  exam_done: boolean;
}

class _ProctoringTestingExam extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      start_exam: false,
      start_time: "",
      duration: 0,
      added_minutes: 0,
      group_id: "",
      exam_done: false,
    };
  }

  componentDidMount() {
    this.props.FC_AddRules({
      allow_to_leave_browser: true,
      copy_enabled: true,
      paste_enabled: false,
      right_click_enabled: false,
      stop_candidate_when_comeback: false,
    });
  }

  doneExam = () => {
    let room_id = "618e42f447194a7f9cf673a2";

    let data = {
      application_session_id: this.props.auth.application_session?._id,
      description: "Done the exam",
      done_by: "System",
      event_type_id: EventsLabels.DONE_EXAM,
      exam_id: "exam_id", //this.props.proctoring.exam.
      position_id: "position_id",
      room_id: room_id,
      time: new Date().toString(),
      user_id: this.props.auth.user.user_id,
    } as EventInterface;

    this.props.FC_SaveEvent(data, (status: boolean, error: string) => {});
    this.props.socket.socket.emit(Events.EXAM_DONE_SERVER, data);
  };

  timeOutExam = () => {
    this.setState({
      exam_done: true,
    });
    let room_id = "618e42f447194a7f9cf673a2";

    let data = {
      application_session_id: this.props.auth.application_session?._id,
      description: "Time out",
      done_by: "System",
      event_type_id: EventsLabels.TIME_OUT,
      exam_id: "exam_id", //this.props.proctoring.exam.
      position_id: "position_id",
      room_id: room_id,
      time: new Date().toString(),
      user_id: this.props.auth.user.user_id,
    } as EventInterface;

    this.props.FC_SaveEvent(data, (status: boolean, error: string) => {});
    this.props.socket.socket.emit(Events.TIME_OUT_SERVER, data);
  };

  getExams = (status: boolean, exam: any) => {
    console.log("GET EXAM DETAILS");
    console.log({ exam, status });
  };

  requestExam = (
    exam_id: string,
    getExam: (status: boolean, exam: any) => void
  ) => {
    this.props.socket.socket.emit(Events.REQUEST_EXAM_SERVER, exam_id, getExam);
  };

  render() {
    return (
      <div className="p-8">
        <div className="p-3">
          <button
            className="p-2 px-3 bg-yellow-500"
            onClick={this.requestExam.bind(
              this,
              "61cafae0812eee40efce1d21",
              this.getExams
            )}
          >
            Load exam
          </button>
        </div>
        <ProctoringCandidateOnline
          homeProctoring={false}
          candidateProctoringDetails={(
            candidate_data: LiveServer__Candidate_Details
          ) => console.log({ candidate_data })}
          room_id="618e42f447194a7f9cf673a2"
          exam_id="618e570013764995c7ef1e01"
          position_id="61549482e69bc404709d936f"
          application_session_id="6137030f311c9b303d7c6c2c"
          // Functions
          restartExam={(reason: string) => {
            console.log(`Exam restartExam: ${reason}`);
            this.setState({ exam_done: false });
          }}
          startExam={() => {
            console.log("Exam startExam");
            this.setState({
              start_exam: true,
              exam_done: false,
            });
          }}
          stopExam={() => {
            console.log(`Exam restartExam`);
            this.setState({ start_exam: false, exam_done: false });
          }}
          candidateStopExam={(reason: string) =>
            console.log(`candidateStopExam: ${reason}`)
          }
          candidateContinueExam={(reason: string) =>
            console.log(`candidateContinueExam: ${reason}`)
          }
          removeClient={(reason: string) =>
            console.log(`Exam removeClient: ${reason}`)
          }
          examAlreadyDone={() => console.log("Exam already done")}
          joinedRoom={(data: LiveServer__Room) => {
            console.log({ Joined_Room: data });
            this.setState({
              group_id: data.group_id,
              exam_done: false,
            });
          }}
          changeTime={(timer: {
            duration: number;
            start_time: string;
            added_minutes: string;
          }) => {
            console.log({ changeTime: timer });
            this.setState({
              duration: timer.duration,
              start_time: timer.start_time,
            });
          }}
        >
          <div className="p-5">
            {this.state.start_exam === true && (
              <div className="text-8xl">
                <CountDown
                  start_time={this.state.start_time}
                  duration_min={this.state.duration}
                  timeout={() => this.timeOutExam()}
                  returnString={true}
                  labels
                  days
                />
              </div>
            )}
            <h1 className="text-4xl mb-2">{this.props.auth.user.user_id}</h1>

            {this.state.exam_done === true && (
              <div className="p-4 bg-red-500 text-white text-center text-3xl">
                Exam Done
              </div>
            )}

            <button
              className="btn bg-red-500 text-white"
              onClick={() => this.doneExam()}
            >
              Done exam
            </button>
            <hr />
            <div className="p-2">
              <input
                type="text"
                name=""
                id=""
                className="form p-2 w-full h-20"
              />
            </div>
            <div className="border p-3 w-1/2 bg-gray-900 max-h-96 overflow-scroll">
              {this.props.proctoring.events.map((event, i) => (
                <div className=" border-b border-gray-500" key={i}>
                  <p className="font-xs text-blue-100">
                    <b className=" font-bold text-red-400">
                      {event.event_type_id}:
                    </b>{" "}
                    {event.description}
                  </p>
                </div>
              ))}
            </div>
          </div>
        </ProctoringCandidateOnline>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  proctoring,
  socket,
}: StoreState): { auth: Auth; proctoring: Proctoring; socket: Socket } => {
  return { auth: auth, proctoring, socket };
};

export const ProctoringTestingExam = connect(mapStateToProps, {
  FC_SaveEvent,
  FC_AddRules,
})(_ProctoringTestingExam);
