import { Action, ActionTypes, candidateApplyStoreInterface } from "../actions";
import { ApplicationStepsTypes } from "../containers/Application/ApplicationSteps/ApplicationSteps";

// default state
const defaultState: candidateApplyStoreInterface = {
  user_id: null,
  currentStep: ApplicationStepsTypes.Step1,
  selectedPosition: null,
  exams: null,
  contract: null,
  completed: false,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const candidateApplyReducer = (
  state: candidateApplyStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.INITIALIZE_APPLICATION_PROCESS:
      return {
        ...state,
        selectedPosition: action.payload.position,
        user_id: action.payload.user_id,
      };
    case ActionTypes.SET_CANDIDATE_APPLICATION_STEP:
      return {
        ...state,
        currentStep: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_EXAM_CENTER:
      return {
        ...state,
        exams:
          state.exams !== null
            ? [
                ...state.exams.filter(
                  (itm) => itm.exam.course_id !== action.payload.exam.course_id
                ),
                action.payload,
              ]
            : [action.payload],
      };
    case ActionTypes.SET_COMPLETE_APPLY:
      return {
        ...state,
        completed: action.payload,
      };
    case ActionTypes.CLEAN_CANDIDATE_APPLY:
      return {
        ...state,
        user_id: null,
        currentStep: ApplicationStepsTypes.Step1,
        selectedPosition: null,
        exams: null,
        contract: null,
        completed: false,
      };
    case ActionTypes.LOGOUT:
      return {
        ...state,
        user_id: null,
        currentStep: ApplicationStepsTypes.Step1,
        selectedPosition: null,
        exams: null,
        contract: null,
        completed: false,
      };
    default:
      return state;
  }
};
