import axios from "axios";
import React, { useState } from "react";
import { BsCheckLg } from "react-icons/bs";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  candidateApplyStoreInterface,
  schoolLevelsStoreInterface,
} from "../../../../actions";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { PositionAPI, returnValidatePosition } from "../../../../interfaces";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API, RTB_ID } from "../../../../utils/api";

export enum ApplicationStepsTypes {
  Step1,
  Step2,
  Step3,
}

interface ApplicationStepsProps {
  currentStep: ApplicationStepsTypes;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  currentPosition: PositionAPI;
  initializeApplicationProcess: (
    position: PositionAPI | null,
    user_id: string | null
  ) => void;
  auth: Auth;
  candidateApply: candidateApplyStoreInterface;
  PushPopupAlert: (alert: PopupAlertData) => void;
  RemoveUserApplication: (id: string) => void;
  candidateApplications: CandidateApplicationsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
}

const CandidateApplicationSteps = ({
  currentStep,
  setCandidateApplicationStep,
  currentPosition,
  initializeApplicationProcess,
  auth,
  candidateApply,
  PushPopupAlert,
  RemoveUserApplication,
  candidateApplications,
  schoolLevels,
}: ApplicationStepsProps) => {
  const [loading, setLoading] = useState(false);
  const deleteApplication = async () => {
    let test = candidateApplications.candidate_applications?.find(
      (itm) =>
        itm.user_id === auth.user.user_id &&
        schoolLevels.selectedEducationCategory !== null &&
        itm.position.position_id ===
          candidateApply.selectedPosition?.position_id &&
        itm.application_session._id ===
          schoolLevels.selectedEducationCategory.session_plan?._id
    );
    console.log("Test: ", test);
    console.log("List: ", candidateApplications);
    if (test !== undefined) {
      setLoading(true);
      try {
        await axios.delete(`${API.APPLICATION}/apply/${test.application_id}`);
        RemoveUserApplication(test.application_id);
        setLoading(false);
        PushPopupAlert({
          title:
            candidateApply.currentStep === ApplicationStepsTypes.Step2
              ? `Now you are on step two(2)`
              : candidateApply.selectedPosition?.education_institute_id
                  .education_institute_id === RTB_ID
              ? `Now you are on step two(2)`
              : `Application process canceled successfully!`,
          description: "You are able to modify your application",
          theme: PopupAlertTheme.info,
        });
      } catch (error: any) {
        setLoading(false);
        PushPopupAlert({
          title: `Failed to cancel application process!`,
          theme: PopupAlertTheme.warning,
        });
      }
    }
  };
  return (
    <div className="bg-blue-500 text-white pb-4">
      <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
        <div className="flex flex-col md:flex-row items-center justify-between gap-4 border-b border-blue-200 py-3 mb-3">
          <div className="flex flex-row text-center md:text-left items-center gap-3 text-xl font-light">
            <span className="hidden md:block">Applying for:</span>
            <span className="font-bold">{currentPosition.position_title}</span>
          </div>
          <div>
            {candidateApply.completed === false && (
              <div
                onClick={() => {
                  if (
                    window.confirm(
                      "Are you sure do you want to cancel application process?"
                    )
                  ) {
                    loading === false && deleteApplication();
                    initializeApplicationProcess(null, null);
                    setCandidateApplicationStep(ApplicationStepsTypes.Step1);
                  }
                }}
                className={`${
                  loading === true
                    ? "bg-gray-200 cursor-not-allowed text-gray-700"
                    : "bg-red-800 cursor-pointer hover:bg-red-500 text-white"
                } rounded-full py-1 w-max px-3`}
              >
                {loading === true ? "Canceling..." : "Cancel process"}
              </div>
            )}
          </div>
        </div>
        {/* Steps here */}
        <div className="flex flex-row items-center justify-center w-full">
          <div
            className="grid grid-cols-2"
            style={{ width: "calc(100% - 4rem)" }}
          >
            <div className="col-span-1">
              <div className="flex flex-row items- gap-0 w-full my-">
                <div className="z-20 flex flex-col items-center justify-center text-center">
                  <div
                    onClick={() =>
                      candidateApply.completed === false &&
                      setCandidateApplicationStep(ApplicationStepsTypes.Step1)
                    }
                    className={`cursor-pointer h-16 w-16 rounded-full ${
                      currentStep === ApplicationStepsTypes.Step1 ||
                      returnValidatePosition(
                        currentPosition.requirements,
                        auth.documents
                      ) === true
                        ? "bg-white border-white text-blue-500 shadow-lg"
                        : "bg-blue-500 border-blue-50 text-white shadow-xl"
                    } flex items-center justify-center border-4`}
                  >
                    {auth.user.user_id &&
                    candidateApply.selectedPosition !== null &&
                    candidateApply.user_id !== null &&
                    currentPosition !== null &&
                    returnValidatePosition(
                      currentPosition.requirements,
                      auth.documents
                    ) === true ? (
                      <BsCheckLg className="text-green-500 text-4xl" />
                    ) : (
                      <span className="text-4xl font-normal">1</span>
                    )}
                  </div>
                  <div className="font-semibold truncate hidden md:block">
                    Add Requirements
                  </div>
                  <div className="font-semilight text-xs text-blue-200 hidden md:block">
                    Add Requirements
                  </div>
                </div>
                <div
                  className={`h-4 ${
                    currentStep !== ApplicationStepsTypes.Step1
                      ? "bg-white"
                      : "bg-blue-300"
                  } w-full left-0 overflow-hidden z-0 mr-5 mt-6`}
                  style={{ marginLeft: "-19%", marginRight: "-19%" }}
                ></div>
              </div>
            </div>
            <div className="col-span-1">
              <div className="flex flex-row items- gap-0 w-full my-">
                <div className="z-20 flex flex-col items-center justify-center text-center">
                  <div
                    onClick={() => {
                      if (candidateApply.completed === false) {
                        loading === false && deleteApplication();
                        returnValidatePosition(
                          currentPosition.requirements,
                          auth.documents
                        ) === true
                          ? setCandidateApplicationStep(
                              ApplicationStepsTypes.Step2
                            )
                          : PushPopupAlert({
                              title: "Please complete step one (1)",
                              description:
                                "You need to fill all required documents for this position in order to continue",
                              theme: PopupAlertTheme.warning,
                            });
                      }
                    }}
                    className={`cursor-pointer h-16 w-16 rounded-full ${
                      currentStep === ApplicationStepsTypes.Step2 ||
                      candidateApply.selectedPosition?.courses.length ===
                        candidateApply.exams?.length
                        ? "bg-white border-white text-blue-500 shadow-lg"
                        : "bg-blue-500 border-blue-50 text-white shadow-xl"
                    } flex items-center justify-center border-4`}
                  >
                    {candidateApply.selectedPosition?.courses.length ===
                    candidateApply.exams?.length ? (
                      <BsCheckLg className="text-green-500 text-4xl" />
                    ) : (
                      <span className="text-4xl font-normal">2</span>
                    )}
                  </div>
                  {candidateApply.selectedPosition?.education_institute_id
                    .education_institute_id === RTB_ID ? (
                    <>
                      <div className="font-semibold truncate hidden md:block">
                        Residence
                      </div>
                      <div className="font-semilight text-xs text-blue-200 truncate hidden md:block">
                        Location of residence
                      </div>
                    </>
                  ) : (
                    <>
                      <div className="font-semibold truncate hidden md:block">
                        Exam center
                      </div>
                      <div className="font-semilight text-xs text-blue-200 truncate hidden md:block">
                        Location of exam center
                      </div>
                    </>
                  )}
                </div>
                <div
                  className={`h-4 ${
                    currentStep === ApplicationStepsTypes.Step3
                      ? "bg-white"
                      : "bg-blue-300"
                  } w-full left-0 overflow-hidden z-0 -mr-5 mt-6`}
                  style={{ marginLeft: "-19%", marginRight: "-50%" }}
                ></div>
              </div>
            </div>
          </div>

          <div className="flex flex-row items- gap-0 my- -ml-5">
            <div className="z-20 flex flex-col items-center justify-center text-center">
              <div
                onClick={() => {
                  if (candidateApply.completed === false) {
                    candidateApply.selectedPosition?.courses.length !==
                      candidateApply.exams?.length &&
                      // ? setCandidateApplicationStep(ApplicationStepsTypes.Step3)
                      PushPopupAlert({
                        title:
                          candidateApply.selectedPosition
                            ?.education_institute_id.education_institute_id ===
                          RTB_ID
                            ? "Verify your residence"
                            : "Please complete step two (2)",
                        description:
                          candidateApply.selectedPosition
                            ?.education_institute_id.education_institute_id ===
                          RTB_ID
                            ? "Please you need to verify and confirm your residence"
                            : "You need to fill examination centers for all exams",
                        theme: PopupAlertTheme.warning,
                      });
                  }
                }}
                className={`cursor-pointer h-16 w-16 rounded-full ${
                  currentStep === ApplicationStepsTypes.Step3 ||
                  candidateApply.completed === true
                    ? "bg-white border-white text-blue-500 shadow-lg"
                    : "bg-blue-500 border-blue-50 text-white shadow-xl"
                } flex items-center justify-center border-4`}
              >
                {candidateApply.completed === true ? (
                  <BsCheckLg className="text-green-500 text-4xl" />
                ) : (
                  <span className="text-4xl font-normal">3</span>
                )}
              </div>
              <div className="font-semibold truncate hidden md:block">
                Placement agreement
              </div>
              <div className="font-semilight text-xs text-blue-200 truncate hidden md:block">
                Modify placement priorities
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CandidateApplicationSteps;
