import React, { Component } from "react";
import { MdCancel } from "react-icons/md";
import { Link } from "react-router-dom";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  CandidatePlacementContractStoreInterface,
  DistrictLocationItem,
  FT_LoadCandidateContract,
  FT_LoadSchoolsByDistrict,
  FT_LoadSchoolsByIDs,
  FT_SubmitContract,
  LocationAPI,
  ProvinceLocationItem,
  SchoolContract,
  SectorLocationItem,
} from "../../../actions";
import ContractAgreement from "../../../components/ContractAgreement/ContractAgreement";
import ContractInfo from "../../../components/ContractAgreement/ContractInfo";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
// import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { GetApplicationsAPI } from "../../../interfaces/application";
import {
  CandidatePlacementContract,
  PContractItem,
} from "../../../interfaces/candidate_placement_contract";
import { ContractCategoryType } from "../PlacementContract";
import ContractHeader from "./ContractHeader";
import ContractItem from "./ContractItem";
import DistrictSelector from "./Selectors/DistrictSelector";
import ProvinceSelector from "./Selectors/ProvinceSelect";
import SchoolsSelectors from "./Selectors/SchoolsSelectors";
import SectorSelector from "./Selectors/SectorSelector";
interface ContractProps {
  auth: Auth;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidatePlacementContract: CandidatePlacementContractStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
  FC_LoadCandidateContract: FT_LoadCandidateContract;
  initializeContract: (
    user_id: string,
    position_id: string,
    session_id: string
  ) => void;
  UpdateContractLocations: (
    type: ContractCategoryType,
    data: PContractItem
  ) => void;
  locations: LocationAPI | null;
  FC_LoadSchoolsByDistrict: FT_LoadSchoolsByDistrict;
  FC_LoadSchoolsByIDs: FT_LoadSchoolsByIDs;
  FC_SubmitContract: FT_SubmitContract;
  SubmitApplication?: () => void;
  applicationGoBack?: () => void;
}

interface ContractState {
  loading: boolean;
  contract: CandidatePlacementContract | null;
  add_province: boolean;
  add_district: boolean;
  add_sector: boolean;
  add_school: boolean;
  districts: DistrictLocationItem[];
  provinces: ProvinceLocationItem[];
  sectors: SectorLocationItem[];
  selected_schools: SchoolContract[];
  loaded_schools: SchoolContract[];
  loading_schools: boolean;
  loading_schools_error: string;
  schools_loaded: boolean;
  pre_schools_loading: boolean;
  submit_contract_loading: boolean;
  error: {
    target: string;
    msg: string;
  };
  terms_and_conditions: boolean;
  preview_contract: boolean;
}

export interface ContractItemList {
  title: string;
  id: string;
}
export class Contract extends Component<ContractProps, ContractState> {
  constructor(props: ContractProps) {
    super(props);

    this.state = {
      loading: true,
      contract: null,
      add_province: false,
      add_district: false,
      add_sector: false,
      add_school: false,
      districts: [],
      provinces: [],
      sectors: [],
      selected_schools: [],
      loaded_schools: [],
      loading_schools: false,
      loading_schools_error: "",
      schools_loaded: false,
      pre_schools_loading: true,
      submit_contract_loading: false,
      error: {
        target: "",
        msg: "",
      },
      terms_and_conditions: true,
      preview_contract: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  componentDidMount = () => {
    if (this.props.candidateApplications.selected_application !== null) {
      this.props.FC_LoadCandidateContract(
        this.props.auth.user.user_id,
        this.props.candidateApplications.selected_application.position
          .position_id,
        this.props.candidateApplications.selected_application
          .application_session._id,
        (status, data, msg) => {
          if (status === true && data !== null) {
            this.setState({ contract: data, loading: false });
            if (this.props.locations !== null) {
              this.setState({
                districts: this.props.locations.districts,
                provinces: this.props.locations.provinces,
                sectors: this.props.locations.sectors,
              });
            }
          } else {
            window.alert(msg);
          }
        }
      );
    }
  };

  submitContractHandler = () => {
    if (this.state.contract !== null) {
      this.setState({
        submit_contract_loading: true,
        error: {
          target: "",
          msg: "",
        },
      });
      let contract = {
        ...this.state.contract,
        school: {
          ...this.state.contract.school,
          values: this.state.selected_schools.map(
            (school) => +school.school_code
          ),
        },
      } as CandidatePlacementContract;
      this.props.FC_SubmitContract(contract, (status, err) => {
        if (status === true) {
          this.setState({
            submit_contract_loading: false,
            error: {
              target: "SUCCESS",
              msg: "Submitted successfully",
            },
          });
        } else {
          this.setState({
            submit_contract_loading: false,
            error: {
              target: "ERROR",
              msg: err,
            },
          });
        }
      });
    }
    if (this.props.SubmitApplication !== undefined) {
      this.props.SubmitApplication();
    }
  };

  closeModal = () =>
    this.setState({
      add_province: false,
      add_district: false,
      add_sector: false,
      add_school: false,
    });

  addProvinceItem = (id: string) => {
    const id_number: number = +id;
    if (
      this.state.contract !== null &&
      this.state.contract.province.values.includes(id_number) === false
    ) {
      let new_provinces = [...this.state.contract.province.values, id_number];

      if (this.state.contract !== null) {
        this.setState((prevState) => {
          if (prevState.contract !== null) {
            return {
              ...prevState,
              contract: {
                ...prevState.contract,
                province: {
                  ...prevState.contract.province,
                  values: new_provinces,
                },
                // district: {
                //   ...prevState.contract.district,
                //   values: [],
                // },
                // sector: {
                //   ...prevState.contract.sector,
                //   values: [],
                // },
                // school: {
                //   ...prevState.contract.school,
                //   values: [],
                // },
              },
            };
          } else {
            return this.state;
          }
        });
      }

      this.UpdateProvinceStructure(
        this.state.contract.province.strict,
        new_provinces
      );
    }
  };

  addDistrictItem = (id: string) => {
    const id_number: number = +id;
    if (
      this.state.contract !== null &&
      this.state.contract.district.values.includes(id_number) === false
    ) {
      let new_district_list = [
        ...this.state.contract.district.values,
        id_number,
      ];
      this.setState({
        contract: {
          ...this.state.contract,
          district: {
            ...this.state.contract.district,
            values: new_district_list,
          },
          // sector: {
          //   ...this.state.contract.sector,
          //   values: [],
          // },
          // school: {
          //   ...this.state.contract.school,
          //   values: [],
          // },
        },
      });

      this.UpdateDistrictStructure(
        this.state.contract.district.strict,
        new_district_list
      );
    }
  };

  addSectorItem = (id: string) => {
    const id_number: number = +id;
    if (
      this.state.contract !== null &&
      this.state.contract.sector.values.includes(id_number) === false
    ) {
      this.setState({
        contract: {
          ...this.state.contract,
          sector: {
            ...this.state.contract.sector,
            values: [...this.state.contract.sector.values, id_number],
          },
          // school: {
          //   ...this.state.contract.school,
          //   values: [],
          // },
        },
      });
    }
  };

  removeProvince = (id: string) => {
    if (this.state.contract !== null) {
      let new_provinces = this.state.contract.province.values.filter(
        (el) => el.toString() !== id
      );
      let new_strict =
        this.state.contract.province.values.length <= 1
          ? false
          : this.state.contract.province.strict;

      this.setState({
        contract: {
          ...this.state.contract,
          province: {
            ...this.state.contract.province,
            values: new_provinces,
            strict: new_strict,
          },
          district: {
            ...this.state.contract.district,
            values: [],
          },
          sector: {
            ...this.state.contract.sector,
            values: [],
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });

      this.UpdateProvinceStructure(new_strict, new_provinces);
    }
  };

  changeStrictProvince = (status: boolean) => {
    if (
      this.state.contract !== null &&
      status !== this.state.contract.province.strict
    ) {
      this.setState({
        contract: {
          ...this.state.contract,
          province: {
            ...this.state.contract.province,
            strict: status,
          },
          district: {
            ...this.state.contract.district,
            values: [],
          },
          sector: {
            ...this.state.contract.sector,
            values: [],
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });

      this.UpdateProvinceStructure(status, this.state.contract.province.values);
    }
  };

  removeDistrict = (id: string) => {
    if (this.state.contract !== null) {
      let new_values = this.state.contract.district.values.filter(
        (el) => el.toString() !== id
      );
      let new_strict =
        new_values.length <= 0 ? false : this.state.contract.district.strict;
      this.setState({
        contract: {
          ...this.state.contract,
          district: {
            ...this.state.contract.district,
            values: new_values,
            strict: new_strict,
          },
          sector: {
            ...this.state.contract.sector,
            values: [],
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });

      this.UpdateDistrictStructure(new_strict, new_values);
    }
  };

  changeDistrictStrict = (status: boolean) => {
    if (
      this.state.contract !== null &&
      status !== this.state.contract.district.strict
    ) {
      this.setState({
        contract: {
          ...this.state.contract,
          district: {
            ...this.state.contract.district,
            strict: status,
          },
          sector: {
            ...this.state.contract.sector,
            values: [],
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });

      this.UpdateDistrictStructure(status, this.state.contract.district.values);
    }
  };

  removeSector = (id: string) => {
    if (this.state.contract !== null) {
      let new_values = this.state.contract.sector.values.filter(
        (el) => el.toString() !== id
      );
      this.setState({
        contract: {
          ...this.state.contract,
          sector: {
            ...this.state.contract.sector,
            values: new_values,
            strict:
              new_values.length <= 0
                ? false
                : this.state.contract.sector.strict,
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });
    }
  };

  changeSectorStrict = (status: boolean) => {
    if (
      this.state.contract !== null &&
      status !== this.state.contract.sector.strict
    ) {
      this.setState({
        contract: {
          ...this.state.contract,
          sector: {
            ...this.state.contract.sector,
            strict: status,
          },
          school: {
            ...this.state.contract.school,
            values: [],
          },
        },
      });
    }
  };

  UpdateProvinceStructure = (
    province_strict: boolean,
    selected_provinces: number[]
  ) => {
    if (this.props.locations !== null && this.state.contract !== null) {
      let districts: DistrictLocationItem[] = this.props.locations.districts;
      let sectors: SectorLocationItem[] = this.props.locations.sectors;
      //
      if (province_strict === true) {
        districts = districts.filter((dist) =>
          selected_provinces.includes(+dist.province_code)
        );

        let selected_district_list: string[] = districts.map(
          (itm) => itm.district_code
        ); // map the ids into an array of strings
        sectors = sectors.filter((sect) =>
          selected_district_list.includes(sect.district_code)
        );
      }

      this.setState((prevState) => {
        if (prevState.contract !== null) {
          return {
            ...prevState,
            sectors: sectors,
            districts: districts,
          };
        } else {
          return this.state;
        }
      });
    }
  };

  UpdateDistrictStructure = (
    district_strict: boolean,
    province_provinces: number[]
  ) => {
    if (this.props.locations !== null && this.state.contract !== null) {
      let sectors: SectorLocationItem[] = this.props.locations.sectors;
      if (district_strict === true) {
        sectors = sectors.filter((sect) =>
          province_provinces.includes(+sect.district_code)
        );
      }

      this.setState((prevState) => {
        if (prevState.contract !== null) {
          return {
            ...prevState,
            sectors: sectors,
          };
        } else {
          return this.state;
        }
      });
    }
  };

  // Change the strict of school
  changeSchoolStrict = (status: boolean) => {
    if (
      this.state.contract !== null &&
      status !== this.state.contract.school.strict
    ) {
      this.setState({
        contract: {
          ...this.state.contract,
          school: {
            ...this.state.contract.school,
            strict: status,
          },
        },
      });
    }
  };

  // get Schools by sectors
  getSchoolsBySector = (sector_id: string) => {
    if (sector_id !== "") {
      this.setState({
        loading_schools: true,
        loading_schools_error: "",
        loaded_schools: [],
      });

      this.props.FC_LoadSchoolsByDistrict(
        sector_id,
        (status, schools, error_msg) => {
          if (status === true) {
            this.setState({
              loading_schools: false,
              loading_schools_error: "",
              loaded_schools: schools,
            });
          } else {
            this.setState({
              loading_schools: false,
              loading_schools_error: error_msg,
              loaded_schools: [],
            });
          }
        }
      );
    }
  };

  addSchoolItem = (school: SchoolContract) => {
    let schools_list = [...this.state.selected_schools, school];
    if (this.state.contract !== null) {
      this.setState({
        contract: {
          ...this.state.contract,
          school: {
            ...this.state.contract.school,
            values: schools_list.map((itm) => +itm.school_code),
          },
        },
        selected_schools: schools_list,
      });
    }
  };

  removeSchoolItem = (school_id: string) => {
    if (this.state.contract !== null) {
      let schools_list = this.state.selected_schools.filter(
        (school) => school.school_code !== school_id
      );
      this.setState({
        contract: {
          ...this.state.contract,
          school: {
            ...this.state.contract.school,
            values: schools_list.map((itm) => +itm.school_code),
            strict:
              schools_list.length <= 0
                ? false
                : this.state.contract.school.strict,
          },
        },
        selected_schools: schools_list,
      });
    }
  };

  closeAlert = () => this.setState({ error: { target: "", msg: "" } });

  loadSchoolsByIds = () => {
    if (
      this.state.contract !== null &&
      this.state.schools_loaded === false &&
      this.state.contract.school.values.length >= 1
    ) {
      this.setState({
        pre_schools_loading: true,
      });
      this.props.FC_LoadSchoolsByIDs(
        this.state.contract.school.values.map((itm) => itm.toString()),
        (status, schools, err) => {
          if (status === true) {
            this.setState({
              selected_schools: schools,
              schools_loaded: true,
              pre_schools_loading: false,
            });
          }
        }
      );
    }
  };

  render() {
    let ProvincesList: ContractItemList[] = [];
    let DistrictList: ContractItemList[] = [];
    let SectorList: ContractItemList[] = [];

    if (this.state.contract !== null && this.props.locations !== null) {
      // * Province
      this.state.contract.province.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.provinces.find(
            (element) => element.province_code === el.toString()
          );
          if (findIt !== undefined) {
            ProvincesList.push({
              id: findIt.province_code,
              title: findIt.province_name,
            } as ContractItemList);
          }
        }
      });

      // * Province
      this.state.contract.district.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.districts.find(
            (element) => element.district_code === el.toString()
          );
          if (findIt !== undefined) {
            DistrictList.push({
              id: findIt.district_code,
              title: findIt.district_name,
            } as ContractItemList);
          }
        }
      });

      // * Sector
      this.state.contract.sector.values.forEach((el) => {
        if (this.props.locations !== null) {
          let findIt = this.props.locations.sectors.find(
            (element) => element.sector_code === el.toString()
          );
          if (findIt !== undefined) {
            SectorList.push({
              id: findIt.sector_code,
              title: findIt.sector_name,
            } as ContractItemList);
          }
        }
      });
    }

    if (
      this.state.loading === true ||
      this.props.candidateApplications.candidate_applications === null ||
      this.state.contract === null
    ) {
      return (
        <div className="bg-white mt-6 mx-6 p-6 h-full flex flex-col items-start justify-center rounded">
          <div className="text-xl mx-4 my-4 animate-pulse">
            Loading contract, Please wait...
          </div>
          <Loading />
        </div>
      );
    }

    if (this.state.preview_contract === true)
      return (
        <>
          <div className="p-2 py-6 bg-white rounded mt-4 mb-12">
            <ContractAgreement
              contract={{
                province: {
                  strict: this.state.contract.province.strict,
                  values: ProvincesList,
                },
                district: {
                  strict: this.state.contract.district.strict,
                  values: DistrictList,
                },
                sector: {
                  strict: this.state.contract.sector.strict,
                  values: SectorList,
                },
                school: {
                  strict: this.state.contract.school.strict,
                  values: this.state.selected_schools.map((itm) => ({
                    id: itm.school_code,
                    title: itm.school_name,
                  })),
                },
              }}
              position={
                this.props.candidateApplications.selected_application?.position
                  .position_title + ""
              }
            />

            <div className="mb-2 flex">
              <div className="flex-1"></div>
              <div className="flex-1">
                {this.state.error.target === "SUCCESS" && (
                  <div className=" items-center bg-green-200 text-green-900 mt-2 rounded animate__animated animate__zoomIn animate__faster">
                    <div className="flex items-center">
                      <div className="flex-1 p-3">{this.state.error.msg}</div>
                      <div className="">
                        <button
                          className="p-1 text-3xl text-green-700 hover:text-green-900"
                          onClick={this.closeAlert}
                        >
                          <MdCancel />
                        </button>
                      </div>
                    </div>
                  </div>
                )}

                {this.state.error.target === "FAILED" && (
                  <div className=" items-center bg-red-200 text-red-900 mt-2 rounded animate__animated animate__zoomIn animate__faster">
                    <div className="flex items-center">
                      <div className="flex-1 p-3">{this.state.error.msg}</div>
                      <div className="">
                        <button
                          className="p-1 text-3xl text-red-700 hover:text-red-900"
                          onClick={this.closeAlert}
                        >
                          <MdCancel />
                        </button>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>

            <div className="flex w-full">
              <div className="flex-1"></div>
              <div className="">
                {this.state.error.target === "SUCCESS" && (
                  <Link
                    to="/applications"
                    className="py-3 mx-2 px-4 bg-yellow-100 text-yellow-900 hover:bg-yellow-800 hover:text-white text-lg rounded border border-yellow-600"
                  >
                    Close
                  </Link>
                )}
                <button
                  className="py-3 mx-2 px-4 bg-gray-200 text-gray-900 hover:bg-gray-800 hover:text-white text-lg rounded"
                  disabled={this.state.submit_contract_loading}
                  onClick={() => this.setState({ preview_contract: false })}
                >
                  Back
                </button>
                <button
                  className="py-3 px-4 bg-blue-700 hover:bg-blue-800 text-white text-lg rounded"
                  disabled={this.state.submit_contract_loading}
                  onClick={this.submitContractHandler}
                >
                  {this.state.submit_contract_loading === true ? (
                    <span className="animate__animated animate__fadeIn animate__infinite">
                      loading...
                    </span>
                  ) : (
                    "Yes I agree"
                  )}
                </button>
              </div>
            </div>
          </div>
        </>
      );

    return (
      <>
        {this.state.terms_and_conditions === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ terms_and_conditions: false })}
            backDropClose={false}
            widthSizeClass={ModalSize.large}
            displayClose={true}
            padding={{
              title: true,
              body: false,
              footer: false,
            }}
            title={"Important Info for you."}
          >
            <ContractInfo
              close={() => this.setState({ terms_and_conditions: false })}
              names={`${this.props.auth.user.fname} ${this.props.auth.user.lname}`}
            />
          </Modal>
        )}
        <div className="mr-2 -ml-2">
          {this.props.locations !== null && this.state.contract !== null && (
            <>
              {this.state.add_province === true && (
                <ProvinceSelector
                  close={this.closeModal}
                  data={this.state.provinces}
                  selectItem={this.addProvinceItem}
                  selected={this.state.contract.province.values}
                  removeItem={this.removeProvince}
                  maximum={4}
                />
              )}
              {this.state.add_district === true && (
                <DistrictSelector
                  close={this.closeModal}
                  data={this.state.districts}
                  selectItem={this.addDistrictItem}
                  selected={this.state.contract.district.values}
                  removeItem={this.removeDistrict}
                  maximum={4}
                />
              )}
              {this.state.add_sector === true && (
                <SectorSelector
                  close={this.closeModal}
                  data={this.state.sectors}
                  selectItem={this.addSectorItem}
                  selected={this.state.contract.sector.values}
                  removeItem={this.removeSector}
                  maximum={4}
                />
              )}
              {this.state.add_school === true && (
                <SchoolsSelectors
                  close={this.closeModal}
                  data={this.state.loaded_schools}
                  selectItem={this.addSchoolItem}
                  selected={this.state.selected_schools}
                  removeItem={this.removeSchoolItem}
                  maximum={4}
                  loading={this.state.loading_schools}
                  sectorsData={this.state.sectors}
                  getSchoolsBySector={this.getSchoolsBySector}
                  error={this.state.loading_schools_error}
                />
              )}
            </>
          )}

          <div className="rounded bg-white">
            <ContractHeader
              back={() => {
                this.props.SelectUserApplication(null);
                this.props.applicationGoBack && this.props.applicationGoBack();
              }}
              position_title={
                this.props.candidateApplications.selected_application?.position
                  .position_title
              }
              submitContractHandler={this.submitContractHandler}
              loading={this.state.submit_contract_loading}
            />
            <div className="py-2">
              {this.state.error.target === "SUCCESS" && (
                <div className=" items-center bg-green-200 text-green-900 mt-2 rounded animate__animated animate__zoomIn animate__faster">
                  <div className="flex items-center">
                    <div className="flex-1 p-3">{this.state.error.msg}</div>
                    <div className="">
                      <button
                        className="p-1 text-3xl text-green-700 hover:text-green-900"
                        onClick={this.closeAlert}
                      >
                        <MdCancel />
                      </button>
                    </div>
                  </div>
                </div>
              )}

              {this.state.error.target === "FAILED" && (
                <div className=" items-center bg-red-200 text-red-900 mt-2 rounded animate__animated animate__zoomIn animate__faster">
                  <div className="flex items-center">
                    <div className="flex-1 p-3">{this.state.error.msg}</div>
                    <div className="">
                      <button
                        className="p-1 text-3xl text-red-700 hover:text-red-900"
                        onClick={this.closeAlert}
                      >
                        <MdCancel />
                      </button>
                    </div>
                  </div>
                </div>
              )}

              {this.state.submit_contract_loading === true && (
                <div className="p-4">Submitting contract</div>
              )}
              {this.props.locations !== null &&
                this.state.contract !== null &&
                this.state.submit_contract_loading === false && (
                  <>
                    {/* <ContractItem
                      title="Province"
                      data={ProvincesList}
                      removeData={this.removeProvince}
                      addItem={() =>
                        this.setState({
                          add_province: true,
                          add_district: false,
                          add_sector: false,
                          add_school: false,
                        })
                      }
                      strict={this.state.contract.province.strict}
                      changeStrict={this.changeStrictProvince}
                    /> */}

                    <ContractItem
                      title="District"
                      data={DistrictList}
                      removeData={this.removeDistrict}
                      addItem={() =>
                        this.setState({
                          add_district: true,
                          add_province: false,
                          add_sector: false,
                          add_school: false,
                        })
                      }
                      strict={this.state.contract.district.strict}
                      changeStrict={this.changeDistrictStrict}
                    />
                    {/* <ContractItem
                      title="Sector"
                      data={SectorList}
                      removeData={this.removeSector}
                      addItem={() =>
                        this.setState({
                          add_sector: true,
                          add_province: false,
                          add_district: false,
                          add_school: false,
                        })
                      }
                      strict={this.state.contract.sector.strict}
                      changeStrict={this.changeSectorStrict}
                    /> */}
                    {/* <ContractItem
                      title="School"
                      expandClick={this.loadSchoolsByIds}
                      loading={
                        this.state.contract.school.values.length <= 0
                          ? false
                          : this.state.pre_schools_loading
                      }
                      data={this.state.selected_schools.map((itm) => ({
                        id: itm.school_code,
                        title: itm.school_name,
                      }))}
                      removeData={this.removeSchoolItem}
                      addItem={() =>
                        this.setState({
                          add_school: true,
                          add_province: false,
                          add_district: false,
                          add_sector: false,
                        })
                      }
                      strict={this.state.contract.school.strict}
                      changeStrict={this.changeSchoolStrict}
                    /> */}
                  </>
                )}

              <div className="text-right px-5 py-3">
                {DistrictList.length >= 1 && (
                  <button
                    className="py-3 px-4 bg-blue-700 hover:bg-blue-800 text-white text-lg rounded"
                    onClick={() => this.setState({ preview_contract: true })}
                  >
                    Continue
                  </button>
                )}
                {/* <br /> */}
              </div>
            </div>
          </div>
          <br />
          <br />
          <br />
          <br />
        </div>
      </>
    );
  }
}

export default Contract;
