export interface locationInterface {
  province_id: string;
  province_name: string;
  districts: {
    district_id: string;
    district_name: string;
    centers: {
      center_id: string;
      center_name: string;
      rooms: {
        room_id: string;
        room_name: string;
        room_type: RoomType;
        capacity: number;
      }[];
    }[];
  }[];
}

export enum RoomType {
  Physical = "Physical",
  Virtual = "Virtual",
  Default = "Default",
}

export interface LocationAPI {
  districts: {
    centers: {
      rooms: {
        center_id: string;
        isUsed: boolean;
        room_type: RoomType;
        room_number: number;
        capacity: number;
        _id: string;
        occupied: number;
      }[];
      isUsed: true;
      center_name: string;
      district_id: string;
      _id: string;
    }[];
    _id: string;
    district_name: string;
    district_code: number;
    province_code: number;
    province_id: string;
    status: number;
    createdAt: string;
    updatedAt: string;
  }[];
  _id: string;
  province_name: string;
  full_name: string;
  province_code: number;
  status: number;
  createdAt: string;
  updatedAt: string;
}
