import React, { Component } from "react";
import { Redirect } from "react-router";
import { schoolLevelsStoreInterface } from "../../actions";
import { education_category } from "../../interfaces";
import Loading from "../Loading/Loading";
import Modal, { ModalSize, Themes } from "../Modal/Modal";

interface InitializeInstitutionProps {
  schoolLevels: schoolLevelsStoreInterface;
  redirect?: boolean;
  setSelectedEducationCategory: (data: education_category) => void;
  onSelect: () => void;
  onClose?: () => void; //when defined, it displays close icon
}
interface InitializeInstitutionState {
  redirect: boolean;
}

class InitializeInstitution extends Component<
  InitializeInstitutionProps,
  InitializeInstitutionState
> {
  constructor(props: InitializeInstitutionProps) {
    super(props);

    this.state = {
      redirect: false,
    };
  }
  render() {
    if (this.state.redirect === true) {
      return (
        <Redirect
          to={this.props.redirect !== undefined ? "/about" : "/apps-page"}
        />
      );
    }
    return (
      <Modal
        backDrop={true}
        theme={Themes.default}
        close={() =>
          this.props.onClose === undefined
            ? this.setState({ redirect: true })
            : this.props.onClose()
        }
        backDropClose={true}
        widthSizeClass={ModalSize.medium}
        displayClose={this.props.onClose !== undefined ? true : false}
        padding={{
          title: this.props.onClose === undefined ? undefined : true,
          body: undefined,
          footer: undefined,
        }}
        title={
          this.props.onClose === undefined ? (
            ""
          ) : (
            <div className="text-left text-2xl text-gray-600 font-bold ">
              Please Choose
            </div>
          )
        }
      >
        <div>
          <div className="p-3">
            {this.props.onClose === undefined && (
              <div className="text-center text-2xl text-gray-600 font-bold mb-5 mt-3 ">
                Please Choose
              </div>
            )}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
              {this.props.schoolLevels.education_categories === null ? (
                <>
                  <div className="col-span-1">
                    <div className="border border-gray-200 animate-pulse rounded-md p-1 pt-4">
                      <Loading />
                    </div>
                  </div>
                  <div className="col-span-1">
                    <div className="border border-gray-200 animate-pulse rounded-md p-1 pt-4">
                      <Loading />
                    </div>
                  </div>
                </>
              ) : this.props.schoolLevels.education_categories.length === 0 ? (
                <div className="text-center gap-5 rounded-md bg-white">
                  <span>No available institutes</span>
                </div>
              ) : (
                this.props.schoolLevels.education_categories.map((item, i) => (
                  <div
                    key={i + 1}
                    onClick={() => {
                      // this.props.onSelect();
                      this.props.setSelectedEducationCategory(item);
                    }}
                    className={`col-span-1 ${
                      this.props.schoolLevels.selectedEducationCategory
                        ?.education_institute_id === item.education_institute_id
                        ? "bg-blue-500 text-white"
                        : "bg-blue-100 hover:bg-blue-500"
                    }  rounded p-5 cursor-pointer group animate__animated animate__fadeIn animate__faster`}
                  >
                    <div
                      className={`text-3xl font-bold ${
                        this.props.schoolLevels.selectedEducationCategory
                          ?.education_institute_id ===
                        item.education_institute_id
                          ? "text-white"
                          : "text-blue-800 group-hover:text-white"
                      }  uppercase`}
                    >
                      {item.education_category_title}
                    </div>
                    <div
                      className={`${
                        this.props.schoolLevels.selectedEducationCategory
                          ?.education_institute_id ===
                        item.education_institute_id
                          ? "text-white"
                          : "text-blue-700 group-hover:text-white"
                      } font-light`}
                    >
                      {item.description}
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>
      </Modal>
    );
  }
}

export default InitializeInstitution;
