import { Action, ActionTypes } from "../actions";
import { CandidateApplicationsStoreInterface } from "../actions/candidate_applications";

// default state
const defaultState: CandidateApplicationsStoreInterface = {
  candidate_applications: null,
  selected_application: null,
  loading: false,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const CandidateApplicationsReducer = (
  state: CandidateApplicationsStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOAD_CANDIDATE_APPLICATIONS:
      return {
        ...state,
        candidate_applications: action.payload,
        loading: false,
      };

    case ActionTypes.ADD_CANDIDATE_APPLICATION:
      return {
        ...state,
        candidate_applications:
          state.candidate_applications === null
            ? [action.payload]
            : [
                ...state.candidate_applications.filter(
                  (itm) => itm.application_id !== action.payload.application_id
                ),
                action.payload,
              ],
      };
    case ActionTypes.SELECT_CANDIDATE_APPLICATION:
      return {
        ...state,
        selected_application: action.payload,
      };
    case ActionTypes.REMOVE_CANDIDATE_APPLICATION:
      return {
        ...state,
        candidate_applications:
          state.candidate_applications === null
            ? []
            : state.candidate_applications.filter(
                (itm) => itm.application_id !== action.payload
              ),
      };
    case ActionTypes.LOADING_CANDIDATE_APPLICATION:
      return {
        ...state,
        loading: action.payload,
      };
    case ActionTypes.SET_CANDIDATE_APPLICATIONS:
      return {
        ...state,
        candidate_applications: action.payload,
      };
    case ActionTypes.LOGOUT:
      return {
        ...defaultState,
      };
    default:
      return state;
  }
};
