import {
  Action,
  ActionTypes,
  CandidatePlacementContractStoreInterface,
} from "../actions";
import { ContractCategory } from "../containers/PlacementContract/PlacementContract";

// default state
const defaultState: CandidatePlacementContractStoreInterface = {
  contract: null,
};

/**
 * Candidate placement contract
 * @param state
 * @param action
 * @returns
 */
export const CandidatePlacementContractReducer = (
  state: CandidatePlacementContractStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOAD_CANDIDATE_PLACEMENT_CONTRACT:
      return { ...state, contract: action.payload };

    case ActionTypes.UPDATE_CONTRACT_LOCATIONS:
      let temp = state.contract;
      if (state.contract !== null) {
        temp = {
          user_id: state.contract.user_id,
          position_id: state.contract.position_id,
          session_id: state.contract.session_id,
          province:
            action.payload.type.key === ContractCategory.PROVINCE
              ? action.payload.data
              : state.contract.province,
          district:
            action.payload.type.key === ContractCategory.DISTRICT
              ? action.payload.data
              : state.contract.district,
          sector:
            action.payload.type.key === ContractCategory.SECTOR
              ? action.payload.data
              : state.contract.sector,
          school:
            action.payload.type.key === ContractCategory.SCHOOL
              ? action.payload.data
              : state.contract.school,
        };
        return { ...state, contract: temp };
      } else {
        return state;
      }
    case ActionTypes.LOGOUT:
      return {
        ...defaultState,
      };
    default:
      return state;
  }
};
