export interface questionType {
  status: number;
  title: string;
  element: questionTypeElementsType;
  description: string;
  createdAt?: string;
  updatedAt?: string;
  id?: string;
}

export interface uploadQuestions {
  course: course;
  questionsFile: File[];
}

export enum QuestionLanguage {
  Eng = "Eng",
  Fr = "Fr",
  Kiny = "Kiny",
}

export interface LanguageInterface {
  key: QuestionLanguage;
  value: string;
}

export const QuestionLanguages: { key: QuestionLanguage; value: string }[] = [
  { key: QuestionLanguage.Eng, value: "English" },
  { key: QuestionLanguage.Fr, value: "French" },
  { key: QuestionLanguage.Kiny, value: "Kinyarwanda" },
];

export interface uploadQuestionsResponse {
  insertedCount: number;
  insertedData: QuestionInterface[];
  insertedIds: string[];
  result: { ok: number; n: number };
}

export interface SubQuestionInterface {
  _id: string;
  description: string;
  typeShort:
    | questionShortElementsType
    | QuestionForEmbedded
    | QuestionForHybrid;
  type: string; //TYPE ID
  marks: number;
  reference: number;
}

export interface QuestionItemInterface {
  description: string;
  language: QuestionLanguage;
  status: number;
  title: string;
  answers: QuestionAnswerInterface[];
  _id: string;
  sub_question: SubQuestionInterface[];
}

export interface QuestionInterface {
  question_id: string;
  course: string; // Course ID
  question: QuestionItemInterface[];
  type: questionTypeElementsType | "Invalid";
  chapters: string[]; // CourseChapter: ID
  customNumber: number;
  marks: number;
  answers: QuestionAnswerInterface[];
  is_deleted: boolean;
}

