import { Action, ActionTypes } from "../actions";
import { marksPublicationInterface } from "../interfaces/marks_publication";

export const marksPublicationReducer = (
  state: marksPublicationInterface[] | null = null,
  action: Action
) => {
  // let index;
  switch (action.type) {
    case ActionTypes.GET_MARKS_PUBLICATION:
      return action.payload;
    case ActionTypes.CREATE_MARKS_PUBLICATION:
      if (state === null) {
        return [action.payload];
      }
      return [...state, action.payload];
    case ActionTypes.UPDATE_MARKS_PUBLICATION:
      if (state === null) {
        return [action.payload];
      }
      let index = state.findIndex(
        (item) => item.publication_id === action.payload.publication_id
      );
      if (index > -1) {
        let dataCopy = [...state];
        dataCopy[index] = action.payload;
        return dataCopy;
      }
      return state;
    case ActionTypes.LOGOUT:
      return null;
    default:
      return state;
  }
};
