import React, { FC, ReactElement, useEffect } from "react";
import { MdBackHand, MdOutlineBackHand } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  FC_SetHand,
  FT_SetHand,
  Proctoring,
  Socket,
} from "../../actions";
import {
  Events,
  HandEventInterface,
  StartExam_Callback,
} from "../../interfaces";
import { StoreState } from "../../reducers";
import {
  PROCTORING_VIDEO_HEIGHT,
  PROCTORING_VIDEO_WIDTH,
} from "../../utils/app.config";

const styles_videoContainerBig: React.CSSProperties = {
  width: "auto",
};
const styles_videoContainer: React.CSSProperties = {
  width: `${PROCTORING_VIDEO_WIDTH}px`,
  height: `${PROCTORING_VIDEO_HEIGHT}px`,
  borderRadius: "8px",
  position: "relative",
};
const styles_videoElement: React.CSSProperties = {
  width: `${PROCTORING_VIDEO_WIDTH}px`,
  height: `${PROCTORING_VIDEO_HEIGHT}px`,
  position: "absolute",
  top: "0",
};

type HomeProctoringCandidateControlsProps = {
  proctoring: Proctoring;
  socket: Socket;
  FC_SetHand: FT_SetHand;
  auth: Auth;
  room_id: string;
};

const _HomeProctoringCandidateControls: FC<
  HomeProctoringCandidateControlsProps
> = ({ proctoring, FC_SetHand, socket, auth, room_id }): ReactElement => {
  let localStream = proctoring.my_stream.stream;
  const localVideoRef = React.useRef<HTMLVideoElement>(null);
  // const canvasRef = React.useRef<HTMLCanvasElement>(null);
  // const canvasRef = React.useRef(null);

  useEffect(() => {
    if (localStream) {
      const localVideo = localVideoRef.current;

      if (localVideo !== null) {
        localVideo.srcObject = localStream;
        localVideo.onloadedmetadata = () => {
          localVideo.play();
        };
      }
    }
  }, [localStream]);

  const checkTime = () => {
    socket.socket.emit(
      Events.GET_ROOM_TIME,
      room_id,
      (data: StartExam_Callback | null) => {
        console.log({ timer: data });
      }
    );
  };

  const handEvent = (status: boolean) => {
    FC_SetHand(status);
    socket.socket.emit(Events.HOME_PROCTORING__SET_HAND, {
      user_id: auth.user.user_id,
      room_id: room_id,
      status: status,
    } as HandEventInterface);
  };
  return (
    <div className="p-1 bg-white rounded-lg" style={styles_videoContainerBig}>
      <div style={styles_videoContainer} className="bg-black mx-auto mt-1">
        <video style={styles_videoElement} ref={localVideoRef} autoPlay muted />
        {/* <canvas
          ref={canvasRef}
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zIndex: 9,
            // display: "none",
            width: "250px",
            height: "187px",
            // background: "red",
            // opacity: "0.5",
          }}
        /> */}
      </div>

      {proctoring.supervisor_audio.audio === true && (
        <h2 className="text-center text-sm mt-2 mb-1 p-1 bg-green-400 text-black">
          The supervisor is talking to you
        </h2>
      )}

      <button onClick={() => handEvent(!proctoring.hand)} className="text-2xl">
        {proctoring.hand === true ? <MdBackHand /> : <MdOutlineBackHand />}
      </button>

      <button onClick={() => checkTime()}>Check time</button>
    </div>
  );
};

const mapStateToProps = ({
  proctoring,
  socket,
  auth,
}: StoreState): {
  proctoring: Proctoring;
  socket: Socket;
  auth: Auth;
} => {
  return {
    proctoring: proctoring,
    socket: socket,
    auth: auth,
  };
};

const HomeProctoringCandidateControls = connect(mapStateToProps, {
  FC_SetHand,
})(_HomeProctoringCandidateControls);

export default HomeProctoringCandidateControls;
