export interface EventInterface {
  event_id: string;
  user_id: string;
  room_id: string;
  exam_id: string;
  position_id: string;
  application_session_id: string;
  event_type_id: string;
  description: string;
  done_by: string;
  time: string;
  createdAt?: string;
  updatedAt?: string;
  group_id: string;
}

export interface LiveServer__Candidate {
  user_id: string;
  peer_id: string;
  socket_id: string;
  stream_id: string;
  device_id: string;
  // states
  active: boolean;
  done: boolean;
  joined: boolean;
  removed: boolean;
  room_id: string;
  stopped: boolean;
  supervisor: boolean;
  added_minutes: number;
  seat_number: number;
  start_time: string;
  start_added_time: string;
  duration: number;
}

export interface LiveServer__Room {
  exam_done: boolean;
  group_id: string;
  room_id: string;
  start_exam: boolean;
  start_time: null | string;
  candidates: LiveServer__Candidate[];
  duration: number;
  exam_id: string;
}

export interface LiveServer__Candidate_Details extends LiveServer__Room {
  user: LiveServer__Candidate;
  candidate_timer: {
    start_time: string;
    duration: number;
    minutes: number;
  };
}

export interface AudioEventManagement {
  user_id: string;
  room_id: string;
}

export interface HandEventInterface {
  user_id: string;
  room_id: string;
  status: boolean;
}

export interface StartExamEvent {
  room_id: string;
  data: string;
  description: string;
  msg: string;
  duration: number;
  dynamic_time: boolean;
}

export interface StartExam_Callback {
  start_time: string;
  end_time: string;
  fixed_duration: number;
  duration: number;
  added_minutes: number;
  remaining_minutes: number;
}
