import { Action, ActionTypes, DocumentClaimUser } from "../actions";
import {
  allClaimApplicationInterface,
  CandidateClaimInterface,
  claimApplicationsInterface,
  ClaimExamResultsDetails,
  claimReasonInterface,
  ClaimTypesInterfaceAPI,
  PlansTypeKeys,
} from "../interfaces";
import { candidatePlacementInterface } from "../interfaces/placement";

export interface ClaimingReducerType {
  claimTypes: ClaimTypesInterfaceAPI[] | null;
  selectedClaimType: PlansTypeKeys | "all";
  myClaims: CandidateClaimInterface[] | null;
  myAnswer: ClaimExamResultsDetails[] | null;
  myPlacement: candidatePlacementInterface | null;
  allAnswer: ClaimExamResultsDetails[];
  allClaims: CandidateClaimInterface[] | null;
  candidateApplications: claimApplicationsInterface[] | null;
  candidateDocuments: DocumentClaimUser[] | null;
  allApplications: allClaimApplicationInterface[];
  claimReasons: claimReasonInterface[] | null;
}

// default state
const initialState: ClaimingReducerType = {
  claimTypes: null,
  selectedClaimType: "all",
  myClaims: null,
  allClaims: null,
  myAnswer: null,
  myPlacement: null,
  allAnswer: [],
  allApplications: [],
  candidateApplications: null,
  candidateDocuments: null,
  claimReasons: null,
};

export const claimingReducer = (
  state: ClaimingReducerType = initialState,
  action: Action
) => {
  // let index;
  switch (action.type) {
    case ActionTypes.GET_CLAIMING_TYPES:
      return {
        ...state,
        claimTypes: action.payload,
      };
    case ActionTypes.SELECT_CLAIM_TYPE:
      return {
        ...state,
        selectedClaimType: action.payload,
      };
    case ActionTypes.GET_MY_CLAIMS:
      return {
        ...state,
        myClaims: action.payload,
      };
    case ActionTypes.GET_ALL_CLAIMS:
      return {
        ...state,
        allClaims: action.payload,
      };
    case ActionTypes.GET_ALL_APPLICATION:
      return {
        ...state,
        allApplications: [...state.allApplications, action.payload],
      };
    case ActionTypes.CREATE_CLAIM:
      if (state.myClaims === null) {
        return {
          ...state,
          myClaims: [action.payload],
        };
      }
      return {
        ...state,
        myClaims: [action.payload, ...state.myClaims],
      };
    case ActionTypes.GET_CANDIDATES_APPLICATIONS:
      return {
        ...state,
        candidateApplications: action.payload,
      };
    case ActionTypes.GET_CANDIDATES_DOCUMENTS:
      return {
        ...state,
        candidateDocuments: action.payload,
      };
    case ActionTypes.EVALUATE_CLAIM:
      if (state.allClaims) {
        let _allClaim: CandidateClaimInterface[] = [...state.allClaims];
        let index: number = _allClaim.findIndex((claim) =>
          action.payload.id
            ? claim._id === action.payload.id
            : claim._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _allClaim[index]._id = action.payload.id
            ? action.payload.id
            : action.payload._id;
          _allClaim[index].claim_level = action.payload.claim_level;
          _allClaim[index].claims = action.payload.claims;
          _allClaim[index].last_claim_status = action.payload.last_claim_status;
          _allClaim[index].period_id = action.payload.period_id;
          _allClaim[index].previous = action.payload.previous;
          _allClaim[index].updatedAt = action.payload.updatedAt;
        }
        return { ...state, allClaims: _allClaim };
      }
      return state;
    case ActionTypes.RE_CLAIM:
      if (state.myClaims) {
        let _myClaims: CandidateClaimInterface[] = [...state.myClaims];
        const index: number = _myClaims.findIndex((claim) =>
          action.payload.id
            ? claim._id === action.payload.id
            : claim._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _myClaims[index]._id = action.payload.id
            ? action.payload.id
            : action.payload._id;
          _myClaims[index].claim_level = action.payload.claim_level;
          _myClaims[index].claims = action.payload.claims;
          _myClaims[index].last_claim_status = action.payload.last_claim_status;
          _myClaims[index].period_id = action.payload.period_id;
          _myClaims[index].previous = action.payload.previous;
          _myClaims[index].updatedAt = action.payload.updatedAt;
        }
        return { ...state, myClaims: _myClaims };
      }
      return state;
    case ActionTypes.GET_CLAIM_REASON:
      return {
        ...state,
        claimReasons: action.payload,
      };
    case ActionTypes.GET_CANDIDATES_ANSWERS:
      return {
        ...state,
        myAnswer: action.payload,
      };
    case ActionTypes.GET_ALL_CANDIDATES_ANSWERS:
      return {
        ...state,
        allAnswer: [...state.allAnswer, action.payload],
      };
    case ActionTypes.GET_MY_PLACEMENT:
      return {
        ...state,
        myPlacement: action.payload,
      };
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
