import React, { FC, ReactElement, useState } from "react";
import { SchoolContract, SectorLocationItem } from "../../../../actions";
import Modal, { Themes, ModalSize } from "../../../../components/Modal/Modal";
import search from "../../../../utils/search";

type ProvinceSelectorProps = {
  close: () => void;
  data: SchoolContract[];
  sectorsData: SectorLocationItem[];
  selectItem: (school: SchoolContract) => void;
  selected: SchoolContract[];
  removeItem: (id: string) => void;
  maximum: number;
  loading: boolean;
  getSchoolsBySector: (sector_id: string) => void;
  error: string;
};

const ProvinceSelector: FC<ProvinceSelectorProps> = ({
  close,
  data,
  selectItem,
  removeItem,
  maximum,
  selected,
  loading,
  sectorsData,
  getSchoolsBySector,
}): ReactElement => {
  const [searchText, setSearchText] = useState("");
  const [selectedSector, setSelectedSector] = useState(false);

  let results = search(data, searchText, {
    school_name: true,
  }) as SchoolContract[];

  let itemsFull = maximum <= selected.length ? true : false;

  return (
    <Modal
      backDrop={true}
      backDropClose={true}
      close={() => close()}
      displayClose={true}
      theme={Themes.default}
      title={"Select schools"}
      padding={{
        body: false,
        title: true,
      }}
      widthSizeClass={ModalSize.large}
    >
      <div className="p-2 bg-gray-200">
        <div className="flex gap-x-2 items-center">
          <div className=" w-1/4">
            <label htmlFor="select-sector" className="text-sm">
              Select sector
            </label>
            <select
              id="select-sector"
              className="p-2 bg-white border block w-full "
              disabled={loading}
              onChange={(e) => {
                getSchoolsBySector(e.target.value);
                setSelectedSector(true);
              }}
            >
              <option value="">Select a sector</option>
              {sectorsData.map((itm) => (
                <option key={itm.sector_code} value={itm.sector_code}>
                  {itm.sector_name}
                </option>
              ))}
            </select>
          </div>
          <div className=" flex-1">
            <label htmlFor="search-text" className="text-sm">
              Search by name
            </label>
            <input
              disabled={loading}
              id="search-text"
              type="search"
              className="p-2 w-full rounded border bg-white block"
              placeholder="Search school"
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
            />
          </div>
        </div>

        <div className="flex">
          <p className="px-2 flex-1 text-gray-800">
            {selected.length} selected out of {maximum}
          </p>
          <p className="text-right">
            Results: <b>{data.length}</b>
          </p>
        </div>
      </div>
      <div className="bg-300 p-3 overflow-auto" style={{ height: "70vh" }}>
        {loading === true ? (
          <div className="p-8 text-center animate__fadeIn animate__animated animate__infinite text-3xl text-gray-500 font-light">
            Loading...
          </div>
        ) : results.length >= 1 ? (
          results.map((itm) => {
            let schoolExist: boolean =
              selected.find((el) => el.school_code === itm.school_code) ===
              undefined
                ? false
                : true;
            return (
              <div
                key={itm.school_code}
                className={`p-1 items-center group border my-1 rounded hover:bg-blue-50 ${
                  schoolExist === true
                    ? "bg-blue-100 text-blue-900 border-blue-200"
                    : ""
                } px-2 flex`}
              >
                <div className="flex-1 ">{itm.school_name}</div>

                {schoolExist === true ? (
                  <button
                    className="p-1 bg-red-300 px-2 rounded text-sm bg-opacity-40 hover:bg-red-700 text-red-700 hover:text-white group-hover:bg-opacity-100"
                    onClick={() => removeItem(itm.school_code)}
                  >
                    Remove
                  </button>
                ) : itemsFull === true ? null : (
                  <button
                    onClick={() => selectItem(itm)}
                    className="p-1 bg-blue-300 px-2 rounded text-sm bg-opacity-40 hover:bg-blue-700 text-blue-700 hover:text-white group-hover:bg-opacity-100"
                  >
                    Select
                  </button>
                )}
              </div>
            );
          })
        ) : (
          <div className="py-20 text-center text-3xl text-gray-500 font-light">
            {selectedSector === false
              ? "Please select a sector"
              : "No results found"}
          </div>
        )}
      </div>
    </Modal>
  );
};
export default ProvinceSelector;
