import axios from "axios";
import React, { Component, Fragment } from "react";
import { BsCheckCircle } from "react-icons/bs";
import { MdCheckCircle } from "react-icons/md";
import { Redirect } from "react-router";
import { Auth, candidateApplyStoreInterface } from "../../../../actions";
import Modal, { ModalSize, Themes } from "../../../../components/Modal/Modal";
import { GetApplicationsAPI } from "../../../../interfaces/application";
import { API } from "../../../../utils/api";
import { CandidateExams } from "../../../CandidateExams/CandidateExams";

interface CongratulationProps {
  auth: Auth;
  candidateApply: candidateApplyStoreInterface;
  cleanCandidateApply: () => void;
  selectedApplication: GetApplicationsAPI | undefined;
}
interface CongratulationState {
  redirect: boolean;
  showModal: boolean;
}

class Congratulation extends Component<
  CongratulationProps,
  CongratulationState
> {
  constructor(props: CongratulationProps) {
    super(props);

    this.state = {
      redirect: false,
      showModal: true,
    };
  }

  sendFeedBack = async (feedback: boolean) => {
    if (this.props.selectedApplication !== undefined) {
      try {
        const res = await axios.post(`${API}/feedback/save`, {
          user_id: this.props.auth.user.user_id,
          application_id: this.props.selectedApplication?.application_id,
          feedback: feedback,
        });
        console.log("Res: ", res);
      } catch (error) {}
    }
  };

  render() {
    if (this.state.redirect === true) {
      this.props.cleanCandidateApply();
      return <Redirect to="/applications" />;
    }
    return (
      <Fragment>
        <div className="bg-white mt-5 animate__animated animate__bounceIn">
          <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 mt-5 bg-white py-5">
            <div className="text-center mt-3">
              <div className="w-full flex items-center justify-center">
                <MdCheckCircle className="text-7xl text-green-600 animate__animated animate__jackInTheBox animate-" />
              </div>
              <div className="font-extrabold text-4xl">Congratulations</div>
              <div className="mt-5 font-light text-lg">
                <span>Congratulations, Dear </span>
                <span className="font-semibold uppercase">
                  {this.props.auth.user.fname} {this.props.auth.user.lname}{" "}
                </span>
                , you have completed your application for the{" "}
                <span className="font-bold">
                  {this.props.candidateApply.selectedPosition?.position_title}
                </span>
                <br />
                <span className="ml-2">
                  Note that, once you are shortlisted you will need to come back
                  to this system to choose the Examination center.
                </span>
                .
              </div>
              {/* Exams list */}
              <div className="text-left bg-gray-100 px-2 pl-6 pt-1 mt-2 rounded-md border">
                <div className="text-xl font-base text-left -ml-2 mt-2 text-blue-600 flex w-full items-center justify-between gap-2">
                  <div>Examination timetable</div>
                  <div className="underline cursor-pointer text-base font-bold">
                    {/* Download my timetable */}
                  </div>
                </div>
                {/* Table here */}
                <CandidateExams asComponent={true} />
              </div>
              <div className="flex w-full items-center justify-end py-3 mt-5 gap-3">
                <div
                  onClick={() => {
                    this.props.cleanCandidateApply();
                  }}
                  className="bg-gray-100 hover:bg-gray-200 text-gray-800 btn cursor-pointer font-light"
                >
                  Continue to apply
                </div>
                <div
                  onClick={() => {
                    this.setState({ redirect: true });
                  }}
                  className="bg-blue-100 hover:bg-blue-200 text-blue-600 btn cursor-pointer font-light border border-blue-500"
                >
                  Go to applications
                </div>
              </div>
            </div>
          </div>
        </div>
        {this.state.showModal === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ showModal: false })}
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{
              title: undefined,
              body: true,
              footer: undefined,
            }}
          >
            <div>
              <div className="flex flex-col items-center justify-center gap-3 py-5  animate__animated animate__bounceIn">
                <div>
                  <BsCheckCircle className="text-8xl text-green-600" />
                </div>
                <div className="font-extrabold text-4xl">Congratulations</div>
                <div>
                  <div className="mt-5 font-light text-lg text-center">
                    <span>Congratulations, Dear </span>
                    <span className="font-semibold uppercase">
                      {this.props.auth.user.fname} {this.props.auth.user.lname}{" "}
                    </span>
                    , you have completed your application for the{" "}
                    <span className="font-bold">
                      {
                        this.props.candidateApply.selectedPosition
                          ?.position_title
                      }
                    </span>
                    <br />
                    <span className="ml-2">
                      Note that, once you are shortlisted you will need to come
                      back to this system to choose the Examination center.
                    </span>
                    .
                  </div>
                </div>
                <div className="font-bold mt-3">
                  How was the application process?
                </div>
                <div className="flex flex-row items-center justify-center gap-5">
                  <div
                    onClick={() => {
                      this.sendFeedBack(true);
                      this.setState({ showModal: false });
                    }}
                    className="btn bg-white border border-gray-500 hover:bg-gray-500 hover:text-white font-light text-gray-600 cursor-pointer"
                  >
                    It was easy
                  </div>
                  <div
                    onClick={() => {
                      this.sendFeedBack(false);
                      this.setState({ showModal: false });
                    }}
                    className="btn bg-white border border-blue-500 hover:bg-blue-500 hover:text-white font-light text-blue-600 cursor-pointer"
                  >
                    It was difficult
                  </div>
                </div>
              </div>
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}

export default Congratulation;
