import { ActionTypes } from "./types";
import { ApplicationStepsTypes } from "../containers/Application/ApplicationSteps/ApplicationSteps";
import { PositionAPI } from "../interfaces/index";
import { LocationTableInterface } from "../containers/Application/ExamCenter/ChooseExamCenter";
import { sentExamInterface } from "../containers/Application/ExamCenter/ExamCenter";
import { CandidatePlacementContract } from "../interfaces/candidate_placement_contract";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface setCandidateApplicationStepAction {
  type: ActionTypes.SET_CANDIDATE_APPLICATION_STEP;
  payload: ApplicationStepsTypes;
}

export interface setCandidateExamCenterAction {
  type: ActionTypes.SET_CANDIDATE_EXAM_CENTER;
  payload: {
    location: LocationTableInterface;
    exam: sentExamInterface;
  };
}

export interface setInitializeCandidateApplicationStepAction {
  type: ActionTypes.INITIALIZE_APPLICATION_PROCESS;
  payload: {
    user_id: string | null;
    position: PositionAPI | null;
  };
}

export interface setCompleteApplyStepAction {
  type: ActionTypes.SET_COMPLETE_APPLY;
  payload: boolean;
}

export interface cleanCandidateApplyAction {
  type: ActionTypes.CLEAN_CANDIDATE_APPLY;
}

export interface candidateApplyStoreInterface {
  user_id: string | null;
  currentStep: ApplicationStepsTypes;
  selectedPosition: PositionAPI | null;
  contract: CandidatePlacementContract | null;
  completed: boolean;
  exams:
    | {
        location: LocationTableInterface;
        exam: sentExamInterface;
      }[]
    | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const setCandidateApplicationStep = (
  step: ApplicationStepsTypes
): setCandidateApplicationStepAction => {
  return {
    type: ActionTypes.SET_CANDIDATE_APPLICATION_STEP,
    payload: step,
  };
};

export const initializeApplicationProcess = (
  position: PositionAPI | null,
  user_id: string | null
): setInitializeCandidateApplicationStepAction => {
  return {
    type: ActionTypes.INITIALIZE_APPLICATION_PROCESS,
    payload: {
      user_id: user_id,
      position: position,
    },
  };
};

export const addCandidateExamLocation = (
  location: LocationTableInterface,
  exam: sentExamInterface
): setCandidateExamCenterAction => {
  return {
    type: ActionTypes.SET_CANDIDATE_EXAM_CENTER,
    payload: {
      location: location,
      exam: exam,
    },
  };
};

export const setApplyCompletionStatus = (
  status: boolean
): setCompleteApplyStepAction => {
  return {
    type: ActionTypes.SET_COMPLETE_APPLY,
    payload: status,
  };
};

export const cleanCandidateApply = (): cleanCandidateApplyAction => {
  return {
    type: ActionTypes.CLEAN_CANDIDATE_APPLY,
  };
};
