import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  FC_ForgetPassword,
  FC_ForgetPassword_Check,
  FC_ForgetPassword_Interface,
  FC_ForgetPassword_Check_Interface,
} from "../../actions";
import { StoreState } from "../../reducers";
import { validateEmail } from "../../utils/validation";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import ForgetPasswordChange from "./ForgetPasswordChange";
import {
  MdVerifiedUser,
  MdOutlineMarkEmailUnread,
  MdPhoneIphone,
  MdClose,
} from "react-icons/md";

// props for the component
interface AppProps {
  auth: Auth;
  FC_ForgetPassword: Function;
  FC_ForgetPassword_Check: Function;
  openLogin: () => void;
  close: () => void;
}

enum ValueType {
  email = "email",
  phone = "phone",
}
enum errorType {
  main_error,
  main_success,
  address,
  none,
}

interface AppState {
  type: ValueType;
  address: string;
  loading: boolean;
  error: {
    target: errorType;
    msg: string;
  };
  step_two: boolean;
  success: boolean;
}

class _ForgetPassword extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      type: ValueType.email,
      loading: false,
      address: "",
      error: { target: errorType.none, msg: "" },
      step_two: false,
      success: false,
    };
  }

  handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ): void => {
    this.setState({
      ...this.state,
      error: {
        target: errorType.none,
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (this.state.address.length <= 4) {
      return this.setState({
        error: {
          target: errorType.address,
          msg: "Required",
        },
      });
    }

    if (
      this.state.type === ValueType.email &&
      !validateEmail(this.state.address)
    ) {
      return this.setState({
        error: {
          target: errorType.address,
          msg: "Pleas enter your email",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: errorType.none,
        msg: "",
      },
    });
    // * Reset password
    this.props.FC_ForgetPassword(
      {
        address: this.state.address,
        verify_type: this.state.type,
      } as FC_ForgetPassword_Interface,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: errorType.none,
              msg: "",
            },
            step_two: true,
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: errorType.main_error,
              msg: msg,
            },
          });
        }
      }
    );
  };

  //* Reset password reset
  resetPasswordHandler = (data: { code: string; password: string }) => {
    this.setState({ loading: true });
    this.props.FC_ForgetPassword_Check(
      {
        address: this.state.address,
        new_password: data.password,
        verification_code: data.code,
      } as FC_ForgetPassword_Check_Interface,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: errorType.main_success,
              msg: "Password has been changed",
            },
            success: true,
          });
        } else {
          this.setState({
            loading: false,
            error: { target: errorType.main_error, msg: msg },
          });
        }
      }
    );
  };
  render() {
    return (
      <>
        <div
          className="fixed animate__animated animate__fadeIn animate__slower top-0 bottom-0 right-0 left-0 z-50 bg-red bg-opacity-20"
          onClick={this.props.close}
        ></div>

        <div className="animate__animated animate__zoomIn fixed top-0 right-0 m-auto pt-20 z-lg left-0  w-80">
          <>
            {this.state.step_two === true ? (
              <div className="">
                {this.state.success === true ? (
                  <div className="mt-6 text-center animate__animated animate__zoomIn ">
                    <MdVerifiedUser className="text-green-600 text-7xl inline-block" />
                    <h2 className="text-2xl font-bold mt-2 text-green-700">
                      Password updated
                    </h2>
                    <p className="text-gray-500 mt-2">
                      your password has been updates <br />
                      you can now log-in with your new password
                    </p>
                    <div className="mt-2">
                      <button
                        type="button"
                        onClick={this.props.openLogin}
                        className="px-4 py-2 bg-green-100 text-green-800 rounded mt-2 hover:bg-green-600 hover:text-white"
                      >
                        Login
                      </button>
                    </div>
                  </div>
                ) : (
                  <div className="mt-4">
                    {this.state.error.target === errorType.main_error && (
                      <div className="mx-auto w-full">
                        <Alert
                          title={this.state.error.msg}
                          theme={AlertInterface.danger}
                        />
                      </div>
                    )}
                    {this.state.error.target === errorType.main_success && (
                      <div className="mx-auto w-full">
                        <Alert
                          title={this.state.error.msg}
                          theme={AlertInterface.success}
                        />
                      </div>
                    )}
                    <ForgetPasswordChange
                      submitData={this.resetPasswordHandler}
                      close={() =>
                        this.setState({
                          step_two: false,
                        })
                      }
                      address={this.state.address}
                    />
                  </div>
                )}
              </div>
            ) : (
              <div className="bg-white border border-gray-200 shadow-xl mx-auto px-4 py-5 rounded  mt-6 sm:mx-auto  w-80">
                <form onSubmit={this.submitHandler}>
                  <div className="flex w-full items-center">
                    <h1 className="px-2 text-2xl text-blue-500  pt-2 pb-4 flex-1">
                    Reset password
                  </h1>
                  <button
                      type="button"
                      onClick={this.props.close}
                      className=" bg-red-200 text-red-800 p-1 rounded-full text-xl"
                    >
                      <MdClose />
                    </button>
                  </div>
                  

                  {this.state.error.target === errorType.main_error && (
                    <div className="p-2">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.danger}
                      />
                    </div>
                  )}

                  {this.state.error.target === errorType.main_success && (
                    <div className="mx-auto w-full">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.success}
                      />
                    </div>
                  )}

                  <div className="p-2 mt-1">
                    <div className="">
                      <div className="">
                        <div className=" text-gray-900 ">
                          Where do you want to receive the code?
                        </div>
                        <div className="pb-6 flex w-full gap-2">
                          {[
                            {
                              title: "Email",
                              icon: MdOutlineMarkEmailUnread,
                              value: ValueType.email,
                            },
                            {
                              title: "SMS",
                              icon: MdPhoneIphone,
                              value: ValueType.phone,
                            },
                          ].map(({ title, icon, value }, i) => {
                            const Icon = icon;
                            return (
                              <button
                                key={i}
                                type="button"
                                className={`text-center flex-1 border-2 hover:bg-blue-600 hover:text-white hover:border-blue-700 border-gray-200 rounded p-2
                                  ${
                                    this.state.type === value
                                      ? "bg-blue-100 border-2 text-blue-800 border-blue-700"
                                      : "text-gray-700"
                                  }
                                  `}
                                onClick={() => this.setState({ type: value })}
                              >
                                <h4>
                                  <Icon className="mx-auto text-4xl" />
                                </h4>
                                <h6 className="text-sm">{title}</h6>
                              </button>
                            );
                          })}
                        </div>
                      </div>

                      <div className="">
                        <label htmlFor="" className="text-gray-900">
                          {this.state.type === ValueType.email
                            ? "email"
                            : "Phone"}{" "}
                          used on your account
                        </label>
                        <input
                          type={
                            this.state.type === ValueType.email
                              ? "email"
                              : "text"
                          }
                          name="address"
                          onChange={this.handleChange}
                          className={`input-white w-20 bg-gray-100 inline-block  ${
                            this.state.error?.target === errorType.address
                              ? "border-red-500 border-2"
                              : "input-border"
                          }       `}
                          placeholder={`${
                            this.state.type === ValueType.email
                              ? "my_email@mail.com"
                              : "0700000000"
                          }`}
                          value={this.state.address}
                          disabled={this.state.loading}
                        />
                        {this.state.error?.target === errorType.address && (
                          <span className="text-red-600">
                            {this.state.error?.msg}
                          </span>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="p-2">
                    <div className="flex ">
                      <div className=" ">
                        <button
                          type="button"
                          onClick={this.props.openLogin}
                          className="font-bold text-blue-600 underline"
                        >
                          Login
                        </button>
                      </div>

                      <div className="flex-1  text-right">
                        <button
                          className={`py-2 font-bold  px-8 rounded bg-hero text-white inline-block`}
                          disabled={this.state.loading}
                          type="submit"
                        >
                          {this.state.loading === true ? (
                            <b className="animate__animated animate__fadeIn animate__infinite">
                              Resetting...
                            </b>
                          ) : (
                            "Reset password"
                          )}
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            )}
          </>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({ todos, auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const ForgetPassword = connect(mapStateToProps, {
  FC_ForgetPassword,
  FC_ForgetPassword_Check,
})(_ForgetPassword);
