import { QuestionAnswersStore } from "../../../actions/candidate_exam";
import { questionTypesStoreInterface } from "../../../actions/questionTypes";
import { exam_question } from "../../../interfaces/exam_question";
import {
  QuestionInterface,
  questionType,
  questionTypeElements,
} from "../../../interfaces/question";
import { newAnswer } from "../DoExam";
import { questionTypeElementsType } from "@mastrecruit/common";

export const findResult = (
  examQuestions: exam_question[],
  answersList: newAnswer[]
): number => {
  let marks: number = 0;
  if (answersList !== undefined && answersList !== null) {
    for (let i in answersList) {
      let answer = answersList;
      if (answer[i].user_answers?.status === true) {
        marks = marks + 1;
      }
    }
  }
  return examQuestions !== undefined ? (marks * 100) / examQuestions.length : 0;
};

export const findQuestionType = (
  question_type_id: string | null | undefined,
  question_types: questionType[] | null
): questionTypeElementsType | null => {
  let res: questionTypeElementsType | null = null;
  if (question_types !== null && question_type_id !== undefined) {
    let item: questionType | undefined = question_types.find(
      (itm) => itm.id === question_type_id
    );
    if (item !== undefined) {
      let element = questionTypeElements.find(
        (x) => item && x.key === item.element
      );
      if (element !== undefined) {
        res = element.key;
      }
    }
  }
  return res;
};

export const getRandomArbitrary = (min: number, max: number) => {
  return Math.random() * (max - min) + min;
};

export const questionAnswered = (
  questionsAnswers: QuestionAnswersStore[],
  question: QuestionInterface,
  question_type: questionTypeElementsType
): boolean => {
  let res = false;
  let answersTemp = questionsAnswers.find(
    (x) => x.question_id === question.question_id
  );
  if (answersTemp !== undefined) {
    if (
      answersTemp.answers.length > 0 &&
      answersTemp.answers[0].answer.length !== 0
    ) {
      switch (question_type) {
        case questionTypeElementsType.SingleMultiple:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.status === true)
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.MultipleChoice:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.status === true)
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.CalculatedMultipleChoice:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.status === true)
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.Matching:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.status === "")
              .length === 0
              ? true
              : false;
          break;
        case questionTypeElementsType.TrueFalse:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.status === true)
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.Numeric:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.value[0] !== "")
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.ShortAnswer:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.value !== "")
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.SimpleCalculate:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.value[0] !== "")
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.Essay:
          res =
            answersTemp.answers[0].answer.filter((itm) => itm.value !== "")
              .length === 0
              ? false
              : true;
          break;
        case questionTypeElementsType.Embedded:
          if (question.question.length > 0) {
            if (
              question.question[0].sub_question.length ===
              answersTemp.answers.length
            ) {
              res = true;
              for (let item of answersTemp.answers) {
                if (
                  item.answer.filter((test) => test.value === "").length !== 0
                ) {
                  res = false;
                  break;
                }
              }
            }
          }
          break;
        case questionTypeElementsType.Hybrid:
          if (question.question.length > 0) {
            console.log("qqq: ", answersTemp.answers);
            if (
              question.question[0].sub_question.length ===
              answersTemp.answers.length
            ) {
              res = true;
              for (let itemTest of answersTemp.answers) {
                if (
                  itemTest.answer.filter((testQ) => testQ.value.length === 0)
                    .length !== 0
                ) {
                  res = false;
                  break;
                }
              }
            }
          }
          break;
      }
    }
  }
  return res;
};

export const NumberOfAnsweredQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[],
  questionTypes: questionTypesStoreInterface
) => {
  let tot = 0;
  for (let test of questions) {
    if (
      questionAnswered(
        answers,
        test,
        findQuestionType(test.type, questionTypes.questionTypes)!
      ) === true
    ) {
      tot = tot + 1;
    }
  }
  return tot;
};

export const ExamAnsweredQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[],
  questionTypes: questionTypesStoreInterface
) => {
  let res: QuestionInterface[] = [];
  for (let test of questions) {
    if (
      questionAnswered(
        answers,
        test,
        findQuestionType(test.type, questionTypes.questionTypes)!
      ) === true
    ) {
      res = [...res, test];
    }
  }
  return res;
};

export const ExamPendingQuestions = (
  answers: QuestionAnswersStore[],
  questions: QuestionInterface[],
  questionTypes: questionTypesStoreInterface
) => {
  let res: QuestionInterface[] = [];
  for (let test of questions) {
    if (
      questionAnswered(
        answers,
        test,
        findQuestionType(test.type, questionTypes.questionTypes)!
      ) === false
    ) {
      res = [...res, test];
    }
  }
  return res;
};
