import { Action, ActionTypes, LocationAPI } from "../actions";
import { CentersInterface, RoomLocationInterface } from "../shared/interfaces";

export interface LocationReducerInterface {
  roomLocation: RoomLocationInterface[];
  waitingLocation: LocationAPI | null;
}

// default state
const initialState = {
  roomLocation: [],
  waitingLocation: null,
};

export const locationsReducer = (
  state: LocationReducerInterface = initialState,
  action: Action
) => {
  let index: number, index_2: number, index_3: number, index_4: number;
  let location: RoomLocationInterface[] = [];
  let center: CentersInterface[] = [];
  switch (action.type) {
    case ActionTypes.GET_ALL_ROOM:
      return {
        ...state,
        roomLocation: action.payload,
      };
    case ActionTypes.CREATE_PROVINCE:
      return {
        ...state,
        roomLocation: [...state.roomLocation, action.payload],
      };
    case ActionTypes.UPDATE_PROVINCE:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        state.roomLocation[index].province_name = action.payload.province_name;
        state.roomLocation[index].province_code = action.payload.provice_code;
      }
      return state;
    case ActionTypes.DELETE_PROVINCE:
      return {
        ...state,
        roomLocation: state.roomLocation.filter(
          (element) => element._id !== action.payload
        ),
      };
    case ActionTypes.CREATE_DISTRICT:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        state.roomLocation[index].districts.push(action.payload.data);
      }
      return state;
    case ActionTypes.UPDATE_DISTRICT:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          state.roomLocation[index].districts[index_2].district_name =
            action.payload.district_name;
          state.roomLocation[index].districts[index_2].district_code =
            action.payload.district_code;
        }
      }
      return state;
    case ActionTypes.DELETE_DISTRICT:
      let forDistrict: RoomLocationInterface[] = [...state.roomLocation];
      state.roomLocation.forEach((element, index) => {
        if (element._id === action.payload.province_id) {
          forDistrict[index].districts = state.roomLocation
            .filter(
              (province) => province._id === action.payload.province_id
            )[0]
            .districts.filter(
              (element) => element._id !== action.payload.district_id
            );
        }
      });
      return {
        ...state,
        roomLocation: forDistrict,
      };
    case ActionTypes.CREATE_SECTOR:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (item) => item._id === action.payload.district_id
        );
        if (index_2 >= 0) {
          state.roomLocation[index].districts[index_2].sectors.push(
            action.payload.data
          );
        }
      }
      return state;
    case ActionTypes.UPDATE_SECTOR:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          index_3 = state.roomLocation[index].districts[
            index_2
          ].sectors.findIndex(
            (item) => item.sector_id === action.payload.sector_id
          );
          if (index_3 >= 0) {
            state.roomLocation[index].districts[index_2].sectors[
              index_3
            ].sector_name = action.payload.sector_name;
            state.roomLocation[index].districts[index_2].sectors[
              index_3
            ].sector_code = action.payload.sector_code;
          }
        }
      }
      return state;
    case ActionTypes.DELETE_SECTOR:
      forDistrict = [...state.roomLocation];
      state.roomLocation.forEach((element, index) => {
        if (element._id === action.payload.province_id) {
          element.districts.forEach((item, indexDistrict) => {
            if (item._id === action.payload.district_id) {
              forDistrict[index].districts[indexDistrict].sectors =
                state.roomLocation
                  .filter(
                    (province) => province._id === action.payload.province_id
                  )[0]
                  .districts.filter(
                    (dis_element) =>
                      dis_element._id !== action.payload.district_id
                  )[0]
                  .sectors.filter(
                    (sec_element) =>
                      sec_element.sector_id !== action.payload.sector_id
                  );
            }
          });
        }
      });
      return {
        ...state,
        roomLocation: forDistrict,
      };
    case ActionTypes.CREATE_CENTER:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );
        if (index_2 >= 0) {
          state.roomLocation[index].districts[index_2].centers.push(
            action.payload.data
          );
        }
      }
      return state;
    case ActionTypes.UPDATE_CENTER:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          index_3 = state.roomLocation[index].districts[
            index_2
          ].centers.findIndex(
            (center) => center._id === action.payload.examination_center_id
          );

          if (index_3 >= 0) {
            state.roomLocation[index].districts[index_2].centers[
              index_3
            ].center_name = action.payload.center_name;
          }
        }
      }
      return state;
    case ActionTypes.DELETE_CENTER:
      location = [...state.roomLocation];
      center = [];
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          center = state.roomLocation[index].districts[index_2].centers.filter(
            (element) => element._id !== action.payload.center_id
          );
        }
        location[index].districts[index_2].centers = center;
      }
      return { ...state, roomLocation: location };
    case ActionTypes.CREATE_ROOM:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );
        if (index_2 >= 0) {
          index_3 = state.roomLocation[index].districts[
            index_2
          ].centers.findIndex(
            (center) => center._id === action.payload.center_id
          );
          if (index_3 >= 0) {
            state.roomLocation[index].districts[index_2].centers[
              index_3
            ].rooms.push(action.payload.data);
          }
        }
      }
      return state;
    case ActionTypes.UPDATE_ROOM:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          index_3 = state.roomLocation[index].districts[
            index_2
          ].centers.findIndex(
            (center) => center._id === action.payload.center_id
          );

          if (index_3 >= 0) {
            index_4 = state.roomLocation[index].districts[index_2].centers[
              index_3
            ].rooms.findIndex((room) => room._id === action.payload.room_id);

            if (index_4 >= 0) {
              state.roomLocation[index].districts[index_2].centers[
                index_3
              ].rooms[index_4].room_number = action.payload.room_number;
              state.roomLocation[index].districts[index_2].centers[
                index_3
              ].rooms[index_4].capacity = action.payload.capacity;
            }
          }
        }
      }
      return state;
    case ActionTypes.DELETE_ROOM:
      index = state.roomLocation.findIndex(
        (province) => province._id === action.payload.province_id
      ); //finding index of the item
      if (index >= 0) {
        index_2 = state.roomLocation[index].districts.findIndex(
          (district) => district._id === action.payload.district_id
        );

        if (index_2 >= 0) {
          index_3 = state.roomLocation[index].districts[
            index_2
          ].centers.findIndex(
            (center) => center._id === action.payload.center_id
          );

          if (index_3 >= 0) {
            state.roomLocation[index].districts[index_2].centers[
              index_3
            ].rooms = state.roomLocation[index].districts[index_2].centers[
              index_3
            ].rooms.filter((element) => element._id !== action.payload.room_id);
          }
        }
      }
      return state;
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    case ActionTypes.GET_LOCATION_WAITING_LIST:
      return {
        ...state,
        waitingLocation: action.payload,
      };
    default:
      return state;
  }
};
