export enum examStatus {
  Waiting = "Waiting",
  Pending = "Pending",
  Paused = "Paused",
  Editing = "Editing",
  Done = "Done",
  Canceled = "Canceled",
  Active = "Active",
}

export enum UserExamStatus {
  // Not Done Exam
  // NotDone = "NotDone",
  // when user start exam like nswering question
  Editing = "Editing",
  // when user have been paused
  Paused = "Paused",
  // when user have been removed from exam
  Removed = "Removed",
  // when user have been finished doin exam
  Done = "Done",
}

export interface GetUserAnswers {
  question: string;
  user: string;
  number_of_tries: number;
  marks_of_each_try: number[];
  language: LanguageInterface | QuestionLanguage;
  answers: QuestionAnswerInterface[];
  marked: boolean;
  markedBy: string;
  reason: string;
  user_marks: number;
  createdAt: string;
  updatedAt: string;
  answer_id: string;
}

export interface UserExamsListInterface {
  _id: string;
  period: string;
  course: string;
  course_name: string;
  course_code: string;
  questions: string[];
  exam_status: examStatus;
  termes: string;
  questions_per_page: number;
  exam_date: string;
  start_time: string;
  end_time: string;
  duration: number;
  position: string;
  number_format: QuestionNumberFormat;
  discription: string;
  found: boolean;
  user_marks: number;
  rules: {
    right_click_enabled: boolean;
    copy_enabled: boolean;
    paste_enabled: boolean;
    stop_candidate_when_comeback: boolean;
    allow_to_leave_browser: boolean;
  };
  user_status: UserExamStatus;
  status: number;
  createdAt: string;
  updatedAt: string;
}

export interface ExamInterface {
  exam_id: string;
  exam_date_time: Date;
  duration: number;
  course: {
    course_id: string;
    course_name: string;
    course_code: string;
  };
  exam_location: {
    province: string;
    district: string;
    center: string;
    room: string;
  };
  proctoring_type: string;
  academic_year: string;
  exam_questions: exam_question[];
  exam_questions_answers: examQuestionAnswerInterface;
  status: examStatus;
}

