import {
  Proctoring,
  Action,
  ActionTypes,
  ActiveCandidateLiveServer,
} from "../actions";
import {
  CandidateProctoringInterface,
  CandidateProctoringInterfaceAPI,
  ProctoringExam,
  RoomServer,
} from "../interfaces";
// default state

const defaultCandidateEmpty = {
  course_code: "",
  course_id: "",
  course_name: "",
  district: "",
  education_institute_id: "",
  email: "",
  fname: "No first name",
  lname: "No last name",
} as CandidateProctoringInterfaceAPI;

const defaultMe = {
  audio: false,
  hand: false,
  mic: false,
  active: false,
  email: "",
  fname: "",
  lname: "",
  stopped: false,
  user_id: "",
  muted: false,
  peer: "",
  peer_id: "",
  course_code: "",
  course_id: "",
  course_name: "",
  district: "",
  education_institute_id: "",
  n_id: "",
  phone_number: "",
  position_id: "",
  position_title: "",
  school_level_id: "",
  done: false,
  joined: true,
  removed: false,
  room_id: "",
  supervisor: false,
  seat_number: 0,
  added_minutes: 0,
  start_added_time: "",
  duration: 0,
  group_id: "",
  stream_id: "",
  device_id: "",
  socket_id: "",
  start_time: "",
  ai: false,
} as CandidateProctoringInterface;

const defaultRoom = {
  loaded: false,
  center_id: "",
  center_name: "",
  district_id: "",
  district_name: "",
  province_id: "",
  province_name: "",
  room_id: "",
  room_number: "",
} as RoomServer;

const defaultExam = {
  video_support: false,
  start_exam: false,
  start_time: "",
  end_time: "",
  exam_done: false,
  period_id: "",
  exam_status: "",
  exam_date: "",
  duration: 0,
  position_id: "",
  position_title: "",
  course_id: "",
  course_code: "",
  course_name: "",
  room_id: "",
  exam_id: "",
} as ProctoringExam;

const defaultState = {
  my_stream: {
    stream: null,
    error: false,
    socket_id: "",
    peer_id: "",
    stream_id: "",
  },
  streams: [],
  focus: [],
  events: [],
  room: defaultRoom,
  all_candidates: [],
  candidates: [],
  candidate_loaded: false,
  me: defaultMe,
  room_exams: [],
  room_exams_loaded: false,
  exam: defaultExam,
  exam_rules: {
    allow_to_leave_browser: true,
    copy_enabled: true,
    paste_enabled: true,
    right_click_enabled: true,
    stop_candidate_when_comeback: true,
  },
  exams_list: {
    exams: [],
    loaded: false,
  },
  room_not_found: false,
  error: "",
  room_loaded: false,
  room_groups: null,
  supervisor_audio: {
    mic: false,
    audio: false,
  },
  hand: false,
} as Proctoring;

/**
 * @param state
 * @param action
 * @returns
 */
export const proctoringReducer = (
  state: Proctoring = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.ADD_ROOM_DETAILS:
      return {
        ...state,
        candidates: action.payload.candidates,
        room: { ...state.room, ...action.payload.room },
      } as Proctoring;
    case ActionTypes.ADD_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map(
          (cand: CandidateProctoringInterface) => {
            // find the current candidate to the list of active candidates
            let candExists = action.payload.find(
              (itm: ActiveCandidateLiveServer) => itm.user_id === cand.user_id
            );
            return {
              ...cand,
              active: candExists === undefined ? false : true,
            } as CandidateProctoringInterface;
          }
        ),
      } as Proctoring;

    case ActionTypes.ADD_ONE_ACTIVE_USER_TO_ROOM:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload.user_id
            ? {
                ...itm,
                ...action.payload,
                active: true,
              }
            : itm
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_OFFLINE:
      return {
        ...state,
        candidates: state.candidates.map((itm: CandidateProctoringInterface) =>
          itm.user_id === action.payload
            ? { ...itm, active: false, stopped: true, audio: false, mic: false }
            : itm
        ),
        focus: state.focus.filter((cand) => cand !== action.payload),
      } as Proctoring;
    case ActionTypes.EXAM_START_STOP:
      let data_exam = {
        ...state.exam,
        start_exam: action.payload.status,
      };
      if (action.payload.start_time !== "" && action.payload.duration !== 0) {
        data_exam = {
          ...data_exam,
          start_time: action.payload.start_time,
          duration: action.payload.duration,
        };
      }
      return {
        ...state,
        exam: data_exam,
      } as Proctoring;
    case ActionTypes.CANDIDATE_CONTINUE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: false } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload ? { ...cand, stopped: true } : cand
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_REMOVE_EXAM:
      return {
        ...state,
        candidates: state.candidates.filter(
          (cand) => cand.user_id !== action.payload
        ),
      } as Proctoring;

    case ActionTypes.CANDIDATE_EXAM_EVENT:
      return {
        ...state,
        candidates: state.candidates.map((cand: CandidateProctoringInterface) =>
          cand.user_id === action.payload.user_id
            ? { ...cand, stopped: true }
            : cand
        ),
        events: [...state.events, action.payload],
      } as Proctoring;
    case ActionTypes.ADD_MY_DATA_TO_PROCTORING:
      return {
        ...state,
        me: action.payload as CandidateProctoringInterface,
      } as Proctoring;
    case ActionTypes.ME_START_STOP_EXAM:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
        exam: {
          ...state.exam,
          start_exam: action.payload,
          exam_done: !action.payload,
        },
      } as Proctoring;
    case ActionTypes.BLOCK_EXAM:
      return {
        ...state,
        me: {
          ...state.me,
          stopped: action.payload,
        },
      } as Proctoring;
    case ActionTypes.LOAD_ROOM_DETAILS:
      return {
        ...state,
        room: {
          ...action.payload.room,
          loaded: true,
        },
        room_exams: action.payload.room_exams,
        room_exams_loaded: true,
      };
    case ActionTypes.PROCTORING_GET_EXAMS_LIST:
      return {
        ...state,
        exams_list: {
          ...state.exams_list,
          loaded: true,
          exams: action.payload,
        },
      };
    case ActionTypes.LOAD_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: action.payload,
      } as Proctoring;
    case ActionTypes.CLEAR_EXAM_DETAILS_TO_ROOM_PROCTORING:
      return {
        ...state,
        exam: defaultState.exam,
        candidate_loaded: false,
        candidates: [],
      } as Proctoring;
    case ActionTypes.ADD_CANDIDATES_ROOM_PROCTORING:
      return {
        ...state,
        candidate_loaded: true,
        all_candidates: action.payload.candidates,
        room_groups: action.payload.groups,
        candidates: [],
      } as Proctoring;
    case ActionTypes.PROCTORING_ADD_EVENT:
      return {
        ...state,
        events: [action.payload, ...state.events],
      } as Proctoring;
    case ActionTypes.PROCTORING_LOAD_EVENTS:
      return {
        ...state,
        events: action.payload,
      } as Proctoring;
    case ActionTypes.UPDATE_THE_EXAM_START_STATUS:
      return {
        ...state,
        room: {
          ...state.room,
          start_exam: true,
          exam_done: false,
        },
        exam: {
          ...state.exam,
          start_exam: true,
          exam_done: false,
          start_time: action.payload,
        },
      } as Proctoring;
    case ActionTypes.ROOM_NOT_FOUND_ONLINE:
      return {
        ...state,
        room_not_found: true,
        error: action.payload,
      } as Proctoring;
    case ActionTypes.ROOM_DETAILS_ONLINE:
      return {
        ...state,
        room_loaded: true,
        room_not_found: false,
        candidates: action.payload.candidates
          .filter((cand) => cand.supervisor === false)
          .map((candidate) => {
            let findCandidateDetails:
              | CandidateProctoringInterfaceAPI
              | undefined = state.all_candidates.find(
              (itm) => itm.user_id === candidate.user_id
            );

            if (findCandidateDetails === undefined) {
              findCandidateDetails = defaultCandidateEmpty;
            }

            return {
              ...findCandidateDetails,
              ...candidate,
            };
          }),
        room: {
          ...state.room,
          group_id: action.payload.group_id,
        },
        exam: {
          ...state.exam,
          exam_done: action.payload.exam_done,
          start_exam: action.payload.start_exam,
          start_time: action.payload.start_time,
          duration: action.payload.duration,
        },
      } as Proctoring;
    case ActionTypes.ROOM_DETAILS_CANDIDATE_ONLINE:
      return {
        ...state,
        me: {
          ...state.me,
          ...action.payload.user,
          active: true,
        },
        room: {
          ...state.room,
          group_id: action.payload.group_id,
        },
        exam: {
          ...state.exam,
          start_exam: action.payload.start_exam,
          start_time: action.payload.start_time,
          exam_done: action.payload.exam_done,
        },
      } as Proctoring;
    case ActionTypes.CANDIDATE_DONE_EXAM:
      return {
        ...state,
        candidates: state.candidates.map((cand) =>
          cand.user_id === action.payload ? { ...cand, done: true } : cand
        ),
      } as Proctoring;
    case ActionTypes.CANDIDATE_STOP_EVENTS:
      return {
        ...state,
        exam: {
          ...state.exam,
          exam_done: action.payload,
        },
        me: {
          ...state.me,
          done: action.payload,
        },
      } as Proctoring;
    case ActionTypes.EXAM_DONE:
      return {
        ...state,
        exam: {
          ...state.exam,
          exam_done: action.payload,
        },
      } as Proctoring;
    case ActionTypes.ADD_TIME_TO_CANDIDATE:
      return {
        ...state,
        candidates: state.candidates.map((candid) =>
          candid.user_id === action.payload.user_id
            ? {
                ...candid,
                added_minutes: action.payload.duration,
                stopped: false,
                start_added_time: action.payload.start_time,
              }
            : candid
        ),
      } as Proctoring;
    case ActionTypes.ADD_EXAM_RULES:
      return {
        ...state,
        exam_rules: action.payload,
      } as Proctoring;
    case ActionTypes.SET_ROOM_TO_FINISH:
      let room_room_groups =
        state.room_groups === null ? [] : state.room_groups;
      return {
        ...state,
        room_groups: room_room_groups.map((group) =>
          group._id === action.payload ? { ...group, isExpired: true } : group
        ),
      } as Proctoring;
    case ActionTypes.CLEAR_PROCTORING:
      return {
        ...state,
        events: [],
        candidates: [],
        // room: defaultRoom,
        room_loaded: false,
        room_not_found: false,
        exam: defaultExam,
        error: "",
        candidate_loaded: false,
        all_candidates: [],
        room_groups: null,
        exam_rules: {
          allow_to_leave_browser: true,
          copy_enabled: true,
          paste_enabled: true,
          right_click_enabled: true,
          stop_candidate_when_comeback: true,
        },
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__ADD_USER_STREAM:
      return {
        ...state,
        streams: [
          ...state.streams.filter((stream) => stream.id !== action.payload.id),
          action.payload,
        ],
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__ADD_MY_STREAM:
      return {
        ...state,
        my_stream: action.payload,
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SET_PEER_ID:
      return {
        ...state,
        my_stream: {
          ...state.my_stream,
          peer_id: action.payload,
        },
      } as Proctoring;
    case ActionTypes.LOGOUT:
      return {
        ...defaultState,
      };
    case ActionTypes.HOME_PROCTORING__FOCUS_ON_USER:
      return {
        ...state,
        focus:
          state.focus.indexOf(action.payload) >= 0
            ? state.focus.filter((itm) => itm !== action.payload)
            : state.focus.length <= 3
            ? [...state.focus, action.payload]
            : [state.focus[1], state.focus[2], state.focus[3], action.payload],
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SPEAK_TO_ALL:
      return {
        ...state,
        candidates: state.candidates.map((itm) => ({ ...itm, mic: true })),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__STOP_SPEAK_TO_ALL:
      return {
        ...state,
        candidates: state.candidates.map((itm) => ({ ...itm, mic: false })),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__LISTEN_ALL:
      return {
        ...state,
        candidates: state.candidates.map((itm) => ({ ...itm, audio: true })),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__STOP_LISTEN_ALL:
      return {
        ...state,
        candidates: state.candidates.map((itm) => ({ ...itm, audio: false })),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__ADD_MIC:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload ? { ...itm, mic: true } : itm
        ),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__STOP_MIC:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload ? { ...itm, mic: false } : itm
        ),
      } as Proctoring;

    case ActionTypes.HOME_PROCTORING__MIC_ONLY:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload
            ? { ...itm, mic: true }
            : { ...itm, mic: false }
        ),
      } as Proctoring;

    case ActionTypes.HOME_PROCTORING__ADD_SOUND:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload ? { ...itm, audio: true } : itm
        ),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__STOP_SOUND:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload ? { ...itm, audio: false } : itm
        ),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SOUND_ONLY:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload
            ? { ...itm, audio: true }
            : { ...itm, audio: false }
        ),
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SUPERVISOR_AUDIO:
      return {
        ...state,
        supervisor_audio: {
          ...state.supervisor_audio,
          audio: action.payload,
        },
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SUPERVISOR_MIC:
      return {
        ...state,
        supervisor_audio: {
          ...state.supervisor_audio,
          mic: action.payload,
        },
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SET_HAND:
      return {
        ...state,
        hand: action.payload,
      } as Proctoring;
    case ActionTypes.HOME_PROCTORING__SET_HAND_SUPERVISOR:
      return {
        ...state,
        candidates: state.candidates.map((itm) =>
          itm.user_id === action.payload.user_id
            ? { ...itm, hand: action.payload.status }
            : itm
        ),
      } as Proctoring;
    default:
      return state;
  }
};
