import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  selectPosition,
  positionsStoreInterface,
  FC_Access,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
  CandidateApplicationsStoreInterface,
  LoadCandidateApplications,
  SelectUserApplication,
  CandidatePlacementContractStoreInterface,
  FC_LoadCandidateContract,
  initializeContract,
  UpdateContractLocations,
  FC_getLocations,
  LocationAPI,
  FT_LoadCandidateContract,
  FC_LoadSchoolsByDistrict,
  FT_LoadSchoolsByDistrict,
  FC_LoadSchoolsByIDs,
  FT_LoadSchoolsByIDs,
  FC_SubmitContract,
  FT_SubmitContract,
} from "../../actions";
import Loading from "../../components/Loading/Loading";
import { GetApplicationsAPI } from "../../interfaces/application";
import {
  CandidatePlacementContract,
  PContractItem,
} from "../../interfaces/candidate_placement_contract";
import { StoreState } from "../../reducers";
import ApplicationsList from "./ApplicationsList/ApplicationsList";
import Contract from "./Contract/Contract";

export enum ContractCategory {
  PROVINCE = "PROVINCE",
  DISTRICT = "DISTRICT",
  SECTOR = "SECTOR",
  SCHOOL = "SCHOOL",
}

export type ContractCategoryType = { key: ContractCategory; title: string };

export const contractContentTypes: ContractCategoryType[] = [
  { key: ContractCategory.PROVINCE, title: "Provinces" },
  { key: ContractCategory.DISTRICT, title: "Districts" },
  { key: ContractCategory.SECTOR, title: "Sectors" },
  { key: ContractCategory.SCHOOL, title: "Schools" },
];

export interface ContractItem {
  key: ContractCategoryType;
  data: PContractItem;
}

export const GetContractLocationsList = (
  candidatePlacementContract: CandidatePlacementContract
): ContractItem[] => {
  let res: ContractItem[] = [];
  res = [
    ...res,
    {
      key: contractContentTypes[0],
      data: candidatePlacementContract.province,
    },
  ];
  res = [
    ...res,
    {
      key: contractContentTypes[1],
      data: candidatePlacementContract.district,
    },
  ];
  res = [
    ...res,
    {
      key: contractContentTypes[2],
      data: candidatePlacementContract.sector,
    },
  ];
  res = [
    ...res,
    {
      key: contractContentTypes[3],
      data: candidatePlacementContract.school,
    },
  ];
  return res;
};

interface PlacementContractProps {
  auth: Auth;
  candidateApplications: CandidateApplicationsStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  candidatePlacementContract: CandidatePlacementContractStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
  FC_LoadCandidateContract: FT_LoadCandidateContract;
  initializeContract: (
    user_id: string,
    position_id: string,
    session_id: string
  ) => void;
  UpdateContractLocations: (
    type: ContractCategoryType,
    data: PContractItem
  ) => void;
  FC_LoadSchoolsByDistrict: FT_LoadSchoolsByDistrict;
  FC_LoadSchoolsByIDs: FT_LoadSchoolsByIDs;
  FC_SubmitContract: FT_SubmitContract;
  SubmitApplication?: () => void;
  applicationGoBack?: () => void;
}
interface PlacementContractState {
  loading: boolean;
  locations: LocationAPI | null;
  loading_location: boolean;
  loading_error: boolean;
}

export class _PlacementContract extends Component<
  PlacementContractProps,
  PlacementContractState
> {
  constructor(props: PlacementContractProps) {
    super(props);

    this.state = {
      loading: false,
      locations: null,
      loading_location: true,
      loading_error: false,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  componentDidMount = () => {
    if (this.state.locations === null)
      FC_getLocations(
        (status: boolean, data: LocationAPI | null, msg: string) => {
          if (status === true && data !== null) {
            this.setState({
              locations: data,
              loading_location: false,
            });
          } else {
            this.setState({
              loading_location: false,
              loading_error: true,
            });
          }
        }
      );

    if (
      this.props.candidateApplications.candidate_applications === null ||
      this.props.candidateApplications.candidate_applications.length === 0
    ) {
      // Load my applications
      this.props.LoadCandidateApplications(
        this.props.auth.user.user_id,
        this.setLoading
      );

      this.setState({
        loading_location: true,
        loading_error: false,
      });
    }
  };

  render() {
    if (this.props.auth.user.user_id === "") {
      return (
        <div className="bg-red-100 mt-6 mx-6 p-6 h-full flex flex-col items-center justify-center rounded">
          <div className="text-4xl mx-4 my-4 animate-pulse text-red-700 font-extrabold">
            Not authenticated
          </div>
        </div>
      );
    }

    if (this.state.loading_error === true) {
      return (
        <div className="bg-red-100 mt-6 mx-6 p-6 h-full flex flex-col items-center justify-center rounded">
          <div className="text-4xl mx-4 my-4 animate-pulse text-red-700 font-extrabold">
            Failed to load locations
          </div>
        </div>
      );
    }

    if (
      this.state.loading === true ||
      this.props.candidateApplications.candidate_applications === null ||
      this.state.loading_location === true
    ) {
      return (
        <div className="bg-white mt-6 mx-6 p-6 h-full flex flex-col items-start justify-center rounded">
          <div className="text-xl mx-4 my-4 animate-pulse">
            Loading, Please wait...
          </div>
          <Loading />
        </div>
      );
    }
    if (this.props.candidateApplications.selected_application === null) {
      return (
        <ApplicationsList
          list={this.props.candidateApplications.candidate_applications}
          SelectUserApplication={this.props.SelectUserApplication}
        />
      );
    }
    return (
      <Contract
        auth={this.props.auth}
        candidateApplications={this.props.candidateApplications}
        candidatePlacementContract={this.props.candidatePlacementContract}
        LoadCandidateApplications={this.props.LoadCandidateApplications}
        SelectUserApplication={this.props.SelectUserApplication}
        FC_LoadCandidateContract={this.props.FC_LoadCandidateContract}
        initializeContract={this.props.initializeContract}
        UpdateContractLocations={this.props.UpdateContractLocations}
        locations={this.state.locations}
        FC_LoadSchoolsByDistrict={this.props.FC_LoadSchoolsByDistrict}
        FC_LoadSchoolsByIDs={this.props.FC_LoadSchoolsByIDs}
        FC_SubmitContract={this.props.FC_SubmitContract}
        SubmitApplication={this.props.SubmitApplication}
        applicationGoBack={this.props.applicationGoBack}
      />
    );
  }
}

const mapStateToProps = ({
  auth,
  candidateApplications,
  positions,
  periods,
  candidatePlacementContract,
}: StoreState): {
  auth: Auth;
  candidateApplications: CandidateApplicationsStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  candidatePlacementContract: CandidatePlacementContractStoreInterface;
} => {
  return {
    auth: auth,
    candidateApplications: candidateApplications,
    positions,
    periods: periods,
    candidatePlacementContract: candidatePlacementContract,
  };
};

export const PlacementContract = connect(mapStateToProps, {
  selectPosition,
  FC_Access,
  loadApplicationPeriods,
  setSelectedPeriod,
  LoadCandidateApplications,
  SelectUserApplication,
  FC_LoadCandidateContract,
  initializeContract,
  UpdateContractLocations,
  FC_LoadSchoolsByDistrict,
  FC_LoadSchoolsByIDs,
  FC_SubmitContract,
})(_PlacementContract);
