import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { GetApplicationsAPI } from "../interfaces/application";
import { API } from "../utils/api";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface CandidateApplicationsActions {
  type: ActionTypes.LOAD_CANDIDATE_APPLICATIONS;
  payload: GetApplicationsAPI[];
}

export interface AddCandidateApplicationActions {
  type: ActionTypes.ADD_CANDIDATE_APPLICATION;
  payload: GetApplicationsAPI;
}

export interface SelectCandidateApplicationActions {
  type: ActionTypes.SELECT_CANDIDATE_APPLICATION;
  payload: GetApplicationsAPI | null;
}

export interface RemoveCandidateApplicationActions {
  type: ActionTypes.REMOVE_CANDIDATE_APPLICATION;
  payload: string;
}

export interface LoadingCandidateApplicationActions {
  type: ActionTypes.LOADING_CANDIDATE_APPLICATION;
  payload: boolean;
}

export interface GetCandidateApplicationActions {
  type: ActionTypes.SET_CANDIDATE_APPLICATIONS;
  payload: GetApplicationsAPI[];
}

export interface CandidateApplicationsStoreInterface {
  candidate_applications: GetApplicationsAPI[] | null;
  selected_application: GetApplicationsAPI | null;
  loading: boolean;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const LoadCandidateApplications = (
  user_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    dispatch<LoadingCandidateApplicationActions>({
      type: ActionTypes.LOADING_CANDIDATE_APPLICATION,
      payload: true,
    });
    try {
      const res = await axios.get<GetApplicationsAPI[]>(
        `${API.APPLICATION}/apply/user/${user_id}`
      );
      dispatch<CandidateApplicationsActions>({
        type: ActionTypes.LOAD_CANDIDATE_APPLICATIONS,
        payload: res.data,
      });
      // console.log("Candidate applications: ", res.data);
    } catch (error: any) {
      dispatch<LoadingCandidateApplicationActions>({
        type: ActionTypes.LOADING_CANDIDATE_APPLICATION,
        payload: false,
      });
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const AddUserApplication = (application: GetApplicationsAPI) => {
  return async (dispatch: Dispatch) => {
    dispatch<AddCandidateApplicationActions>({
      type: ActionTypes.ADD_CANDIDATE_APPLICATION,
      payload: application,
    });
  };
};

export const SelectUserApplication = (
  application: GetApplicationsAPI | null
) => {
  return async (dispatch: Dispatch) => {
    dispatch<SelectCandidateApplicationActions>({
      type: ActionTypes.SELECT_CANDIDATE_APPLICATION,
      payload: application,
    });
  };
};

export const RemoveUserApplication = (application_id: string) => {
  return async (dispatch: Dispatch) => {
    dispatch<RemoveCandidateApplicationActions>({
      type: ActionTypes.REMOVE_CANDIDATE_APPLICATION,
      payload: application_id,
    });
  };
};

export const setLoadingApplications = (status: boolean) => {
  return async (dispatch: Dispatch) => {
    dispatch<LoadingCandidateApplicationActions>({
      type: ActionTypes.LOADING_CANDIDATE_APPLICATION,
      payload: status,
    });
  };
};

export const addCandidateApplications = (
  applications: GetApplicationsAPI[]
) => {
  return async (dispatch: Dispatch) => {
    dispatch<GetCandidateApplicationActions>({
      type: ActionTypes.SET_CANDIDATE_APPLICATIONS,
      payload: applications,
    });
  };
};
