import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  ApplicationPeriodInterface,
  UserInfoInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

/**
 * * ****************************** INTERFACES *****************************
 */
export interface GetUserInfoInterface {
  user_id: string;
  user_info: UserInfoInterface;
}

export interface GetUserInfoAction {
  type: ActionTypes.GET_USER_INFO;
  payload: GetUserInfoInterface[];
}

export interface GetApplicationPerPeriodAction {
  type: ActionTypes.GET_APPLICATION_PER_PERIOD;
  payload: ApplicationPeriodInterface[];
}

export type AdminDashboardActionType =
  | GetUserInfoAction
  | GetApplicationPerPeriodAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const GetUserInfo = (callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<GetUserInfoInterface[]>(
        `${API.AUTH}/usersinfo`
      );
      dispatch<GetUserInfoAction>({
        type: ActionTypes.GET_USER_INFO,
        payload: res.data,
      });
      console.log("====================================");
      console.log({ GetUserInfo: res.data });
      console.log("====================================");
      callback(false, "");
    } catch (error) {
      console.error({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

export const GetApplicationPerPeriod = (
  period_id: string,
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<ApplicationPeriodInterface[]>(
        `${API.APPLICATION}/apply/byperiod/${period_id}`
      );
      console.log({ res });
      dispatch<GetApplicationPerPeriodAction>({
        type: ActionTypes.GET_APPLICATION_PER_PERIOD,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error({ GetApplicationPerPeriod_error: error });
      callback(false, errorToText(error));
    }
  };
};

export type DashboardCountType = {
  totalApplicationOnPosition: {
    _id: string;
    total: number;
  }[];
  totalShortListedApplication: {
    _id: string;
    total: number;
  }[];
  totalRejectedApplicationApplication: {
    _id: string;
    total: number;
  }[];
  totalApplicants: number;
  totalApplications: number;
};
export const FC_GetApplicationCountPerPeriod = async (
  period_id: string,
  callback: (
    status: boolean,
    msg: string,
    data: DashboardCountType | null
  ) => void
) => {
  callback(true, "", null);
  try {
    setAxiosToken();
    const res = await axios.get<DashboardCountType>(
      // `${API.APPLICATION}/counter/gposition/62a5ea76b038313901018796`
      `${API.APPLICATION}/counter/gposition/${period_id}`
    );
    console.log(
      "=================FC_GetApplicationCountPerPeriod==================="
    );
    console.log({ res });
    console.log(
      "=================FC_GetApplicationCountPerPeriod==================="
    );
    callback(false, "", res.data);
  } catch (error) {
    console.error({ GetApplicationPerPeriod_error: error });
    callback(false, errorToText(error), null);
  }
};

