import { Action, ActionTypes } from "../actions";
import { UserAccessInterface } from "../actions/users";

// default state
const initialState: UserAccessInterface = {
  users: null,
  next: null,
  previous: null,
  total_users: 0,
};

export const userReducer = (
  state: UserAccessInterface = initialState,
  action: Action
) => {
  let index: number, index_2: number;
  switch (action.type) {
    case ActionTypes.FETCH_USERS_ACCESS:
      if (state.users && action.payload.users) {
        let defaultState = { ...state };
        defaultState.next = action.payload.next;
        defaultState.previous = action.payload.previous;
        defaultState.total_users = action.payload.total_users;
        const users = defaultState.users!.concat(action.payload.users);
        defaultState.users = users;
        return defaultState;
      } else {
        return action.payload;
      }
    case ActionTypes.BLOCK_USER_TO_SYSTEM:
    case ActionTypes.UNBLOCK_USER_TO_SYSTEM:
      if (state.users) {
        index = state.users.findIndex(
          (user) => user.page === action.payload.page
        ); //finding index of the item
        if (index >= 0) {
          index_2 = state.users[index].data.findIndex(
            (user) => user.user_id === action.payload.id
          );
          state.users[index].data[index_2].is_active =
            !state.users[index].data[index_2].is_active;
        }
      }
      return state;
    case ActionTypes.ASSIGN_ROLE_TO_USER:
      if (state.users) {
        let data = [...state.users];
        index = state.users.findIndex(
          (user) => user.page === action.payload.page
        );
        if (index >= 0) {
          index_2 = state.users[index].data.findIndex(
            (user) => user.user_id === action.payload.data.user_id
          );
          if (index_2 >= 0) {
            data[index].data[index_2] = {
              ...data[index].data[index_2],
              default_access: action.payload.data.data,
            };
            data[index].data[index_2] = {
              ...data[index].data[index_2],
              custome_access: null,
            };
          }
        }
        return {
          ...state,
          users: data,
        };
      }
      return state;
    case ActionTypes.CUSTOMIZE_USER_ACCESS:
      if (state.users) {
        index = state.users.findIndex(
          (user) => user.page === action.payload.page
        ); //finding index of the item
        if (index >= 0) {
          index_2 = state.users[index].data.findIndex(
            (user) => user.user_id === action.payload.data.user_id
          ); //finding index of the item
          if (index_2 >= 0) {
            state.users[index].data[index_2].custome_access =
              action.payload.data;
          }
        }
      }
      return state;
    case ActionTypes.RESET_USER_ACCESS:
      if (state.users) {
        index = state.users.findIndex(
          (user) => user.page === action.payload.page
        );
        if (index >= 0) {
          index_2 = state.users[index].data.findIndex(
            (user) => user.user_id === action.payload.user_id
          );
          if (index_2 >= 0) {
            state.users[index].data[index_2].custome_access = null;
          }
        }
      }
      return state;
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
