import { Action, ActionTypes } from "../actions";
import {
  AcademicSessionInterface,
  PlansTypeInterface,
  SessionPlanInterface,
} from "../interfaces";

export interface SessionPlanReducerInterface {
  planTypes: PlansTypeInterface[] | null;
  deletedPlanTypes: PlansTypeInterface[] | null;
  selectedPlanType: PlansTypeInterface | "all";
  allSessionPlans: SessionPlanInterface[] | null;
  activeSessionPlan: SessionPlanInterface | "none" | null;
  academicSessions: AcademicSessionInterface[] | null;
  activeAcademicSession: AcademicSessionInterface | "none" | null;
}

// default state
const initialState: SessionPlanReducerInterface = {
  planTypes: null,
  deletedPlanTypes: null,
  selectedPlanType: "all",
  allSessionPlans: null,
  activeSessionPlan: null,
  academicSessions: null,
  activeAcademicSession: null,
};

export const sessionPlanReducer = (
  state: SessionPlanReducerInterface = initialState,
  action: Action
) => {
  // let index;
  switch (action.type) {
    case ActionTypes.CREATE_PLAN_TYPE:
      if (state.planTypes) {
        return {
          ...state,
          planTypes: [...state.planTypes, action.payload],
        };
      }
      return {
        ...state,
        planTypes: [action.payload],
      };
    case ActionTypes.GET_PLANS_TYPES:
      return {
        ...state,
        planTypes: action.payload,
      };
    case ActionTypes.GET_DELETED_PLANS_TYPES:
      return {
        ...state,
        deletedPlanTypes: action.payload,
      };
    case ActionTypes.UPDATE_PLAN_TYPE:
      if (state.planTypes) {
        let _planTypes: PlansTypeInterface[] = [...state.planTypes];
        let index: number = _planTypes.findIndex(
          (planType) => planType.type_key === action.payload.type_key
        ); //finding index of the item
        if (index >= 0) {
          _planTypes[index] = action.payload;
        }
        return { ...state, planTypes: _planTypes };
      }
      return state;
    case ActionTypes.DELETE_PLAN_TYPE:
      if (state.planTypes) {
        let _planTypes: PlansTypeInterface[] = state.planTypes.filter(
          (planType) => planType.type_key !== action.payload.type_key
        );
        if (state.deletedPlanTypes) {
          return {
            ...state,
            planTypes: _planTypes,
            deletedPlanTypes: [...state.deletedPlanTypes, action.payload],
          };
        }
        return {
          ...state,
          planTypes: _planTypes,
          deletedPlanTypes: [action.payload],
        };
      }
      return state;
    case ActionTypes.SELECT_PLAN_TYPE:
      return {
        ...state,
        selectedPlanType: action.payload,
      };
    case ActionTypes.GET_ACTIVE_SESSION_PLANS:
      return {
        ...state,
        activeSessionPlan: action.payload,
      };
    case ActionTypes.GET_ALL_SESSION_PLANS:
      return {
        ...state,
        allSessionPlans: action.payload,
      };
    case ActionTypes.CREATE_SESSION_PLAN:
      if (state.allSessionPlans) {
        return {
          ...state,
          allSessionPlans: [action.payload, ...state.allSessionPlans],
        };
      }
      return {
        ...state,
        allSessionPlans: [action.payload],
      };
    case ActionTypes.UPDATE_SESSION_PLAN:
      if (state.allSessionPlans) {
        let _allSessionPlans: SessionPlanInterface[] = [
          ...state.allSessionPlans,
        ];
        let index: number = _allSessionPlans.findIndex(
          (plan) => plan._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _allSessionPlans[index] = action.payload;
        }
        return {
          ...state,
          allSessionPlans: _allSessionPlans,
        };
      }
      return state;
    case ActionTypes.SESSION_ACTIVATION:
      if (state.allSessionPlans) {
        let _allSessionPlans: SessionPlanInterface[] = [
          ...state.allSessionPlans,
        ];
        let index: number = _allSessionPlans.findIndex(
          (plan) => plan._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _allSessionPlans[index] = action.payload;
        }
        let oldIndex: number = _allSessionPlans.findIndex(
          (plan) => plan._id !== action.payload._id && plan.is_active
        );
        if (oldIndex && action.payload.is_active) {
          _allSessionPlans[oldIndex].is_active = false;
        }
        return {
          ...state,
          allSessionPlans: _allSessionPlans,
        };
      }
      return state;
    case ActionTypes.GET_ALL_ACADEMIC_SESSION:
      return {
        ...state,
        academicSessions: action.payload,
      };
    case ActionTypes.CREATE_ACADEMIC_SESSION:
      if (state.academicSessions) {
        return {
          ...state,
          academicSessions: [action.payload, ...state.academicSessions],
        };
      }
      return {
        ...state,
        academicSessions: [action.payload],
      };
    case ActionTypes.UPDATE_ACADEMIC_SESSION:
      if (state.academicSessions) {
        let _academicSessions: AcademicSessionInterface[] = [
          ...state.academicSessions,
        ];
        let index: number = _academicSessions.findIndex(
          (plan) => plan._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _academicSessions[index] = action.payload;
        }
        return {
          ...state,
          academicSessions: _academicSessions,
        };
      }
      return state;
    case ActionTypes.ACADEMIC_SESSION_ACTIVATION:
      if (state.academicSessions) {
        let _academicSessions: AcademicSessionInterface[] = [
          ...state.academicSessions,
        ];
        let index: number = _academicSessions.findIndex(
          (plan) => plan._id === action.payload._id
        ); //finding index of the item
        if (index >= 0) {
          _academicSessions[index] = action.payload;
        }
        let oldIndex: number = _academicSessions.findIndex(
          (plan) => plan._id !== action.payload._id && plan.is_active
        );
        if (oldIndex && action.payload.is_active) {
          _academicSessions[oldIndex].is_active = false;
        }
        return {
          ...state,
          academicSessions: _academicSessions,
        };
      }
      return state;
    case ActionTypes.GET_ACTIVE_ACADEMIC_SESSION:
      return { ...state, activeAcademicSession: action.payload };
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
