import React, { useEffect, useState } from "react";
import { BiFile } from "react-icons/bi";
import { RiFileUnknowLine } from "react-icons/ri";
import { Link } from "react-router-dom";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  candidateApplyStoreInterface,
} from "../../../../actions";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { education_category, PositionAPICourse } from "../../../../interfaces";
import { GetApplicationsAPI } from "../../../../interfaces/application";
import { RoomType } from "../../../../interfaces/location";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { RTB_ID } from "../../../../utils/api";
import ChooseExamCenter, {
  LocationTableInterface,
} from "../../../Application/ExamCenter/ChooseExamCenter";
import { sentExamInterface } from "../../../Application/ExamCenter/ExamCenter";
import { EditProfileDetails } from "../../../ProfileDetails/EditProfileDetails";
import { ApplicationStepsTypes } from "../CandidateApplicationSteps/CandidateApplicationSteps";

interface ExamCenterProps {
  candidateApply: candidateApplyStoreInterface;
  auth: Auth;
  PushPopupAlert: (alert: PopupAlertData) => void;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  addCandidateExamLocation: (
    location: LocationTableInterface,
    exam: sentExamInterface
  ) => void;
  // LoadCandidateApplications: (
  //   user_id: string,
  //   callback: (loading: boolean) => void
  // ) => void;
  sendApplication: (loadingState: (status: boolean) => void) => void;
  candidateApplications: CandidateApplicationsStoreInterface;
  selectedEducationCategory: education_category;
  requestedApplications: GetApplicationsAPI[] | null;
}

const ExamCenter = (props: ExamCenterProps) => {
  const [addLocation, setAddLocation] = useState<PositionAPICourse | null>(
    null
  );
  const [addResidence, setAddResidence] = useState<boolean>(true);

  const [loading, setLoading] = useState(false);

  const selectedExam = (Exam: PositionAPICourse): sentExamInterface | null => {
    const test = props.candidateApply.exams?.find(
      (itm) => itm.exam.course_id === Exam.course_id
    );
    return test === undefined ? null : test.exam;
  };

  const selectedExamLocation = (
    Exam: PositionAPICourse
  ): {
    location: LocationTableInterface;
    exam: sentExamInterface;
  } | null => {
    const test = props.candidateApply.exams?.find(
      (itm) => itm.exam.course_id === Exam.course_id
    );
    return test === undefined ? null : test;
  };

  useEffect(() => {
    if (
      addResidence === true &&
      addLocation === null &&
      props.candidateApply.selectedPosition?.courses.length === 1
    ) {
      props.candidateApply.selectedPosition?.education_institute_id
        .education_institute_id === RTB_ID &&
        loading === false &&
        setAddResidence(true);
      props.auth.user.residence?.district_id !== "" &&
        loading === false &&
        setAddLocation(props.candidateApply.selectedPosition.courses[0]);
    }
  }, [addLocation, addResidence, loading, props]);

  return (
    <div className="bg-white mt-5 py-5">
      {/* My residence location */}
      {addResidence === true ? (
        <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
          <div className="text-2xl font-light">My residence location</div>
          <div className="font-light mb-10 text-gray-600">
            You are required to specify your residence location information in
            order to continue your application process
          </div>
          {addResidence === true && (
            <EditProfileDetails
              close={() => {
                if (
                  props.auth.user.residence === undefined ||
                  props.auth.user.residence?.district_id === ""
                ) {
                  props.PushPopupAlert({
                    title: "You are required to choose your residence!",
                    theme: PopupAlertTheme.warning,
                  });
                } else {
                  setAddResidence(false);
                }
              }}
              residenceOnly={true}
            />
          )}
          <div className="mb-3 mt-3">
            {/* <Link
              to="/profile"
              className="font-bold underline text-blue-500 hover:text-blue-800"
            >
              Go to my profile & update residence
            </Link> */}
          </div>
          <div className="flex flex-row items-center justify-between gap-3 w-full">
            <div
              onClick={() => {
                if (props.auth.user.residence?.district_id === "") {
                  props.PushPopupAlert({
                    title: "You are required to choose your residence!",
                    theme: PopupAlertTheme.warning,
                  });
                  props.setCandidateApplicationStep(
                    ApplicationStepsTypes.Step1
                  );
                } else {
                  setAddResidence(false);
                }
              }}
              className="btn bg-gray-100 hover:bg-gray-200 cursor-pointer"
            >
              Cancel
            </div>
            <div
              onClick={() => {
                if (props.auth.user.residence?.district_id === "") {
                  props.PushPopupAlert({
                    title: "You are required to choose your residence!",
                    theme: PopupAlertTheme.warning,
                  });
                  setAddResidence(true);
                } else {
                  setAddResidence(false);
                }
              }}
              className="btn bg-blue-100 hover:bg-blue-200 text-blue-800 cursor-pointer"
            >
              {props.auth.user.residence?.district_id === ""
                ? "Add my residence"
                : "Confirm my residence"}
            </div>
          </div>
        </div>
      ) : addLocation === null ? (
        <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
          <div className="text-2xl">Your residence location</div>
          {/* Exams list */}
          {
            <div className="mt-3 grid md:grid-cols-2 gap-5">
              {props.candidateApply.selectedPosition?.courses.map((item, i) => (
                <div key={i + 1} className="col-span-1 cursor-pinter">
                  <div
                    className={`flex flex-row gap-2 ${
                      selectedExam(item) !== null
                        ? "bg-white border border-green-500"
                        : "bg-gray-100"
                    } p-4 rounded-md`}
                  >
                    <div>
                      {props.candidateApply.exams === null ||
                      selectedExam(item) === null ? (
                        <RiFileUnknowLine className="text-6xl text-gray-300" />
                      ) : (
                        <BiFile className="text-7xl text-green-500" />
                      )}
                    </div>
                    <div>
                      <div className="text-lg font-bold">
                        <span className="font-normal">
                          {item.course_name} ({item.course_code})
                        </span>
                      </div>
                      <div className="text-sm text-light">
                        Province of residence:{" "}
                        <span className="text-blue-500">
                          {props.auth.user.residence?.province_name}
                        </span>
                      </div>
                      <div className="text-sm text-light">
                        District of residence:{" "}
                        <span className="text-blue-500">
                          {props.auth.user.residence?.district_name}
                        </span>
                      </div>
                      <div className="mt-1">
                        {props.candidateApply.exams === null ||
                        selectedExam(item) === null ? (
                          <div
                            onClick={() => {
                              props.candidateApply.selectedPosition
                                ?.education_institute_id
                                .education_institute_id === RTB_ID &&
                                loading === false &&
                                setAddResidence(true);
                              props.auth.user.residence?.district_id !== "" &&
                                loading === false &&
                                setAddLocation(item);
                            }}
                            title="Your location of residence"
                            className="bg-yellow-100 text-yellow-900 rounded px-3 py-2 cursor-pointer border border-yellow-300 font-semibold font-base w-max flex items-center justify-center text-sm"
                          >
                            {props.auth.user.residence?.district_id === ""
                              ? "Add your location"
                              : "Verify your location"}
                          </div>
                        ) : (
                          selectedExamLocation(item) !== null && (
                            <div>
                              {props.candidateApply.selectedPosition
                                ?.education_institute_id
                                .education_institute_id !== RTB_ID && (
                                <>
                                  <div className="flex flex-row items-center gap-3 text-sm mb-1">
                                    <span className="text-gray-500">
                                      Province:
                                    </span>
                                    <span className="text-gray-900 font-bold">
                                      {
                                        selectedExamLocation(item)?.location
                                          .province_name
                                      }
                                    </span>
                                  </div>
                                  <div className="flex flex-row items-center gap-3 text-sm mb-1">
                                    <span className="text-gray-500">
                                      District:
                                    </span>
                                    <span className="text-gray-900 font-bold">
                                      {
                                        selectedExamLocation(item)?.location
                                          .district_name
                                      }
                                    </span>
                                  </div>
                                  <div className="flex flex-row items-center gap-3 text-sm mb-1">
                                    <span className="text-gray-500">
                                      Center:
                                    </span>
                                    <span className="text-gray-900 font-bold">
                                      {
                                        selectedExamLocation(item)?.location
                                          .center_name
                                      }
                                    </span>
                                  </div>
                                </>
                              )}
                              <div
                                onClick={() => {
                                  props.candidateApply.selectedPosition
                                    ?.education_institute_id
                                    .education_institute_id === RTB_ID &&
                                    loading === false &&
                                    setAddResidence(true);
                                  props.auth.user.residence?.district_id !==
                                    "" &&
                                    loading === false &&
                                    setAddLocation(item);
                                }}
                                className="bg-blue-100 text-blue-900 rounded px-3 py-1 cursor-pointer border border-blue-300 font-semibold font-base w-max flex items-center justify-center mt-2 text-base animate__animated animate__zoomIn"
                              >
                                {props.auth.user.residence?.district_id === ""
                                  ? "Add your location"
                                  : "Update your location"}
                              </div>
                            </div>
                          )
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          }
          {/* Footer */}
          <div className="flex flex-row justify-between items-center border-t border-gray-300 my-5 pt-3 pb-5">
            <button
              onClick={() =>
                loading === false &&
                props.setCandidateApplicationStep(ApplicationStepsTypes.Step1)
              }
              className="btn bg-white text-gray-600 border border-gray-300"
            >
              Back
            </button>
            <button
              onClick={() => {
                if (
                  props.candidateApply.selectedPosition?.courses.length ===
                  props.candidateApply.exams?.length
                ) {
                  props.sendApplication((status: boolean) => {
                    setLoading(status);
                    if (status === false) {
                      props.setCandidateApplicationStep(
                        ApplicationStepsTypes.Step3
                      );
                    }
                  });
                } else {
                  if (
                    props.candidateApply.selectedPosition?.courses.length === 1
                  ) {
                    props.candidateApply.selectedPosition
                      ?.education_institute_id.education_institute_id ===
                      RTB_ID &&
                      loading === false &&
                      setAddResidence(true);
                    props.auth.user.residence?.district_id !== "" &&
                      loading === false &&
                      props.candidateApply.selectedPosition !== null &&
                      setAddLocation(
                        props.candidateApply.selectedPosition.courses[0]
                      );
                  }
                  props.PushPopupAlert({
                    title: "Please complete step two (2)",
                    description:
                      props.candidateApply.selectedPosition
                        ?.education_institute_id.education_institute_id ===
                      RTB_ID
                        ? "Please verify your residence location"
                        : "You need to fill examination centers for all exams",
                    theme: PopupAlertTheme.warning,
                  });
                }
              }}
              className={`btn ${
                props.candidateApply.selectedPosition?.courses.length ===
                  props.candidateApply.exams?.length && loading === false
                  ? "bg-green-500 text-white"
                  : "bg-gray-200 text-black cursor-not-allowed"
              }`}
            >
              {loading === true ? "Checking, please wait..." : "Continue"}
            </button>
          </div>
        </div>
      ) : (
        <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24">
          <div className="border-0 border-gray-300 rounded bg-white animate__animated animate__fadeIn animate__faster">
            {props.candidateApply.selectedPosition !== null && (
              <ChooseExamCenter
                addLocation={(
                  course_id: string,
                  course_name: string,
                  course_code: string,
                  location: LocationTableInterface,
                  exam_id: string
                ) => {
                  props.addCandidateExamLocation(location, {
                    course_id: course_id,
                    proctoring_type: RoomType.Physical, //Room type
                    center_id: location.center_id,
                    district_id: location.district_id,
                  });
                }}
                positionCourses={addLocation}
                closeModal={() => setAddLocation(null)}
                changeProctoring={false}
                displayClose={true}
                selectedEducationCategory={
                  props.candidateApply.selectedPosition.education_institute_id
                }
              />
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default ExamCenter;
