import { Action, ActionTypes } from "../actions";
import { UserAccessInterface } from "../actions/users";

// default state
const initialState: UserAccessInterface = {
  users: null,
  next: null,
  previous: null,
  total_users: 0,
};

export const userDocumentReducer = (
  state: UserAccessInterface = initialState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.GET_CANDIDATE_DOCUMENTS:
      if (state.users && action.payload.users) {
        let defaultState = { ...state };
        defaultState.next = action.payload.next;
        defaultState.previous = action.payload.previous;
        defaultState.total_users = action.payload.total_users;
        const users = defaultState.users!.concat(action.payload.users);
        defaultState.users = users;
        return defaultState;
      } else {
        return action.payload;
      }
    case ActionTypes.ALL_VERIFY_DOCUMENT:
      if (state.users) {
        let index: number = state.users.findIndex(
          (user) => user.page === action.payload.page
        ); //finding index of the item
        if (index >= 0) {
          let defaultState = [...state.users];
          let userIndex: number = defaultState[index].data.findIndex(
            (item) => item._id && item._id === action.payload.data.user_id
          );
          if (userIndex >= 0) {
            let docIndex: number = defaultState[index].data[
              userIndex
            ].details.findIndex(
              (item) =>
                item.doc_id && item.doc_id === action.payload.data.doc_id
            );
            defaultState[index].data[userIndex].in_education =
              action.payload.data.in_education;
            defaultState[index].data[userIndex].disability =
              action.payload.data.disability;
            if (docIndex >= 0) {
              defaultState[index].data[userIndex].details[
                docIndex
              ].validation_status = action.payload.data.validation_status;
              defaultState[index].data[userIndex].details[
                docIndex
              ].validated_by = {
                user_id: action.payload.data.validated_by.user_id,
                commet: action.payload.data.validated_by.comment,
                validation_status: action.payload.data.validation_status,
                date: action.payload.data.validated_by.date
                  ? action.payload.data.validated_by.date
                  : "",
              };
              defaultState[index].data[userIndex].details[docIndex].approved =
                action.payload.data.validation_status;
            }
          }

          return { ...state, users: defaultState };
          // const users = defaultState.users!.concat(action.payload.users);
          // defaultState.users = users;
          // return defaultState;
        }
      }
      return state;
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
