import { userDocumentReducer } from "./document_validation";
import { candidateApplyReducer } from "./candidate_apply";

// define the entire state into the entire sire
export interface StoreState {
  proctoring: Proctoring;
  todos: Todo[];
  auth: Auth;
  system_settings: SystemSettingsType;
  claiming: ClaimingReducerType;
  access: AccessType;
  document: DocumentReducerInterface;
  userDocuments: UserAccessInterface;
  locations: LocationReducerInterface;
  applicationToAssess: ApplicationAssessmentInterface[] | null;
  users: UserAccessInterface;
  schools: SchoolReducerType;
  marksPublication: marksPublicationInterface[] | null;
  socket: Socket;
  doExam: examDataInterface;
  application: applicationStoreInterface;

  system: System;
  applications_list: applicationsListStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  questionTypes: questionTypesStoreInterface;
  course: courseStoreInterface;
  dashboard: DashboardReducerType;
  questionBank: QuestionBankStoreInterface;
  prepareExam: PrepareExamsStoreInterface;
  locationsData: LocationsDataStoreInterface;
  candidateExam: candidateExamStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  adminExamReport: ExamReportReducerType;
  waitingList: WaitingListInterface[] | null;
  sessionPlan: SessionPlanReducerInterface;
  candidatePlacementContract: CandidatePlacementContractStoreInterface;
  candidateApply: candidateApplyStoreInterface;
}

export const reducers = combineReducers<StoreState>({
  todos: todosReducer,
  auth: authReducer,
  system_settings: systemSettingsReducer,
  claiming: claimingReducer,
  access: accessReducer,
  document: documentReducer,
  locations: locationsReducer,
  schools: schoolReducer,
  applicationToAssess: ApplicationAssessmentReducer,
  users: userReducer,
  userDocuments: userDocumentReducer,
  marksPublication: marksPublicationReducer,
  socket: socketReducer,
  doExam: doExamReducer,
  application: applicationReducer,
  proctoring: proctoringReducer,
  system: systemReducer,
  applications_list: applicationsListReducer,
  positions: positionsReducer,
  periods: applicationPeriodsReducer,
  schoolLevels: schoolLevelsReducer,
  questionTypes: questionTypesReducer,
  course: courseReducer,
  dashboard: AdminDashboardReducer,
  questionBank: questionBankReducer,
  prepareExam: prepareExamReducer,
  locationsData: locationDataReducer,
  candidateExam: candidateExamReducer,
  candidateApplications: CandidateApplicationsReducer,
  adminExamReport: examReportReducer,
  waitingList: waitingListReducer,
  sessionPlan: sessionPlanReducer,
  candidatePlacementContract: CandidatePlacementContractReducer,
  candidateApply: candidateApplyReducer,
});
