import { Action, ActionTypes } from "../actions";
import { PlacementListInterface } from "../containers/PlacementLimit/PlacementLimit";
import { placedCandidateInterface } from "../interfaces/marks_publication";
import {
  MySchoolInterface,
  PostSchoolInterface,
  SchoolInfoInterface,
  SchoolsInterface,
} from "../shared/interfaces";

export interface SchoolReducerType {
  schools: SchoolsInterface[];
  mySchool: MySchoolInterface | null;
  schoolsInfo: SchoolInfoInterface[];
  schoolPosts: PostSchoolInterface[];
  institutePlacementLimits: PlacementListInterface[] | null;
  districtPlacementLimits: PlacementListInterface[] | null;
  schoolPlacements: placedCandidateInterface[] | null;
  districtPlacements: placedCandidateInterface[] | null;
  sessionPlacements: placedCandidateInterface[] | null;
  districtPosts: PostSchoolInterface[];
}

// default state
const initialState: SchoolReducerType = {
  schools: [],
  mySchool: null,
  schoolsInfo: [],
  schoolPosts: [],
  institutePlacementLimits: null,
  districtPlacementLimits: null,
  schoolPlacements: null,
  districtPosts: [],
  districtPlacements: null,
  sessionPlacements: null,
};

export const schoolReducer = (
  state: SchoolReducerType = initialState,
  action: Action
) => {
  let index;
  switch (action.type) {
    case ActionTypes.GET_SCHOOLS:
      return {
        ...state,
        schools: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL:
      return {
        ...state,
        schools: [...state.schools, action.payload],
      };
    case ActionTypes.UPDATE_SCHOOL:
      index = state.schools.findIndex(
        (school) => school._id === action.payload._id
      ); //finding index of the item
      if (index >= 0) {
        state.schools[index].school_name = action.payload.school_name;
        state.schools[index].education_category_id =
          action.payload.education_category_id;
        state.schools[index].district_id = action.payload.district_id;
        state.schools[index].sector_id = action.payload.sector_id;
        state.schools[index].sector_code = action.payload.sector_code;
        state.schools[index].school_code = action.payload.school_code;
      }
      return state;
    case ActionTypes.DELETE_SCHOOL:
      let data = state.schools.filter(
        (element) => element._id !== action.payload
      );
      return {
        ...state,
        schools: data,
      };
    case ActionTypes.GET_SCHOOLS_INFO:
      return {
        ...state,
        schoolsInfo: action.payload,
      };
    case ActionTypes.GET_SCHOOLS_BY_ID:
      return {
        ...state,
        mySchool: action.payload,
      };
    case ActionTypes.GET_SCHOOL_PLACEMENT:
      return {
        ...state,
        schoolPlacements: action.payload,
      };
    case ActionTypes.FETCH_SCHOOL_POSTS:
      return {
        ...state,
        schoolPosts: action.payload,
      };
    case ActionTypes.CREATE_SCHOOL_POST:
      return {
        ...state,
        schoolPosts: [...state.schoolPosts, action.payload],
      };
    case ActionTypes.UPDATE_SCHOOL_POST:
      let newData: PostSchoolInterface[] = [...state.schoolPosts];
      index = state.schoolPosts.findIndex(
        (post) => post._id === action.payload.data._id
      ); //finding index of the item
      if (index >= 0) {
        if (action.payload.TYPE === "DISTRICT") {
          newData[index].confirmed_posts = action.payload.data.confirmed_posts;
          newData[index].approval_date = action.payload.data.approval_date;
          newData[index].confirmed_by = action.payload.data.confirmed_by;
        } else if (action.payload.TYPE === "RECEIVED") {
          newData[index].received_posts = action.payload.data.received_posts;
        } else {
          newData[index].description = action.payload.data.description;
          newData[index].female_number = action.payload.data.female_number;
          newData[index].male_number = action.payload.data.male_number;
          newData[index].occupied_posts = action.payload.data.occupied_posts;
          newData[index].request_date = action.payload.data.request_date;
          newData[index].requested_by = action.payload.data.requested_by;
          newData[index]._id = action.payload.data._id;
        }
        // state.schoolPosts[index].post_at_school_status =
        //   action.payload.post_at_school_status;
        // state.schoolPosts[index].confirmed_posts =
        //   action.payload.data.confirmed_posts;
      }
      return { ...state, schoolPosts: newData };
    case ActionTypes.FETCH_DISTRICT_POSTS:
      return {
        ...state,
        districtPosts: action.payload,
      };
    case ActionTypes.GET_DISTRICT_PLACEMENT:
      return {
        ...state,
        districtPlacements: action.payload,
      };
    case ActionTypes.GET_SESSION_PLACEMENT:
      return {
        ...state,
        sessionPlacements: action.payload,
      };
    // case ActionTypes.SELECT_DISTRICT_PLACEMENT:
    //   return {
    //     ...state,
    //     selectedDistrict: action.payload,
    //   };
    case ActionTypes.GET_INSTITUTE_PLACEMENT_LIMIT:
      return {
        ...state,
        institutePlacementLimits: action.payload,
      };
    case ActionTypes.GET_DISTRICT_PLACEMENT_LIMIT:
      return {
        ...state,
        districtPlacementLimits: action.payload,
      };
    case ActionTypes.UPDATE_INSTITUTE_PLACEMENT_LIMIT:
      if (state.institutePlacementLimits) {
        let dataCopy = [...state.institutePlacementLimits];
        index = dataCopy.findIndex(
          (item) => item._id === action.payload.limit_id
        );
        if (index >= 0)
          dataCopy[index].confirmed_post = action.payload.confirmed_post;
        return {
          ...state,
          institutePlacementLimits: dataCopy,
        };
      } else return state;
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
