import React, { FC, ReactElement, useState } from "react";
import { ProvinceLocationItem } from "../../../../actions";
import Modal, { Themes, ModalSize } from "../../../../components/Modal/Modal";
import search from "../../../../utils/search";

type ProvinceSelectorProps = {
  close: () => void;
  data: ProvinceLocationItem[];
  selectItem: (id: string) => void;
  selected: number[];
  removeItem: (id: string) => void;
  maximum: number;
};

const ProvinceSelector: FC<ProvinceSelectorProps> = ({
  close,
  data,
  selectItem,
  removeItem,
  maximum,
  selected,
}): ReactElement => {
  const [searchText, setSearchText] = useState("");

  let results = search(data, searchText, {
    province_name: true,
  }) as ProvinceLocationItem[];

  let itemsFull = maximum <= selected.length ? true : false;
  return (
    <Modal
      backDrop={true}
      backDropClose={true}
      close={() => close()}
      displayClose={true}
      theme={Themes.default}
      title={"Select a province"}
      padding={{
        body: false,
        title: true,
      }}
      widthSizeClass={ModalSize.large}
    >
      <div className="p-2 bg-gray-200">
        <input
          type="search"
          className="p-2 w-full rounded border bg-white"
          placeholder="Search Province"
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        <div className="flex">
          <p className="px-2 flex-1 text-gray-800">
            {selected.length} selected out of {maximum}
          </p>
          <p className="text-right">
            Results: <b>{data.length}</b>
          </p>
        </div>
      </div>
      <div className="bg-300 p-3 overflow-auto" style={{ height: "70vh" }}>
        {results.length >= 1 ? (
          results.map((itm) => {
            let itmSelected = selected.includes(+itm.province_code);
            return (
              <div
                key={itm.province_code}
                className={`p-1 items-center group border my-1 rounded ${
                  itmSelected === true
                    ? " font-bold bg-blue-100 text-blue-700 border-blue-200"
                    : "hover:bg-blue-50"
                }  px-2 flex`}
              >
                <div className="flex-1 ">{itm.province_name}</div>
                {itmSelected === true ? (
                  <button
                    className="p-1 bg-red-300 px-2 rounded text-sm bg-opacity-40 hover:bg-red-700 text-red-700 hover:text-white group-hover:bg-opacity-100"
                    onClick={() => removeItem(itm.province_code)}
                  >
                    Remove
                  </button>
                ) : itemsFull === true ? null : (
                  <button
                    onClick={() => selectItem(itm.province_code)}
                    className="p-1 bg-blue-300 px-2 rounded text-sm bg-opacity-40 hover:bg-blue-700 text-blue-700 hover:text-white group-hover:bg-opacity-100"
                  >
                    Select
                  </button>
                )}
              </div>
            );
          })
        ) : (
          <div className="py-20 text-center text-lg">No data found</div>
        )}
      </div>
    </Modal>
  );
};
export default ProvinceSelector;
