import {
  Action,
  ActionTypes,
  applicationPeriodsStoreInterface,
} from "../actions";

// default state
const defaultState: applicationPeriodsStoreInterface = {
  applicationPeriods: null,
  selected_period: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const applicationPeriodsReducer = (
  state: applicationPeriodsStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.APPLICATION_PERIODS:
      if (action.payload !== undefined) {
        state.applicationPeriods = action.payload;
        // state.selected_period =
        //   state.selected_period === null
        //     ? action.payload.filter((itm) => itm.is_active === true).length ===
        //       0
        //       ? null
        //       : action.payload.filter((itm) => itm.is_active === true)[
        //           action.payload.filter((itm) => itm.is_active === true)
        //             .length - 1
        //         ]
        //     : action.payload.find(
        //         (it) => it._id === state.selected_period?._id
        //       )!;
      }
      return {
        ...state,
      };
    case ActionTypes.SELECTED_PERIOD:
      state.selected_period = action.payload;
      return {
        ...state,
      };
    default:
      return state;
  }
};
