import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  positionsStoreInterface,
  applicationPeriodsStoreInterface,
  applicationStoreInterface,
  System,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  loadApplicationPeriods,
  loadAllPositions,
  setSelectedPeriod,
  cleanSelectedSchoolLevel,
  candidateApplyStoreInterface,
  setCandidateApplicationStep,
  FC_GetProfileDetails,
  PushPopupAlert,
  setApplicationStep,
  addCandidateExamLocation,
  CandidateApplicationsStoreInterface,
  SelectUserApplication,
  LoadCandidateApplications,
  FC_LoginModal,
  FT_LoginModal,
  RemoveUserApplication,
  setApplyCompletionStatus,
  addCandidateApplications,
} from "../../../actions";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Loading from "../../../components/Loading/Loading";

import {
  education_category,
  PositionAPI,
  school_level,
} from "../../../interfaces";
import {
  ApplicationPeriodGet,
  GetApplicationResponse,
  GetApplicationsAPI,
} from "../../../interfaces/application";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { API } from "../../../utils/api";
import { ApplicationStepsTypes } from "../../Application/ApplicationSteps/ApplicationSteps";
import { LocationTableInterface } from "../../Application/ExamCenter/ChooseExamCenter";
import {
  sentExamInterface,
  SubmitApplicationAPI,
} from "../../Application/ExamCenter/ExamCenter";
import Agreement from "./Agreement/Agreement";
import CandidateApplicationSteps from "./CandidateApplicationSteps/CandidateApplicationSteps";
import ExamCenter from "./ExamCenter/ExamCenter";
import CandidateApplyRequirements from "./Requirements/CandidateApplyRequirements";

interface CandidateApplyProps {
  auth: Auth;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  candidateApply: candidateApplyStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (
    education_category: education_category
  ) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  setSelectedSchoolLevel: (education_category: school_level) => void;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  cleanSelectedSchoolLevel: () => void;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  initializeApplicationProcess: (
    position: PositionAPI | null,
    user_id: string | null
  ) => void;
  FC_GetProfileDetails: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  addCandidateExamLocation: (
    location: LocationTableInterface,
    exam: sentExamInterface
  ) => void;
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  FC_LoginModal: FT_LoginModal;
  RemoveUserApplication: (id: string) => void;
  setApplyCompletionStatus: (status: boolean) => void;
  cleanCandidateApply: () => void;
  addCandidateApplications: (applications: GetApplicationsAPI[]) => void;
}

interface CandidateApplyState {
  loading: boolean;
  candidateApplications: GetApplicationsAPI[] | null;
}

class _CandidateApply extends Component<
  CandidateApplyProps,
  CandidateApplyState
> {
  constructor(props: CandidateApplyProps) {
    super(props);

    this.state = {
      loading: false,
      candidateApplications:
        this.props.candidateApplications.candidate_applications,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  sendApplication = async (setLoadingStatus: (status: boolean) => void) => {
    // Validation
    if (
      this.props.auth.isAuthenticated === true &&
      this.props.auth.user.user_id !== ""
    ) {
      if (
        this.props.candidateApply.selectedPosition !== null &&
        this.props.candidateApply.exams !== null
      ) {
        let currentYear: Date = new Date();
        let exams: sentExamInterface[] = this.props.candidateApply.exams.map(
          (itm) => itm.exam
        );
        const applicationDetails: SubmitApplicationAPI = {
          user_id: this.props.auth.user.user_id,
          fname: this.props.auth.user.fname,
          lname: this.props.auth.user.lname,
          email: this.props.auth.user.email,
          phone_number: this.props.auth.user.phone_number,
          position: this.props.candidateApply.selectedPosition.position_id,
          application_period: `${currentYear.getFullYear()}-${
            currentYear.getFullYear() + 1
          }`,
          exams: exams,
          district_of_residence_id: this.props.auth.user.residence.district_id,
        };
        setLoadingStatus(true);
        try {
          const res = await axios.post<GetApplicationResponse>(
            `${API.APPLICATION}/apply`,
            applicationDetails
          );
          // Set user applications
          this.props.addCandidateApplications(res.data.userApplications);
          this.setState({ candidateApplications: res.data.userApplications });
          console.log("Create Application res: ", res);

          console.log(
            "After add applications: ",
            this.props.candidateApplications.candidate_applications
          );
          setLoadingStatus(false);
        } catch (error: any) {
          setLoadingStatus(false);
          if (error.response) {
            if (error.response.data) {
              if (error.response.data.errors !== undefined) {
                if (error.response.data.errors.length > 0) {
                  // this.props.PushPopupAlert({
                  //   title: error.response.data.errors[0].message,
                  //   theme: PopupAlertTheme.warning,
                  // });
                }
              } else {
                this.props.PushPopupAlert({
                  title: "Something went wrong! Please try again",
                  theme: PopupAlertTheme.danger,
                });
              }
            }
          }
          console.log("Submit Application err: ", { ...error });
        }
      }
    }
  };

  componentDidMount = () => {
    if (this.props.auth.isAuthenticated === false) {
      this.props.initializeApplicationProcess(null, null);
      this.props.FC_LoginModal(true);
    } else {
      if (
        this.props.candidateApplications.candidate_applications === null &&
        this.props.candidateApply.currentStep === ApplicationStepsTypes.Step3
      ) {
        this.props.LoadCandidateApplications(
          this.props.auth.user.user_id,
          (status: boolean) => {
            this.setLoading(status);
            if (
              status === false &&
              this.props.candidateApplications.candidate_applications !== null
            ) {
              this.setState({
                candidateApplications:
                  this.props.candidateApplications.candidate_applications,
              });
            }
          }
        );
      }
    }
  };
  render() {
    if (this.props.auth.isAuthenticated === false) {
      this.props.initializeApplicationProcess(null, null);
      this.props.FC_LoginModal(true);
    }
    return (
      <div>
        <div>
          {this.props.candidateApply.selectedPosition !== null && (
            <CandidateApplicationSteps
              currentPosition={this.props.candidateApply.selectedPosition}
              currentStep={this.props.candidateApply.currentStep}
              setCandidateApplicationStep={
                this.props.setCandidateApplicationStep
              }
              initializeApplicationProcess={
                this.props.initializeApplicationProcess
              }
              auth={this.props.auth}
              candidateApply={this.props.candidateApply}
              PushPopupAlert={this.props.PushPopupAlert}
              RemoveUserApplication={this.props.RemoveUserApplication}
              candidateApplications={this.props.candidateApplications}
              schoolLevels={this.props.schoolLevels}
            />
          )}
          {/* Body here */}
          <div className="-mt-5">
            {this.props.candidateApply.completed === false &&
              this.props.candidateApply.currentStep ===
                ApplicationStepsTypes.Step1 && (
                <CandidateApplyRequirements
                  auth={this.props.auth}
                  currentPosition={this.props.candidateApply.selectedPosition}
                  periods={this.props.periods}
                  loadApplicationPeriods={this.props.loadApplicationPeriods}
                  FC_GetProfileDetails={this.props.FC_GetProfileDetails}
                  initializeApplicationProcess={
                    this.props.initializeApplicationProcess
                  }
                  candidateApply={this.props.candidateApply}
                  PushPopupAlert={this.props.PushPopupAlert}
                  setCandidateApplicationStep={
                    this.props.setCandidateApplicationStep
                  }
                  sendApplication={this.sendApplication}
                  candidateApplications={this.props.candidateApplications}
                  LoadCandidateApplications={
                    this.props.LoadCandidateApplications
                  }
                />
              )}
            {this.props.schoolLevels.selectedEducationCategory !== null &&
              this.props.candidateApply.completed === false &&
              this.props.candidateApply.currentStep ===
                ApplicationStepsTypes.Step2 && (
                <ExamCenter
                  candidateApply={this.props.candidateApply}
                  PushPopupAlert={this.props.PushPopupAlert}
                  setCandidateApplicationStep={(
                    step: ApplicationStepsTypes
                  ) => {
                    this.props.setCandidateApplicationStep(step);
                  }}
                  addCandidateExamLocation={this.props.addCandidateExamLocation}
                  auth={this.props.auth}
                  // LoadCandidateApplications={
                  //   this.props.LoadCandidateApplications
                  // }
                  sendApplication={this.sendApplication}
                  candidateApplications={this.props.candidateApplications}
                  requestedApplications={this.state.candidateApplications}
                  selectedEducationCategory={
                    this.props.schoolLevels.selectedEducationCategory
                  }
                />
              )}
            {this.props.candidateApply.currentStep ===
              ApplicationStepsTypes.Step3 &&
              this.props.candidateApply.selectedPosition !== null &&
              this.props.schoolLevels.selectedEducationCategory
                ?.session_plan !== null &&
              this.props.schoolLevels.selectedEducationCategory
                ?.session_plan !== undefined &&
              (this.state.loading === true ? (
                <div className="mt-5 bg-white">
                  <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 flex items-center justify-center gap-4 pt-2">
                    <Loading />
                  </div>
                </div>
              ) : (
                <Agreement
                  auth={this.props.auth}
                  candidateApplications={this.props.candidateApplications}
                  SelectUserApplication={this.props.SelectUserApplication}
                  candidateApply={this.props.candidateApply}
                  setCandidateApplicationStep={
                    this.props.setCandidateApplicationStep
                  }
                  PushPopupAlert={this.props.PushPopupAlert}
                  RemoveUserApplication={this.props.RemoveUserApplication}
                  setApplyCompletionStatus={this.props.setApplyCompletionStatus}
                  cleanCandidateApply={this.props.cleanCandidateApply}
                  LoadCandidateApplications={
                    this.props.LoadCandidateApplications
                  }
                  requestedApplications={this.state.candidateApplications}
                  schoolLevels={this.props.schoolLevels}
                  addCandidateApplications={this.props.addCandidateApplications}
                />
              ))}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system,
  positions,
  periods,
  application,
  schoolLevels,
  candidateApply,
  candidateApplications,
}: StoreState): {
  auth: Auth;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidateApply: candidateApplyStoreInterface;
} => {
  return {
    auth,
    system,
    application,
    positions,
    periods,
    schoolLevels,
    candidateApply,
    candidateApplications,
  };
};

export const CandidateApply = React.memo(
  connect(mapStateToProps, {
    loadEducationCategories,
    loadSchoolLevels,
    setSelectedEducationCategory,
    setSelectedSchoolLevel,
    loadApplicationPeriods,
    loadAllPositions,
    setSelectedPeriod,
    cleanSelectedSchoolLevel,
    setCandidateApplicationStep,
    FC_GetProfileDetails,
    PushPopupAlert,
    setApplicationStep,
    addCandidateExamLocation,
    SelectUserApplication,
    LoadCandidateApplications,
    FC_LoginModal,
    RemoveUserApplication,
    setApplyCompletionStatus,
    addCandidateApplications,
  })(_CandidateApply)
);
