import { Action, ActionTypes } from "../actions";
import { schoolLevelsStoreInterface } from "../actions/school_levels";

// default state
const defaultState: schoolLevelsStoreInterface = {
  education_categories: null,
  school_levels: null,
  selectedEducationCategory: null,
  selectedSchoolLevel: null,
  showInstitutes: false,
  enableClose: false,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const schoolLevelsReducer = (
  state: schoolLevelsStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOAD_EDUCATION_CATEGORIES:
      if (action.payload !== undefined) {
        state.education_categories = action.payload;
      }
      return { ...state };
    case ActionTypes.SET_SELECTED_EDUCATION_CATEGORY:
      state.selectedEducationCategory = action.payload;
      return { ...state };
    case ActionTypes.LOAD_SCHOOL_LEVELS:
      if (action.payload !== undefined) {
        state.school_levels = action.payload;
      }
      return { ...state };
    case ActionTypes.SET_SELECTED_SCHOOL_LEVELS:
      state.selectedSchoolLevel = action.payload;
      return { ...state };
    case ActionTypes.REMOVE_SELECTED_EDUCATION_CATEGORY:
      return { ...state, selectedEducationCategory: null };
    case ActionTypes.REMOVE_SELECTED_SCHOOL_LEVEL:
      return { ...state, selectedSchoolLevel: null };
    case ActionTypes.SET_SHOW_INSTITUTES:
      return { ...state, showInstitutes: action.payload };
    default:
      return state;
  }
};
