// CLAIM TYPES ==========================
export const GetClaimingTypes = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<ClaimTypesInterfaceAPI[]>(
        `${API.CLAIMING}/claim-type`
      );
      console.log({ claimtYPES: res.data });
      let claim_types: ClaimTypesInterfaceAPI[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.length > 0) {
          claim_types = res.data;
        }
      }
      dispatch<GetClaimingTypesAction>({
        type: ActionTypes.GET_CLAIMING_TYPES,
        payload: claim_types,
      });
      callback(false, "");
    } catch (error) {
      console.error({ error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

