// MY PERSONAL CLAIMS
export const getMyClaims = (
  user_id: string,
  period_id: string,
  callback: Function,
  order: "desc" | "asc" = "desc",
  sortBy: "createdAt" | "updatedAt" | "last_claim_status" = "updatedAt"
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.post<CandidateClaimInterface[]>(
        `${API.CLAIMING}/user`,
        {
          order,
          sortBy,
          filter: {
            user: user_id,
            period_id,
          },
        }
      );
      // console.log({ api_claim: res.data });
      console.log("=================getMyClaims===================");
      console.log({ getMyClaims: res });
      console.log("=================getMyClaims===================");

      dispatch<GetMyClaimsAction>({
        type: ActionTypes.GET_MY_CLAIMS,
        payload: res.data,
      });
      callback(false, "", res.data);
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};

