import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { QuestionInterface } from "../interfaces/question";
import { ExamItemResponse } from "../interfaces/prepare_exam";
import { CandidateExam } from "../interfaces/exam";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface addExamQuestionActions {
  type: ActionTypes.ADD_EXAM_QUESTION;
  payload: QuestionInterface;
}

export interface addEditExamQuestionActions {
  type: ActionTypes.ADD_EDIT_EXAM_QUESTION;
  payload: QuestionInterface;
}

export interface loadExamsListActions {
  type: ActionTypes.LOAD_EXAMS_LIST;
  payload: ExamItemResponse[];
}
export interface setSelectedExamActions {
  type: ActionTypes.SET_SELECTED_EXAM;
  payload: ExamItemResponse;
}

export interface getExamDetailsActions {
  type: ActionTypes.GET_EXAM_DETAILS;
  payload: CandidateExam;
}

export interface PrepareExamsStoreInterface {
  questions: QuestionInterface[];
  examsList: ExamItemResponse[] | null;
  selectedExam: CandidateExam | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadExamsList = (callback: (loading: boolean) => void) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<ExamItemResponse[]>(`${API.QBank}/exam`);
      dispatch<loadExamsListActions>({
        type: ActionTypes.LOAD_EXAMS_LIST,
        payload: res.data,
      });
      // console.log("Exams list: ", res.data);
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const loadExamsListByPeriod = (
  period_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<ExamItemResponse[]>(
        `${API.QBank}/exam/period/${period_id}`
      );
      dispatch<loadExamsListActions>({
        type: ActionTypes.LOAD_EXAMS_LIST,
        payload: res.data,
      });
      // console.log("Exams list: ", res.data);
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const findExamDetails = (
  exam_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<CandidateExam>(
        `${API.QBank}/exam/${exam_id}`
      );
      dispatch<getExamDetailsActions>({
        type: ActionTypes.GET_EXAM_DETAILS,
        payload: res.data,
      });
      // console.log("Exam details: ", res.data);
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const setSelectedExam = (
  exam: ExamItemResponse
): setSelectedExamActions => {
  return {
    type: ActionTypes.SET_SELECTED_EXAM,
    payload: exam,
  };
};

export const addExamQuestion = (
  question: QuestionInterface
): addExamQuestionActions => {
  return {
    type: ActionTypes.ADD_EXAM_QUESTION,
    payload: question,
  };
};

export const addEditExamQuestion = (
  question: QuestionInterface
): addEditExamQuestionActions => {
  return {
    type: ActionTypes.ADD_EDIT_EXAM_QUESTION,
    payload: question,
  };
};
