import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API, DISTRICTS_LOCATION } from "../utils/api";
import {
  CandidatePlacementContract,
  PContractItem,
} from "../interfaces/candidate_placement_contract";
import { errorToText } from "../utils/errors";
import { ContractCategoryType } from "../containers/PlacementContract/PlacementContract";

// Default contract value
const initialContractValue: PContractItem = {
  values: [],
  strict: false,
};

// Default contract
const DefaultContractInitial = (
  user_id: string,
  position_id: string,
  session_id: string
): CandidatePlacementContract => {
  return {
    user_id: user_id,
    position_id: position_id,
    session_id: session_id,
    province: initialContractValue,
    district: initialContractValue,
    sector: initialContractValue,
    school: initialContractValue,
  };
};

/**
 * * ****************************** INTERFACES *****************************
 */

export interface CandidatePlacementContractActions {
  type: ActionTypes.LOAD_CANDIDATE_PLACEMENT_CONTRACT;
  payload: CandidatePlacementContract;
}
export interface UpdateContractActions {
  type: ActionTypes.UPDATE_CONTRACT_LOCATIONS;
  payload: {
    type: ContractCategoryType;
    data: PContractItem;
  };
}

export interface CandidatePlacementContractStoreInterface {
  contract: CandidatePlacementContract | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const initializeContract = (
  user_id: string,
  position_id: string,
  session_id: string
) => {
  return async (dispatch: Dispatch) => {
    dispatch<CandidatePlacementContractActions>({
      type: ActionTypes.LOAD_CANDIDATE_PLACEMENT_CONTRACT,
      payload: DefaultContractInitial(user_id, position_id, session_id),
    });
  };
};

export const LoadCandidateContract = (
  user_id: string,
  position_id: string,
  session_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<CandidatePlacementContract>(
        `${API.PLACEMENT}/contract/user/${position_id}`
      );

      if (res.data === null || res.data === undefined) {
        dispatch<CandidatePlacementContractActions>({
          type: ActionTypes.LOAD_CANDIDATE_PLACEMENT_CONTRACT,
          payload: DefaultContractInitial(user_id, position_id, session_id),
        });
      } else {
        dispatch<CandidatePlacementContractActions>({
          type: ActionTypes.LOAD_CANDIDATE_PLACEMENT_CONTRACT,
          payload: res.data,
        });
        // console.log("Candidate Placement Contract: ", res.data);
      }
    } catch (error: any) {
      alert(errorToText(error));
      // console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export type FT_LoadCandidateContract = (
  user_id: string,
  position_id: string,
  session_id: string,
  callback: (
    error: boolean,
    contract: CandidatePlacementContract | null,
    msg: string
  ) => void
) => void;
export const FC_LoadCandidateContract = (
  user_id: string,
  position_id: string,
  session_id: string,
  callback: (
    error: boolean,
    contract: CandidatePlacementContract | null,
    msg: string
  ) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.get<CandidatePlacementContract>(
        `${API.PLACEMENT}/contract/user/${user_id}/${position_id}`
      );

      if (res.data === null || res.data === undefined) {
        callback(
          true,
          DefaultContractInitial(user_id, position_id, session_id),
          ""
        );
      } else {
        callback(true, res.data, "");
      }
    } catch (error: any) {
      callback(false, null, errorToText(error));
    }
  };
};

export const UpdateContractLocations = (
  type: ContractCategoryType,
  data: PContractItem
) => {
  return (dispatch: Dispatch) => {
    dispatch<UpdateContractActions>({
      type: ActionTypes.UPDATE_CONTRACT_LOCATIONS,
      payload: {
        type: type,
        data: data,
      },
    });
  };
};

