export interface SchoolContract {
  createdAt: Date;
  school_code: string;
  school_name: string;
  school_status: string;
  sector_code: string;
  sector_id: string;
  status: number;
  updatedAt: Date;
  _id: string;
}
export type FT_LoadSchoolsByDistrict = (
  sector_id: string,
  callback: (error: boolean, contract: SchoolContract[], msg: string) => void
) => void;

export const FC_LoadSchoolsByDistrict = (
  sector_id: string,
  callback: (data: boolean, contract: SchoolContract[], msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.get<SchoolContract[]>(
        `${API.APPLICATION}/sectorschool/${sector_id}`
      );

      callback(true, res.data, "");
    } catch (error: any) {
      // console.log("Err: ", { ...error });
      callback(false, [], errorToText(error));
    }
  };
};

export type FT_LoadSchoolsByIDs = (
  schools: string[],
  callback: (data: boolean, contract: SchoolContract[], msg: string) => void
) => void;
export const FC_LoadSchoolsByIDs = (
  schools: string[],
  callback: (data: boolean, contract: SchoolContract[], msg: string) => void
) => {
  console.log("=================FC_LoadSchoolsByIDs===================");
  console.log({ schools });
  console.log("=================FC_LoadSchoolsByIDs===================");
  return async (dispatch: Dispatch) => {
    try {
      const res = await axios.post<SchoolContract[]>(
        `${API.APPLICATION}/schoolcodes`,
        { codes: schools }
      );

      callback(true, res.data, "");
    } catch (error: any) {
      // console.log("schools_list_by_ids: ", { ...error });
      callback(false, [], errorToText(error));
    }
  };
};

export type FT_SubmitContract = (
  contract: CandidatePlacementContract,
  callback: (data: boolean, msg: string) => void
) => void;
export const FC_SubmitContract = (
  contract: CandidatePlacementContract,
  callback: (data: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      await axios.post(`${API.PLACEMENT}/contract`, contract);

      callback(true, "");
    } catch (error: any) {
      // console.log("Err: ", { ...error });
      callback(false, errorToText(error));
    }
  };
};
