import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import {
  QuestionInterface,
  uploadQuestionsResponse,
} from "../interfaces/question";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface setSelectedQuestionActions {
  type: ActionTypes.SET_SELECTED_QUESTION;
  payload: QuestionInterface;
}

export interface UpdateQuestionActions {
  type: ActionTypes.UPDATE_QUESTION;
  payload: QuestionInterface;
}

export interface loadQuestionBankActions {
  type: ActionTypes.LOAD_QUESTION_BANK;
  payload: QuestionInterface[];
}

export interface deleteQuestionBankActions {
  type: ActionTypes.DELETE_QUESTION_BANK;
  payload: string;
}

export interface setUploadedQuestionsActions {
  type: ActionTypes.SET_UPLOADED_QUESTIONS;
  payload: uploadQuestionsResponse | null;
}

export interface setIncrementQuestionBankActions {
  type: ActionTypes.INCREMENT_QUESTION_BANK;
  payload: QuestionInterface;
}

export interface QuestionBankStoreInterface {
  questions: QuestionInterface[] | null;
  selectedQuestion: QuestionInterface | null;
  uploadedQuestions: uploadQuestionsResponse | null;
}

/**
 * * ****************************** ACTIONS *****************************
 */

export const loadQuestionBank = (
  course_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<QuestionInterface[]>(
        `${API.QBank}/question/course/${course_id}`
      );
      dispatch<loadQuestionBankActions>({
        type: ActionTypes.LOAD_QUESTION_BANK,
        payload: res.data,
      });
      // console.log("Question bank: ", res.data);
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const removeQuestion = (
  question_id: string,
  callback: (loading: boolean, status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true, true, "");
    try {
      const res = await axios.delete(`${API.QBank}/question/${question_id}`);
      dispatch<deleteQuestionBankActions>({
        type: ActionTypes.DELETE_QUESTION_BANK,
        payload: question_id,
      });
      callback(false, true, "Question deleted successfully!");
      console.log("Delete question: ", res);
    } catch (error: any) {
      callback(false, false, "Failed to delete the question");
      console.log("Err: ", { ...error });
    }
  };
};

export const setSelectedQuestion = (
  question: QuestionInterface
): setSelectedQuestionActions => {
  return {
    type: ActionTypes.SET_SELECTED_QUESTION,
    payload: question,
  };
};

export const setUploadedQuestions = (
  questions: uploadQuestionsResponse | null
): setUploadedQuestionsActions => {
  return {
    type: ActionTypes.SET_UPLOADED_QUESTIONS,
    payload: questions,
  };
};

export const UpdateQuestion = (
  question: QuestionInterface,
  callback: (loading: boolean) => void,
  actionStatus: (loading: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      await axios.put(
        `${API.QBank}/question/${question.question_id}`,
        question
      );
      dispatch<UpdateQuestionActions>({
        type: ActionTypes.UPDATE_QUESTION,
        payload: question,
      });
      callback(true);
      actionStatus(true, "Question updated successfully!");
      // console.log("Edited Question: ", res.data);
    } catch (error: any) {
      if (error.response) {
        if (error.response.data) {
          if (error.response.data.errors !== undefined) {
            if (error.response.data.errors.length > 0) {
              actionStatus(false, error.response.data.errors[0].message);
            } else {
              actionStatus(false, "Failed to update Question!");
            }
          } else {
            actionStatus(false, "Failed to update Question!");
          }
        } else {
          actionStatus(false, "Failed to update Question!");
        }
      }
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const IncrementQuestionBank = (
  questions: QuestionInterface
): setIncrementQuestionBankActions => {
  return {
    type: ActionTypes.INCREMENT_QUESTION_BANK,
    payload: questions,
  };
};

export const loadQuestionPreview = (
  question_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<QuestionInterface[]>(
        `${API.QBank}/question/${question_id}`
      );
      dispatch<loadQuestionBankActions>({
        type: ActionTypes.LOAD_QUESTION_BANK,
        payload: res.data,
      });
      // console.log("Question bank: ", res.data);
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};
