import { Action, ActionTypes } from "../actions";
import { PositionAPI } from "../interfaces/position";
import {
  AccessTaskInterface,
  SystemSettingsInterface,
} from "../shared/interfaces";

export interface SystemSettingsType {
  systemSettings: SystemSettingsInterface[];
  accessTasks: AccessTaskInterface[] | null;
  systemPositions: PositionAPI[] | null;
}

// default state
const initialState = {
  systemSettings: [],
  accessTasks: null,
  systemPositions: null,
};

export const systemSettingsReducer = (
  state: SystemSettingsType = initialState,
  action: Action
) => {
  let index: number = -1;
  let data_SETTINGS: SystemSettingsInterface[] = [];
  let _data_POSITION: PositionAPI[] = [];
  switch (action.type) {
    case ActionTypes.GET_SYSTEM_SETTINGS:
      return {
        ...state,
        systemSettings: action.payload,
      };
    case ActionTypes.DISABLE_SYSTEM_SETTINGS:
      data_SETTINGS = [...state.systemSettings];
      index = state.systemSettings.findIndex(
        (item) => item.system_setting_id === action.payload.system_setting_id
      );
      if (index >= 0) {
        data_SETTINGS[index].status = action.payload.status;
      }
      return {
        ...state,
        systemSettings: data_SETTINGS,
      };
    case ActionTypes.GET_TASK_ACCESS:
      return {
        ...state,
        accessTasks: action.payload,
      };
    case ActionTypes.GET_POSTIONS:
      return {
        ...state,
        systemPositions: action.payload,
      };
    case ActionTypes.DELETE_POSITION:
      return {
        ...state,
        systemPositions:
          state.systemPositions &&
          state.systemPositions.filter(
            (element) => element.position_id !== action.payload
          ),
      };
    case ActionTypes.PUBLISH_POSITION:
      if (state.systemPositions) {
        _data_POSITION = [...state.systemPositions];
        index = -1;
        index = _data_POSITION.findIndex(
          (item) => item.position_id === action.payload.position_id
        );
        if (index >= 0) {
          _data_POSITION[index].isPublished = action.payload.status;
        }
        return {
          ...state,
          systemPositions: _data_POSITION,
        };
      }
      return state;
    case ActionTypes.LOGOUT:
      return {
        ...initialState,
      };
    default:
      return state;
  }
};
