/**
 * @description receive the list of active candidates form the live server and activate them
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export type FT_SetActiveCandidates = (
  room: OnlineProctoringRoom,
  candidates: ActiveCandidateLiveServer[]
) => void;
export const FC_SetActiveCandidates = (
  room: OnlineProctoringRoom,
  candidates: ActiveCandidateLiveServer[]
) => {
  return (dispatch: Dispatch) => {
    dispatch<AddSctiveUserToRoom>({
      type: ActionTypes.ADD_ACTIVE_USER_TO_ROOM,
      payload: candidates,
    });

    if (room.start_exam === true && room.start_time !== null) {
      // set the exam to start
      dispatch<UpdateExamStartStatus>({
        type: ActionTypes.UPDATE_THE_EXAM_START_STATUS,
        payload: room.start_time,
      });
    }
  };
};

