export type FT_CardStyle = (
  stopped: boolean,
  dark_theme: boolean,
  done: boolean
) => void;
export const FC_CardStyle = (
  stopped: boolean = false,
  dark_theme: boolean = false,
  done: boolean = false
) => {
  if (done === true) {
    return dark_theme === true ? `card-done-dark` : `card-done-light`;
  } else if (stopped === true) {
    return dark_theme === true ? `card-stopped-dark` : `card-stopped-light`;
  } else {
    return dark_theme === true ? `card-default-dark` : `card-default-light`;
  }
};

