export type FT_AddMyStream = (
  socket_id: string,
  callBack: (stream: any) => void
) => void;
export const FC_AddMyStream = (
  socket_id: string,
  callBack: (stream: any) => void
) => {
  return (dispatch: Dispatch) => {
    navigator.mediaDevices
      .getUserMedia(defaultVideoQuality)
      .then((stream) => {
        //* Add the stream to the redux
        dispatch<AddMyStream>({
          type: ActionTypes.HOME_PROCTORING__ADD_MY_STREAM,
          payload: {
            stream: stream,
            error: false,
            peer_id: null,
            socket_id: socket_id,
            stream_id: stream.id,
          },
        });
        callBack(stream);
      })
      .catch((err) => {
        dispatch<AddMyStream>({
          type: ActionTypes.HOME_PROCTORING__ADD_MY_STREAM,
          payload: {
            stream: null,
            error: true,
            peer_id: null,
            socket_id: null,
            stream_id: null,
          },
        });
        console.log(
          "error occured when trying to get an access to get local stream"
        );
        console.log(err);
      });
  };
};

