export enum ReportUserExamStatus {
  // when user start exam like answering question
  Editing = "Editing",
  // when user have been paused
  Paused = "Paused",
  // when user have been removed from exam
  Removed = "Removed",
  // when user have been finished doin exam
  Done = "Done",
}

export interface ExamDoneAndRemainingByPeriodInterface {
  period: string;
  course: string;
  exam_status: "Done" | "Active";
  exam_date: string;
  start_time: string;
  end_time: string;
  duration: number;
  id: string;
  rules: {
    right_click_enabled: boolean;
    copy_enabled: boolean;
    paste_enabled: boolean;
    stop_candidate_when_comeback: boolean;
    allow_to_leave_browser: boolean;
  };
}

export interface ExamsParcentageSuccessFailedRateByPeriodInterface {
  period: string;
  course: string;
  exam_date: string;
  start_time: string;
  duration: number;
  total_marks: number;
  answer_sheet_id: string;
  // exam_id: string;
}
export interface CandidatesFailedSucceeded {
  exam_location: {
    district_id: string;
    center_id: string;
    room_id: string;
  };
  _id: string;
  period: string;
  exam_start_time: string;
  exam_end_time: string;
  exam_date: string;
  course: string;
  exam_paper: string;
  questions: string[];
  user_answers: string[];
  user_status: ReportUserExamStatus;
  used_duration: number;
  total_marks: number;
  number_of_question_answered: number;
  createdAt: string;
  updatedAt: string;
  __v: number;
  reason: string;
  disability: boolean;
  gender: string;
  in_education: boolean;
  position: string;
  user_reason: string;
  start_time: number;
  end_time: number;
  status: number;
  user: {
    residence: {
      province_id: string;
      province_name: string;
      district_id: string;
      district_name: string;
      sector_id: string;
      sector_name: string;
    };
    n_id: string;
    phone_number: string;
    email: string;
    user_info: {
      fname: string;
      lname: string;
      sex: "F" | "M";
      photo: string;
      sector: string;
      district: string;
      province: string;
    };
    user_id: string;
  };
  answer_sheet_id: string;
}

