const getCourseIdBySheetId = (
  allData: ExamAnswerSheetDone[],
  sheet_id: string
) => {
  return allData.find((item) => item.answer_sheet_id === sheet_id);
};

type ClaimFormProps = {
  claim_type: PlansTypeKeys;
  file_id: string;
  setFormState: (value: boolean, response?: boolean) => void;
  auth: Auth;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidateExam: candidateExamStoreInterface;
  createClaim: Function;
  GetClaimingTypes: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getMyRejectedApplications: Function;
  getMyRejectedDocuments: Function;
  getMyClaims: Function;
  getMyAnswers: Function;
  getClaimReasons: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;

  // DEFAULT DATA ==========
  // candidateDocuments?: DocumentClaimUser[];
  // candidateAnswers?: ClaimExamResultsDetails[];
  // candidateApplications?: claimApplicationsInterface[];
};

type ClaimFormState = {
  loading: boolean;
  loadingForm: boolean;
  formError: {
    element: string;
    msg: string;
  } | null;
  thisClaimType: PlansTypeKeys | null;
  thisClaimTypeId: string;
  claimComment: string;
  claimFileId: string;
  chooseClaimFile: boolean;
  chooseClaimReason: boolean;
  claimReason: claimReasonInterface | null;
  viewThisFile: string;
  formType: "DEFAULT" | "PROPS";
  alertError: string | null;
};

export class ClaimFormComponent extends Component<
  ClaimFormProps,
  ClaimFormState
> {
  constructor(props: ClaimFormProps) {
    super(props);

    this.state = {
      loading: true,
      loadingForm: false,
      formError: null,
      thisClaimType: null,
      thisClaimTypeId: "",
      claimComment: "",
      claimFileId: "",
      chooseClaimFile: false,
      viewThisFile: "",
      formType: "DEFAULT",
      alertError: null,
      chooseClaimReason: false,
      claimReason: null,
    };
  }

  // GENERATE SET STATES FUNCTIONS
  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  setLoadingForm = (state: boolean) => {
    this.setState({ loadingForm: state });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  setAlertError = (data: string | null) => {
    this.setState({ alertError: data });
  };

  setClaimFileId = (data: string) => {
    this.setState({ claimFileId: data });
  };

  setViewThisFile = (data: string) => {
    this.setState({ viewThisFile: data });
  };

  setChooseClaimFile = (data: boolean) => {
    this.setState({ chooseClaimFile: data });
  };

  setClaimComment = (data: string) => {
    this.setState({ claimComment: data });
  };

  setChooseClaimReason = (value: boolean) => {
    this.setState({ chooseClaimReason: value });
  };

  setClaimReason = (value: claimReasonInterface | null) => {
    this.setState({ claimReason: value });
  };

  // CHECK IF PASSED ID IS VALID
  isDocumentIdValid = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    let doc = documents.find((item) => item.doc_id === doc_id);
    if (doc) {
      // CHECK IF DOCUMENT HAS REJECTED STATUS
      if (doc.validated_by.validation_status !== "rejected") {
        return {
          status: false,
          msg: "Only rejected document are allowed for claim",
          alert: PopupAlertTheme.info,
        };
      }
      return { status: true, msg: "", alert: PopupAlertTheme.success };
    } else {
      return {
        status: false,
        msg: "Invalid document id",
        alert: PopupAlertTheme.warning,
      };
    }
  };

  isApplicationIdValid = (
    applications: GetApplicationsAPI[],
    app_id: string
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    const app = applications.find((item) => item.application_id === app_id);
    if (app) {
      // CHECK IF DOCUMENT HAS REJECTED STATUS
      if (app.application_status !== ApplicationStates.Rejected) {
        return {
          status: false,
          msg: "Only rejected application are allowed for claim",
          alert: PopupAlertTheme.info,
        };
      }
      // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
      return { status: true, msg: "", alert: PopupAlertTheme.info };
    } else {
      return {
        status: false,
        msg: "Invalid application id",
        alert: PopupAlertTheme.warning,
      };
    }
  };

  isExamIdValid = (
    exams: ExamAnswerSheetDone[],
    exam_id: string
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    const exam = exams.find((item) => item.answer_sheet_id === exam_id);
    if (exam) {
      // CHECK IF DOCUMENT HAS REJECTED STATUS
      if (exam.total_marks === 100) {
        return {
          status: false,
          msg: "You can't claim on exam which you have 100%",
          alert: PopupAlertTheme.info,
        };
      }

      return { status: true, msg: "", alert: PopupAlertTheme.info };
    } else {
      return {
        status: false,
        msg: "Invalid answer sheet id",
        alert: PopupAlertTheme.warning,
      };
    }
    // return {
    //   status: false,
    //   msg: "No Application, please try again!",
    //   alert: PopupAlertTheme.info,
    // };
  };

  componentDidMount = () => {
    if (this.props.auth.application_session === null) {
      this.props.PushPopupAlert({
        title: "No session active session",
        theme: PopupAlertTheme.danger,
      });
      this.props.setFormState(false);
      this.setState({
        claimFileId: "",
        thisClaimType: null,
        thisClaimTypeId: "",
        claimComment: "",
        chooseClaimFile: false,
        formType: "DEFAULT",
        viewThisFile: "",
        formError: null,
      });
    } else {
      if (
        this.props.documentReducer.documentCategory === null &&
        this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
      ) {
        this.props.getDocumentCategory();
      }
      if (
        this.props.documentReducer.documentSubCategory === null &&
        this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
      ) {
        this.props.getDocumentSubCategory();
      }
      if (
        this.props.documentReducer.documentFields === null &&
        this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
      ) {
        this.props.getDocumentFields();
      }
      if (
        this.props.candidateApplications.candidate_applications !== null &&
        this.props.claim_type === PlansTypeKeys.APPLICATION
      )
        this.setLoading(false);

      if (
        (this.props.claiming.candidateDocuments === null ||
          this.props.claiming.candidateDocuments.length !==
            this.props.auth.documents.length) &&
        this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
      ) {
        this.props.getMyRejectedDocuments(
          (status: boolean, msg: string, data?: DocumentClaimUser[]) => {
            this.setLoading(status);
            if (!status) {
              if (msg.length > 0) {
                this.props.PushPopupAlert({
                  title: msg,
                  theme: PopupAlertTheme.danger,
                });
              }
              if (data) {
                // PROPS VALIDATION PROCESS ###############
                if (
                  this.props.claim_type &&
                  this.props.file_id &&
                  this.state.formType === "DEFAULT"
                ) {
                  if (
                    this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
                  ) {
                    let _isDocumentIdValid = this.isDocumentIdValid(
                      data,
                      this.props.file_id
                    );
                    if (!_isDocumentIdValid.status) {
                      this.props.PushPopupAlert({
                        title: _isDocumentIdValid.msg,
                        theme: _isDocumentIdValid.alert,
                      });
                      // setAlertError(_isDocumentIdValid.msg);
                      this.props.setFormState(false);
                    } else {
                      this.setState({
                        thisClaimType: this.props.claim_type,
                        claimFileId: this.props.file_id,
                        formType: "PROPS",
                      });
                    }
                  }
                }
              }
            }
          }
        );
      } else this.setLoading(false);

      if (this.props.claim_type === PlansTypeKeys.EXAMINATION)
        this.setLoading(false);

      if (this.props.claiming.claimReasons === null) {
        this.props.getClaimReasons((status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length > 0) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        });
      }

      if (this.props.claiming.myClaims === null) {
        this.props.getMyClaims(
          this.props.auth.user.user_id,
          this.props.auth.application_session._id,
          (status: boolean, msg: string) => {
            this.setLoading(status);
            if (status === false && msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        );
      } else this.setLoading(false);

      // PROPS VALIDATION PROCESS ###############
      if (
        !isEmptyOrSpaces(this.props.claim_type) &&
        !isEmptyOrSpaces(this.props.file_id) &&
        this.state.formType === "DEFAULT"
      ) {
        this.setState({ thisClaimType: this.props.claim_type });
        if (
          this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION &&
          this.props.claiming.candidateDocuments
        ) {
          let _isDocumentIdValid = this.isDocumentIdValid(
            this.props.claiming.candidateDocuments,
            this.props.file_id
          );
          if (!_isDocumentIdValid.status) {
            this.props.PushPopupAlert({
              title: _isDocumentIdValid.msg,
              theme: _isDocumentIdValid.alert,
            });
            // setAlertError(_isDocumentIdValid.msg);
            this.props.setFormState(false);
          } else {
            this.setState({
              thisClaimType: this.props.claim_type,
              claimFileId: this.props.file_id,
              formType: "PROPS",
            });
          }
        }
        if (
          this.props.claim_type === PlansTypeKeys.APPLICATION &&
          this.props.candidateApplications.candidate_applications
        ) {
          let _isApplicationIdValid = this.isApplicationIdValid(
            this.props.candidateApplications.candidate_applications,
            this.props.file_id
          );
          if (!_isApplicationIdValid.status) {
            this.props.PushPopupAlert({
              title: _isApplicationIdValid.msg,
              theme: _isApplicationIdValid.alert,
            });
            this.props.setFormState(false);
          } else {
            this.setState({
              thisClaimType: this.props.claim_type,
              claimFileId: this.props.file_id,
              formType: "PROPS",
            });
          }
        }

        if (
          this.props.claim_type === PlansTypeKeys.EXAMINATION &&
          this.props.candidateExam.user_exams_done
        ) {
          let _isExamIdValid = this.isExamIdValid(
            this.props.candidateExam.user_exams_done,
            this.props.file_id
          );
          if (!_isExamIdValid.status) {
            this.props.PushPopupAlert({
              title: _isExamIdValid.msg,
              theme: _isExamIdValid.alert,
            });
            this.props.setFormState(false);
          } else {
            this.setState({
              thisClaimType: this.props.claim_type,
              claimFileId: this.props.file_id,
              formType: "PROPS",
            });
          }
        }
      }
    }
  };

  getDocumentById = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): DocumentClaimUser => {
    return documents.filter((item) => item.doc_id === doc_id)[0];
  };

  getApplicationById = (
    applications: GetApplicationsAPI[],
    application_id: string
  ): claimApplicationsInterface | undefined => {
    let result: claimApplicationsInterface | undefined = undefined;
    let thisApplication = applications.find(
      (item) => item.application_id === application_id
    );

    if (thisApplication)
      result = {
        application_id: thisApplication.application_id,
        application_period: thisApplication.application_session._id,
        createdAt: thisApplication.createdAt,
        exams: thisApplication.exams,
        position: thisApplication.position,
        application_status:
          thisApplication.application_status as ApplicationStates,
        updatedAt: thisApplication.updatedAt,
        user_id: thisApplication.user_id,
        application_history: thisApplication.application_history,
        application_session: thisApplication.application_session,
        override_by: {
          application_status: thisApplication.override_by.application_status,
          comment: thisApplication.override_by.comment,
          date: thisApplication.override_by.date,
          user_id: thisApplication.override_by.user_id,
        },
      };

    return result;
  };

  getValidation = (
    thisId: string
  ): {
    old_reason: string;
    old_status: string;
    old_evaluated_by: string;
  } => {
    let result: {
      old_reason: string;
      old_status: string;
      old_evaluated_by: string;
    } = { old_reason: "", old_status: "", old_evaluated_by: "" };

    if (
      this.state.thisClaimType === PlansTypeKeys.DOCUMENT_VALIDATION &&
      this.props.claiming.candidateDocuments
    ) {
      result = {
        old_reason: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.comment,
        old_status: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.validation_status,
        old_evaluated_by: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.user_id,
      };
    } else if (
      this.state.thisClaimType === PlansTypeKeys.APPLICATION &&
      this.props.candidateApplications.candidate_applications &&
      this.getApplicationById(
        this.props.candidateApplications.candidate_applications,
        thisId
      )
    ) {
      result = {
        old_reason: this.getApplicationById(
          this.props.candidateApplications.candidate_applications,
          thisId
        )!.override_by.comment,
        old_status: this.getApplicationById(
          this.props.candidateApplications.candidate_applications,
          thisId
        )!.override_by.application_status,
        old_evaluated_by: this.getApplicationById(
          this.props.candidateApplications.candidate_applications,
          thisId
        )!.override_by.user_id,
      };
    }

    return result;
  };

  loadUserById = async (user_id: string, callback: Function) => {
    if (isEmptyOrSpaces(user_id)) {
      return this.props.PushPopupAlert({
        title: "No user id provided",
        theme: PopupAlertTheme.danger,
      });
    }
    try {
      setAxiosToken();
      const res = await axios.get<UserInterface>(
        `${API.AUTH}/users/${user_id}`
      );
      console.log("====================================");
      console.log({ loadUserById: res });
      console.log("====================================");
      if (res) {
        callback(res.data);
      }
    } catch (error) {
      console.error({ error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  getClaimTitle = (claimType: PlansTypeKeys): string => {
    let result: string = "";
    // title from document
    if (
      claimType === PlansTypeKeys.DOCUMENT_VALIDATION &&
      this.props.claiming.candidateDocuments
    ) {
      result = ClaimGetDocTitle(
        this.props.documentReducer,
        this.getDocumentById(
          this.props.claiming.candidateDocuments,
          this.state.claimFileId
        )
      ).main;
    }
    // title from application
    if (
      claimType === PlansTypeKeys.APPLICATION &&
      this.props.candidateApplications.candidate_applications &&
      this.getApplicationById(
        this.props.candidateApplications.candidate_applications,
        this.state.claimFileId
      )
    ) {
      result = this.getApplicationById(
        this.props.candidateApplications.candidate_applications,
        this.state.claimFileId
      )!.position.position_title;
    }
    // title from examination
    if (
      claimType === PlansTypeKeys.EXAMINATION &&
      getCourseIdBySheetId(
        this.props.candidateExam.user_exams_done,
        this.state.claimFileId
      )
    ) {
      result = getCourseIdBySheetId(
        this.props.candidateExam.user_exams_done,
        this.state.claimFileId
      )!.course;
    }
    // if (
    //   claimType === PlansTypeKeys.PLACEMENT_TRANSFER &&
    //   this.state.myPlacement
    // ) {
    //   result = "Placed on " + this.state.myPlacement.placed.school.school_name;
    // }

    return result;
  };

  cleanToClose = () => {
    this.props.setFormState(false);
    this.setState({
      claimFileId: "",
      thisClaimType: null,
      thisClaimTypeId: "",
      claimComment: "",
      chooseClaimFile: false,
      formType: "DEFAULT",
      viewThisFile: "",
      formError: null,
    });
  };

  handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!this.state.thisClaimType) {
      return this.setFormError({
        element: "claim_type",
        msg: "Claim type is required!",
      });
    }
    if (isEmptyOrSpaces(this.state.claimFileId)) {
      return this.setFormError({
        element: "claim_file",
        msg: `Please select ${this.state.thisClaimType.toLocaleLowerCase()} you are claiming on!`,
      });
    }

    if (!this.state.claimReason) {
      return this.setFormError({
        element: "claim_reason",
        msg: `Please select the reason of your claim!`,
      });
    }

    if (isEmptyOrSpaces(this.state.claimComment)) {
      return this.setFormError({
        element: "claim_comment",
        msg: `Claim comment is required!`,
      });
    }

    if (!this.props.auth.application_session) {
      return this.setAlertError(`No active claiming session is available!`);
    }
    let old_evaluator_email: string = "info@system.com";
    let old_evaluator_name: string = "System";
    let evaluator_email: string = "";
    let evaluator_name: string = "";
    let title: string = this.getClaimTitle(this.state.thisClaimType);

    if (
      this.state.thisClaimType !== PlansTypeKeys.EXAMINATION &&
      this.getValidation(this.state.claimFileId).old_evaluated_by !== "system"
    ) {
      await this.loadUserById(
        this.getValidation(this.state.claimFileId).old_evaluated_by,
        (data: UserInterface) => {
          old_evaluator_email = data.email;
          old_evaluator_name =
            data.user_info.fname + " " + data.user_info.lname;
        }
      );
    }
    this.setLoadingForm(true);
    try {
      setAxiosToken();
      const res = await axios.post<CandidateClaimInterface>(
        `${API.CLAIMING}/user/create`,
        {
          claiming_on_id: this.state.claimFileId,
          type_key: this.state.thisClaimType,
          user: this.props.auth.user.user_id,
          period_id: this.props.auth.application_session._id,
          default_claim_resaons: this.state.claimReason.id,
          reason: this.state.claimComment,
          // old_reason: this.getValidation(this.state.claimFileId).old_reason,
          // old_status: this.getValidation(this.state.claimFileId).old_status,
          // old_evaluated_by: this.getValidation(this.state.claimFileId)
          //   .old_evaluated_by,
          old_reason:
            this.state.thisClaimType === PlansTypeKeys.EXAMINATION ||
            this.state.thisClaimType === PlansTypeKeys.PLACEMENT_TRANSFER
              ? "System"
              : this.getValidation(this.state.claimFileId).old_reason,
          old_status:
            this.state.thisClaimType === PlansTypeKeys.EXAMINATION ||
            this.state.thisClaimType === PlansTypeKeys.PLACEMENT_TRANSFER
              ? "Failed"
              : this.getValidation(this.state.claimFileId).old_status,
          old_evaluated_by:
            this.state.thisClaimType === PlansTypeKeys.EXAMINATION ||
            this.state.thisClaimType === PlansTypeKeys.PLACEMENT_TRANSFER
              ? ""
              : this.getValidation(this.state.claimFileId).old_evaluated_by,
          old_evaluator_email,
          old_evaluator_name,
          evaluator_email,
          evaluator_name,
          title,
        }
      );
      console.log("====================================");
      console.log({ createClaim: res.data });
      console.log("====================================");
      if (res.status === 201 || res.status === 200) {
        this.props.createClaim(res.data);
        this.props.PushPopupAlert({
          title: "Claim sent successful",
          theme: PopupAlertTheme.success,
        });
        this.cleanToClose();
        this.props.setFormState(false);
      }
    } catch (error) {
      console.error({ error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setFormState(false);
    this.setLoadingForm(false);
  };

  displayClaimType = (claimType: PlansTypeKeys): string => {
    let result: string = "";
    switch (claimType) {
      case PlansTypeKeys.APPLICATION:
        result = "application";
        break;
      case PlansTypeKeys.DOCUMENT_VALIDATION:
        result = "document";
        break;
      case PlansTypeKeys.EXAMINATION:
        result = "exam";
        break;
      case PlansTypeKeys.MARKS_PUBLICATION:
        result = "marks publication";
        break;
      case PlansTypeKeys.PLACEMENT_TRANSFER:
        result = "placement";
        break;
      default:
        result = "";
        break;
    }

    return result;
  };

  selectClaimType = (thisClaim: string) => {
    this.setState({
      thisClaimType: isEmptyOrSpaces(thisClaim)
        ? null
        : (thisClaim as PlansTypeKeys),
    });
  };

  generateChooseFileTitle = (thisClaimType: PlansTypeKeys | null): string => {
    let result: string = "";

    switch (thisClaimType) {
      case PlansTypeKeys.APPLICATION:
        result = `Select application you are claiming on?`;
        break;
      case PlansTypeKeys.DOCUMENT_VALIDATION:
        result = `Select document you are claiming on?`;
        break;
      case PlansTypeKeys.EXAMINATION:
        result = `Select exam you are claiming on?`;
        break;
      case PlansTypeKeys.MARKS_PUBLICATION:
        result = `Select marks publication you are claiming on?`;
        break;
      case PlansTypeKeys.PLACEMENT_TRANSFER:
        result = `Select placement you are claiming on?`;
        break;
      default:
        result = "Select file you are claiming on?";
        break;
    }

    return result;
  };

  getClaimById = (
    myClaims: CandidateClaimInterface[],
    fileId: string,
    sessionId: string
  ): CandidateClaimInterface | undefined => {
    return myClaims.find(
      (item) => item.claiming_on_id === fileId && item.period_id === sessionId
    );
  };

  render() {
    // CHECK IF STILL LOADING DATA ===============
    if (
      this.props.auth.application_session !== null &&
      (this.state.loading ||
        (this.props.claiming.myClaims === null &&
          !isEmptyOrSpaces(this.props.file_id) &&
          !isEmptyOrSpaces(this.props.claim_type)))
    ) {
      return <Loading />;
    }

    if (
      this.props.claiming.myClaims &&
      this.props.auth.application_session &&
      this.getClaimById(
        this.props.claiming.myClaims,
        this.props.file_id,
        this.props.auth.application_session._id
      ) !== undefined
    ) {
      return (
        <div>
          <h2 className="text-center text-2xl font-bold p-3 text-gray-500">
            Claims on this {this.displayClaimType(this.props.claim_type!)}
          </h2>
          {this.getClaimById(
            this.props.claiming.myClaims,
            this.props.file_id,
            this.props.auth.application_session._id
          )!.claims.length >= 1 ? (
            <div>
              {this.getClaimById(
                this.props.claiming.myClaims,
                this.props.file_id,
                this.props.auth.application_session._id
              )!.claims.map((itm) => (
                <div className="p-2 border rounded mb-2">
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Created date:</span>{" "}
                    <b>{DATE(itm.createdAt)}</b>
                  </div>
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Reason:</span>{" "}
                    <p>{itm.reason}</p>
                  </div>
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Evaluation comment:</span>{" "}
                    <p>{itm.evaluation_comment}</p>
                  </div>
                  <div className="p-1">
                    <span className="text-gray-400 block">decision:</span>{" "}
                    <h3 className="text-xl font-bold">
                      {itm.decision_taken_status}
                    </h3>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div>No claims found!</div>
          )}
        </div>
      );
    }

    return (
      <div className="pb-3">
        {/* CLAIM FORM ============================================== */}
        {this.state.viewThisFile.length <= 0 && (
          <form onSubmit={(e) => this.handleSubmit(e)}>
            <div className="px-6 pb-3">
              {this.state.alertError && (
                <div
                  className="alert-warning cursor-pointer"
                  onClick={() => this.setAlertError(null)}
                >
                  {this.state.alertError}
                </div>
              )}
              <div className="text-base text-gray-700 sm:text-lg">
                {/* SELECT CLAIM TYPE ============================================== */}
                <div className="flex flex-col mb-2">
                  <label>Claim type</label>
                  <select
                    className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                      this.state.formType === "PROPS" && "cursor-not-allowed"
                    } ${
                      this.state.formError &&
                      this.state.formError.element === "claim_type"
                        ? "border-red-500"
                        : "border-gray-500"
                    } border-2`}
                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                      this.selectClaimType(e.target.value);
                      this.setClaimFileId("");
                      this.state.formError &&
                        this.state.formError.element === "claim_type" &&
                        this.setFormError(null);
                    }}
                    value={
                      this.state.thisClaimType ? this.state.thisClaimType : ""
                    }
                    disabled={
                      this.state.loadingForm || this.state.formType === "PROPS"
                    }
                  >
                    <option value="">Choose claim type</option>
                    {Object.values(PlansTypeKeys).map((item, index) => (
                      <option value={item} key={index + item}>
                        {capitalizeFirstLetter(
                          item.toLocaleLowerCase().replace(/_/g, " ")
                        )}
                      </option>
                    ))}
                  </select>
                  {this.state.formError &&
                    this.state.formError.element === "claim_type" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END SELECT CLAIM TYPE ============================================== */}

                {/* SELECT FILE TO CLAIM ON ============================================== */}
                <div className="flex flex-col mb-2">
                  <label>
                    {this.generateChooseFileTitle(this.state.thisClaimType)}
                  </label>
                  <div
                    className={`text-left bg-white border-2 ${
                      this.state.formType !== "PROPS"
                        ? "border-gray-700"
                        : "border-gray-400 text-gray-500"
                    } rounded w-full ${
                      this.state.chooseClaimFile ? "h-64" : "h-10"
                    } ${
                      this.state.formError &&
                      this.state.formError.element === "claim_file"
                        ? "border-red-500"
                        : ""
                    } border-2 ${
                      this.state.thisClaimType &&
                      !this.state.chooseClaimFile &&
                      this.state.formType !== "PROPS"
                        ? "cursor-pointer"
                        : !this.state.chooseClaimFile && "cursor-not-allowed"
                    }`}
                    title={
                      !this.state.thisClaimType
                        ? "Choose claim type first!"
                        : ""
                    }
                    onClick={() => {
                      this.state.formType === "DEFAULT" &&
                        this.state.thisClaimType &&
                        !this.state.chooseClaimFile &&
                        this.setChooseClaimFile(true);
                      this.state.formError &&
                        this.state.formError.element === "claim_file" &&
                        this.setFormError(null);
                    }}
                  >
                    {/* DISPLAYING FILTERED SELECTING FILE ============================ */}
                    {this.state.claimFileId.length > 0 &&
                    !this.state.chooseClaimFile ? (
                      <p className="my-1 mx-2">
                        {this.state.thisClaimType ===
                          PlansTypeKeys.DOCUMENT_VALIDATION &&
                          this.props.claiming.candidateDocuments &&
                          ClaimGetDocTitle(
                            this.props.documentReducer,
                            this.getDocumentById(
                              this.props.claiming.candidateDocuments,
                              this.state.claimFileId
                            )
                          ).main}
                        {this.state.thisClaimType ===
                          PlansTypeKeys.APPLICATION &&
                          this.props.candidateApplications
                            .candidate_applications &&
                          this.getApplicationById(
                            this.props.candidateApplications
                              .candidate_applications,
                            this.state.claimFileId
                          ) &&
                          this.getApplicationById(
                            this.props.candidateApplications
                              .candidate_applications,
                            this.state.claimFileId
                          )!.position.position_title}
                      </p>
                    ) : (
                      !this.state.chooseClaimFile && (
                        <p className="my-1 mx-2">
                          Select Claim{" "}
                          {this.state.thisClaimType &&
                            this.state.thisClaimType
                              .toLocaleLowerCase()
                              .replace(/_/g, " ")}
                        </p>
                      )
                    )}
                    {/* END DISPLAYING FILTERED SELECTING FILE ============================ */}

                    {/* LISTING OF REJECTED DOCUMENTS ============================ */}
                    {this.state.chooseClaimFile &&
                      this.state.thisClaimType ===
                        PlansTypeKeys.DOCUMENT_VALIDATION &&
                      this.props.claiming.candidateDocuments &&
                      this.props.claiming.myClaims &&
                      this.props.auth.application_session && (
                        <FormDocumentList
                          setChooseClaimFile={this.setChooseClaimFile}
                          setClaimFileId={this.setClaimFileId}
                          setViewThisFile={this.setViewThisFile}
                          claimFileId={this.state.claimFileId}
                          viewThisFile={this.state.viewThisFile}
                          candidateDocuments={
                            this.props.claiming.candidateDocuments
                          }
                          documentCategories={this.props.documentReducer}
                          myClaims={this.props.claiming.myClaims}
                          activeSession={this.props.auth.application_session}
                        />
                      )}
                    {/* END LISTING OF REJECTED DOCUMENTS ============================ */}
                    {/* LISTING OF REJECTED APPLICATION ============================ */}
                    {this.state.chooseClaimFile &&
                      this.state.thisClaimType === PlansTypeKeys.APPLICATION &&
                      this.props.claiming.candidateApplications &&
                      this.props.claiming.myClaims &&
                      this.props.auth.application_session && (
                        <FormApplicationList
                          setChooseClaimFile={this.setChooseClaimFile}
                          setClaimFileId={this.setClaimFileId}
                          setViewThisFile={this.setViewThisFile}
                          claimFileId={this.state.claimFileId}
                          viewThisFile={this.state.viewThisFile}
                          candidateApplications={
                            this.props.claiming.candidateApplications
                          }
                          myClaims={this.props.claiming.myClaims}
                          activeSession={this.props.auth.application_session}
                        />
                      )}
                    {/* END LISTING OF REJECTED APPLICATION ============================ */}
                  </div>
                  {this.state.formError &&
                    this.state.formError.element === "claim_file" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END SELECT FILE TO CLAIM ON ============================================== */}

                {/* CLAIM REASONS ========================================== */}
                <div className="flex flex-col mb-2">
                  <label>Choose reason of your claim</label>
                  <div
                    className={`text-left bg-white border-2 border-gray-700 rounded w-full ${
                      this.state.chooseClaimReason ? "h-64" : "h-auto"
                    } ${
                      this.state.formError &&
                      this.state.formError.element === "claim_reason"
                        ? "border-red-500"
                        : ""
                    } border-2 ${
                      this.props.claiming.claimReasons === null
                        ? "cursor-wait"
                        : "cursor-pointer"
                    }`}
                    title="Choose the reason for your claim!"
                    onClick={() => {
                      !this.state.chooseClaimReason &&
                        this.setState({ chooseClaimReason: true });
                      this.state.formError &&
                        this.state.formError.element === "claim_reason" &&
                        this.setFormError(null);
                    }}
                  >
                    {/* DISPLAYING SELECTED REASON ============================ */}
                    {this.state.claimReason && !this.state.chooseClaimReason ? (
                      <p className="my-1 mx-2">
                        {this.state.claimReason.reason}
                      </p>
                    ) : (
                      !this.state.chooseClaimReason && (
                        <p className="my-1 mx-2">Select reason</p>
                      )
                    )}
                    {/* END DISPLAYING SELECTED REASON ============================ */}

                    {/* LISTING OF CLAIM REASONS ============================ */}
                    {this.state.chooseClaimReason &&
                      this.props.claiming.claimReasons && (
                        <ChooseReason
                          setChooseClaimReason={this.setChooseClaimReason}
                          setClaimReason={this.setClaimReason}
                          claimReason={this.state.claimReason}
                          allClaimReasons={this.props.claiming.claimReasons}
                        />
                      )}
                    {/* END LISTING OF CLAIM REASONS ============================ */}
                  </div>
                  {this.state.formError &&
                    this.state.formError.element === "claim_reason" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END CLAIM REASONS ========================================== */}

                {/* CLAIM COMMENT ============================================== */}
                <div className="flex flex-col">
                  <label>Comment</label>
                  <textarea
                    className={`text-left input-white w-20 inline-block ${
                      this.state.formError &&
                      this.state.formError.element === "claim_comment"
                        ? "border-red-500"
                        : "border-gray-500"
                    } border-2`}
                    placeholder={`Claim comment`}
                    style={{ resize: "none" }}
                    value={this.state.claimComment}
                    onChange={(e) => {
                      this.setClaimComment(e.target.value);
                      this.state.formError &&
                        this.state.formError.element === "claim_comment" &&
                        this.setFormError(null);
                    }}
                    rows={3}
                    disabled={this.state.loadingForm}
                  ></textarea>
                  {this.state.formError &&
                    this.state.formError.element === "claim_comment" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END CLAIM COMMENT ============================================== */}
              </div>
              <div className="pt-3 flex justify-between">
                <button
                  onClick={() => this.cleanToClose()}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
                    this.state.loadingForm && "cursor-not-allowed"
                  }`}
                  type="button"
                  disabled={this.state.loadingForm}
                >
                  Exit
                </button>
                <button
                  className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                    this.state.loadingForm && "cursor-wait"
                  }`}
                  type="submit"
                  disabled={this.state.loadingForm}
                >
                  {this.state.loadingForm ? "Loading..." : "Claim"}
                </button>
              </div>
            </div>
          </form>
        )}
        {/* END CLAIM FORM ============================================== */}

        {/* CLAIM DOCUMENT VIEW ============================================== */}
        {this.state.viewThisFile.length > 0 &&
          this.props.claiming.candidateDocuments &&
          this.state.thisClaimType === PlansTypeKeys.DOCUMENT_VALIDATION && (
            <DocFormView
              thisDoc={this.getDocumentById(
                this.props.claiming.candidateDocuments,
                this.state.viewThisFile
              )}
              setViewThisFile={this.setViewThisFile}
              documentCategories={this.props.documentReducer}
              setChooseClaimFile={this.setChooseClaimFile}
              setClaimFileId={this.setClaimFileId}
            />
          )}
        {/* END CLAIM DOCUMENT VIEW ============================================== */}
        {/* CLAIM APPLICATION VIEW ============================================== */}
        {this.state.viewThisFile.length > 0 &&
          this.props.candidateApplications.candidate_applications &&
          this.getApplicationById(
            this.props.candidateApplications.candidate_applications,
            this.state.viewThisFile
          ) &&
          this.state.thisClaimType === PlansTypeKeys.APPLICATION && (
            <ApplicationFormView
              thisApplication={
                this.getApplicationById(
                  this.props.candidateApplications.candidate_applications,
                  this.state.viewThisFile
                )!
              }
              documentCategories={this.props.documentReducer}
              setChooseClaimFile={this.setChooseClaimFile}
              setClaimFileId={this.setClaimFileId}
              setViewThisFile={this.setViewThisFile}
            />
          )}
        {/* END CLAIM APPLICATION VIEW ============================================== */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
  claiming,
  candidateApplications,
  candidateExam,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  claiming: ClaimingReducerType;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidateExam: candidateExamStoreInterface;
} => {
  return {
    auth: auth,
    documentReducer: document,
    claiming,
    candidateApplications: candidateApplications,
    candidateExam: candidateExam,
  };
};

export default connect(mapStateToProps, {
  getMyClaims,
  getMyAnswers,
  getClaimReasons,
  createClaim,
  GetClaimingTypes,
  getMyRejectedApplications,
  getMyRejectedDocuments,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  PushPopupAlert,
})(ClaimFormComponent);
