import {
  QuestionForEmbedded,
  QuestionForHybrid,
  questionShortElementsType,
  questionTypeElementsType,
} from "@mastrecruit/common";
import React from "react";
import { Auth } from "../../../actions/auth";
import {
  candidateExamStoreInterface,
  QuestionAnswersStore,
} from "../../../actions/candidate_exam";
import { questionTypesStoreInterface } from "../../../actions/questionTypes";
import { System } from "../../../actions/system";
import Loading from "../../../components/Loading/Loading";
import { EmbeddedQuestion } from "../../../components/QuestionPreview/QuestionPreview";
import { course } from "../../../interfaces/course";
import {
  QuestionAnswerInterface,
  QuestionInterface,
  QuestionLanguage,
} from "../../../interfaces/question";
import { findQuestionType } from "../utils/utils";
import FindAnswer from "./FindAnswer";

interface QuestionsReviewProps {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  submitQuestionAnswer: boolean;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  submitExam: () => void;
  selectedExam: course | null;
  setSubmitModal: (status: boolean) => void;
  selectedLanguage: QuestionLanguage;
  wholeQuestion: QuestionInterface;
  answersList: QuestionAnswersStore[];
}

export const findQType = (
  typeShort: questionShortElementsType | QuestionForEmbedded | QuestionForHybrid
): questionTypeElementsType => {
  let changed: questionTypeElementsType = questionTypeElementsType.Hybrid;
  switch (typeShort) {
    case QuestionForHybrid.Calculated:
      changed = questionTypeElementsType.Calculated;
      break;
    case QuestionForHybrid.CalculatedMultipleChoice:
      changed = questionTypeElementsType.CalculatedMultipleChoice;
      break;
    case QuestionForHybrid.Essay:
      changed = questionTypeElementsType.Essay;
      break;
    case QuestionForHybrid.Matching:
      changed = questionTypeElementsType.Matching;
      break;
    case QuestionForHybrid.MultipleChoice:
      changed = questionTypeElementsType.MultipleChoice;
      break;
    case QuestionForHybrid.Numeric:
      changed = questionTypeElementsType.Numeric;
      break;
    case QuestionForHybrid.ShortAnswer:
      changed = questionTypeElementsType.ShortAnswer;
      break;
    case QuestionForHybrid.SimpleCalculate:
      changed = questionTypeElementsType.SimpleCalculate;
      break;
    case QuestionForHybrid.SingleMultiple:
      changed = questionTypeElementsType.SingleMultiple;
      break;
    case QuestionForHybrid.TrueFalse:
      changed = questionTypeElementsType.TrueFalse;
      break;
  }
  return changed;
};

const FindAnswerHybrid = (props: QuestionsReviewProps) => {
  let str = "a";
  if (props.submitQuestionAnswer === true) {
    return (
      <div className="bg-white rounded mx-4 my-4">
        <div className="text-gray-600 mx-4">Saving changes, Please wait...</div>
        <Loading />
      </div>
    );
  }
  return (
    <div className={`pt-4 border-t border-gray-300 bg-white`}>
      <div className="px-4 pb-2">
        <div className="my-2">
          <div
            className="flex flex-col gap-3 overflow-y-auto"
            style={{ maxHeight: "100%" }}
          >
            {console.log(
              "Whole: ",
              props.wholeQuestion,
              props.selectedLanguage
            )}
            <div>
              {findQuestionType(
                props.wholeQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Hybrid &&
                props.wholeQuestion.question.find(
                  (test) => test.language === props.selectedLanguage
                ) !== undefined &&
                props.wholeQuestion.question
                  .find((test) => test.language === props.selectedLanguage)!
                  .sub_question.map((item, i) => {
                    let stri = String.fromCharCode(
                      str.charCodeAt(str.length - 1) + i
                    );
                    return (
                      <div
                        key={i + 1}
                        className="bg-white hover:bg-white border border-white hover:border-blue-400 px-2 py-2 rounded  animate__animated animate__fadeIn animate__fast group"
                      >
                        <div className="grid md:grid-cols-12 w-full">
                          <div className="col-span-12 flex flex-row items-center gap-3">
                            <div
                              key={i + 1}
                              className="w-6 h-6 p-2 rounded-full bg-blue-100 flex justify-center items-center font-bold text-lg text-blue-600 cursor-pointer"
                            >
                              {stri}
                            </div>
                            <div className="text-sm w-full">
                              {props.wholeQuestion.question.find(
                                (test) =>
                                  test.language === props.selectedLanguage
                              ) !== undefined ? (
                                findQuestionType(
                                  item.type,
                                  props.questionTypes.questionTypes
                                ) === questionTypeElementsType.Embedded ? (
                                  EmbeddedQuestion(
                                    props.wholeQuestion!,
                                    props.questionTypes.questionTypes,
                                    props.selectedLanguage,
                                    props.candidateExam.question_answers,
                                    (data: QuestionAnswerInterface) => {}
                                  )
                                ) : (
                                  <div
                                    className="font-bold text-gray-700 text-base"
                                    dangerouslySetInnerHTML={{
                                      __html: item.description,
                                    }}
                                  ></div>
                                )
                              ) : (
                                props.wholeQuestion.question.length > 0 && (
                                  <div
                                    className="font-bold text-gray-700 text-base max-h-20 truncate"
                                    dangerouslySetInnerHTML={{
                                      __html: item.description,
                                    }}
                                  ></div>
                                )
                              )}
                              <div className="text-blue-700 font-bold text-sm w-full">
                                Answer: &nbsp;
                                {props.questionTypes.questionTypes !== null && (
                                  <FindAnswer
                                    selectedLanguage={props.selectedLanguage}
                                    question={props.wholeQuestion}
                                    answersList={props.answersList}
                                    questionTypes={
                                      props.questionTypes.questionTypes
                                    }
                                    onClick={() => {}}
                                    showCorrectAnswer={false}
                                    subQuestionType={findQType(item.typeShort)}
                                    reference={item.reference}
                                  />
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FindAnswerHybrid;
