import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { WaitingListInterface } from "../interfaces/waitingList";
import { LocationAPI } from ".";

/**
 * * ****************************** INTERFACES *****************************
 */

// GET WAITING LIST
export interface GetWaitingListAction {
  type: ActionTypes.GET_WAITING_LIST;
  payload: WaitingListInterface[];
}

export type WaitingListActionType = GetWaitingListAction;

/**
 * * ****************************** ACTIONS *****************************
 */

// GET WAITING LIST
export const getWaitingList = (callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<WaitingListInterface[]>(
        `${API.PLACEMENT}/waitinglist`
      );
      console.log({ getWaitingList: res.data });

      dispatch<GetWaitingListAction>({
        type: ActionTypes.GET_WAITING_LIST,
        payload: res.data,
      });
      callback(false, "", res.data);
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};
