import React from "react";
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
} from "../../actions";
import { StoreState } from "../../reducers";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import ConfirmationCode from "../../components/ConfirmationCode/ConfirmationCode";
import Spinner from "../../components/Loading/Spinner";
import { MdClose } from "react-icons/md";

// props for the component
interface AppProps {
  auth: Auth;
  fetchAuth: Function;
  FC_GetTwoFactorAuth: Function;
  FC_ChangeTwoFactorType: typeof FC_ChangeTwoFactorType;
  FC_Login: Function;
  FC_LoginCheckCode: Function;
  close: () => void;
  openRegister: () => void;
  openForgetPassword: () => void;
}

interface AppState {
  email: string;
  password: string;
  loading: boolean;
  res: any;
  error: {
    target: string;
    msg: string;
  };
  login_pass: boolean;
  code_loading: boolean;
  redirect: boolean;
  loading_2_auth: boolean;
}

class _Login extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_2_auth: false,
      email: "",
      password: "",
      res: null,
      error: { target: "", msg: "" },
      login_pass: false,
      code_loading: false,
      redirect: false,
    };
  }

  componentDidMount = () => {
    if (this.props.auth.two_factor_auth.loaded === false) {
      this.setState({
        loading_2_auth: true,
      });
      this.props.FC_GetTwoFactorAuth((status: string) => {
        this.setState({
          loading_2_auth: false,
        });
      });
    }
  };

  loginHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    // check for errors
    if (this.state.email.length <= 4) {
      return this.setState({
        error: {
          target: "email",
          msg: "Please enter an email or phone number",
        },
      });
    }

    // check for errors
    if (this.state.password.length <= 4) {
      return this.setState({
        error: {
          target: "password",
          msg: "Please enter your password",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: "",
        msg: "",
      },
      login_pass: false,
    });
    // * Login API
    this.props.FC_Login(
      {
        email: this.state.email,
        password: this.state.password,
        verify_type: this.props.auth.two_factor_auth.type,
        two_step_active: this.props.auth.two_factor_auth.active,
      },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
            error: {
              target: "",
              msg: "",
            },
            login_pass: true,
          });
        } else {
          this.setState({
            loading: false,
            error: {
              target: "all",
              msg: msg,
            },
            login_pass: false,
          });
          this.clearError();
        }
      }
    );
  };

  clearError = () =>
    setTimeout(() => {
      this.setState({
        error: {
          msg: "",
          target: "",
        },
      });
    }, 3000);

  handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    this.setState({
      ...this.state,
      error: {
        target: "",
        msg: "",
      },
      [e.currentTarget.name]: e.target.value,
    } as Pick<AppState, keyof AppState>);
  };

  changeAuthType = (e: React.ChangeEvent<HTMLSelectElement>) => {
    this.props.FC_ChangeTwoFactorType(e.target.value);
  };

  // check code
  checkCodeHandler = (verr_code: number) => {
    this.setState({
      code_loading: true,
    });

    this.props.FC_LoginCheckCode(
      { email: this.props.auth.user.email, verification_code: verr_code },
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            redirect: true,
          });
        } else {
          this.setState({
            error: {
              target: "all",
              msg: msg,
            },
            code_loading: false,
          });
        }
      }
    );
  };

  render() {
    if (
      this.state.redirect === true ||
      this.props.auth.isAuthenticated === true
    ) {
      return null;
    }

    // check if the 2 step auth is loading
    // if (this.state.loading_2_auth === true) {
    //   return (
    //     <div className="mt-16">
    //       <Spinner />
    //     </div>
    //   );
    // }
    return (
      <>
        {/* <div
          className="fixed animate__animated animate__fadeIn animate__slower top-0 bottom-0 right-0 left-0  z-50 bg-black bg-opacity-20"
          onClick={this.props.close}
        ></div> */}
        <div
          className={` animate__animated animate__zoomIn animate__faster fixed top-0 right-0 m-auto pt-20 z-lg xs:mx-6 left-0 w-96 mx-auto `}
        >
          {this.state.login_pass === true ? (
            <div
              className={`bg-white border border-gray-200 shadow-xl p-1 rounded mt-6  ${
                this.state.error.target === "all"
                  ? "animate__animated  animate__shakeX"
                  : ""
              }`}
            >
              {this.state.error.target === "all" && (
                <div className="mx-auto w-full">
                  <Alert
                    title={this.state.error.msg}
                    theme={AlertInterface.danger}
                  />
                </div>
              )}
              <ConfirmationCode
                user={this.props.auth.user}
                code_loading={this.state.code_loading}
                submitCode={this.checkCodeHandler}
              />
            </div>
          ) : (
            <div
              className={` bg-white border border-gray-200 shadow-xl mx-auto px-4 py-5 rounded  mt-6 sm:mx-auto   ${
                this.state.error.target === "all"
                  ? "animate__animated  animate__shakeX"
                  : ""
              }`}
            >
              {this.state.loading_2_auth === true ? (
                <div className="">
                  <h1 className="px-2 font-extrabold text-4xl text-blue-500  pt-2 pb-4">
                    Login
                  </h1>
                  <div className="pt-4 pb-8">
                    <Spinner />
                  </div>
                </div>
              ) : (
                <form onSubmit={this.loginHandler}>
                  <div className="flex items-center">
                    <h1 className="px-2 flex-1 text-4xl text-blue-500  pt-2 pb-4">
                      Login
                    </h1>
                    <button
                      type="button"
                      onClick={this.props.close}
                      className=" bg-red-200 text-red-800 p-1 rounded-full text-xl"
                    >
                      <MdClose />
                    </button>
                  </div>

                  {this.state.error.target === "all" && (
                    <div className="p-2">
                      <Alert
                        title={this.state.error.msg}
                        theme={AlertInterface.danger}
                      />
                    </div>
                  )}

                  <div className="p-2">
                    <label htmlFor="email" className="d-block">
                      Email/Phone number
                    </label>
                    <input
                      type="text"
                      name="email"
                      onChange={this.handleChange}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === "email"
                          ? "border-red-500 border-2"
                          : "input-border"
                      }`}
                      placeholder="Enter email or phone number"
                      value={this.state.email}
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === "email" && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>

                  <div className="p-2">
                    <label htmlFor="username" className="d-block">
                      Password
                    </label>
                    <input
                      type="password"
                      name="password"
                      onChange={this.handleChange}
                      className={`input-white w-20 inline-block  ${
                        this.state.error?.target === "password"
                          ? "border-red-500 border-2"
                          : "input-border"
                      }`}
                      placeholder="Enter password"
                      value={this.state.password}
                      disabled={this.state.loading}
                    />
                    {this.state.error?.target === "password" && (
                      <span className="text-red-600">
                        {this.state.error?.msg}
                      </span>
                    )}
                  </div>

                  {this.props.auth.two_factor_auth.active === true && (
                    <div className="p-1 border-b mt-2 border-gray-2 pb-2 my-1 mx-3">
                      <h3 className="text-lg font-bold m-0 text-gray-500">
                        Two step auth is active
                      </h3>
                      <section className="text-sm">
                        <p className="text-gray-400 inline-block">
                          Send me the code on{" "}
                        </p>
                        <select
                          className="inline-block ml-2 bg-blue-200 rounded text-blue-800 font-bold"
                          name="auth_type"
                          onChange={this.changeAuthType}
                        >
                          <option value="sms">SMS</option>
                          <option value="email">Email</option>
                        </select>
                      </section>
                    </div>
                  )}

                  <div className="p-2">
                    <div className="flex mt-2">
                      <div className="flex-1 ">
                        <p>
                          If you don't have an account, Please{" "}
                          <button
                            type="button"
                            className="font-bold inline-block text-blue-600 hover:underline"
                            onClick={this.props.openRegister}
                          >
                            Register
                          </button>
                        </p>
                      </div>

                      <div className="  text-right">
                        <button
                          className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                          disabled={
                            this.state.loading || this.state.loading_2_auth
                          }
                          type="submit"
                        >
                          {this.state.loading === true ? (
                            <b className="animate__animated animate__fadeIn animate__infinite">
                              Loading...
                            </b>
                          ) : (
                            "Login"
                          )}
                        </button>
                      </div>
                    </div>

                    <div className="mt-7 text-center">
                      <button
                        type="button"
                        onClick={this.props.openForgetPassword}
                        className="text-gray-500 hover:text-blue-600 hover:underline"
                      >
                        Forget password
                      </button>
                    </div>
                  </div>
                </form>
              )}
            </div>
          )}
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  todos,
  auth,
}: StoreState): { todos: Todo[]; auth: Auth } => {
  return { todos: todos, auth: auth };
};

export const Login = connect(mapStateToProps, {
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
})(_Login);
