import moment from "moment";
import React, { useState } from "react";
import {
  compareWithToday,
  ExamDoneDetailsAnswers,
} from "../../containers/CandidateExams/CandidateExams";
import { PlansTypeKeys } from "../../interfaces";
import { TimeZone } from "../../utils/app.config";
import { DATE } from "../../utils/functions";
import ClaimFormComponent from "../ClaimForm/ClaimFormComponent";
import { AnswersDetails } from "./AnswersDetails";

interface CandidateAnswersDetailsProps {
  exam_id: ExamDoneDetailsAnswers;
  showAnswers: boolean;
}

const CandidateAnswersDetails = (props: CandidateAnswersDetailsProps) => {
  const today = moment().tz(TimeZone);
  let tomorrow = new Date();
  tomorrow.setDate(today.date() + 1);
  const [claim, setClaim] = useState<boolean>(false);
  return (
    <div>
      {/* Check if period is open for claims */}
      {compareWithToday(today, props.exam_id.exam_date) === false ? (
        <div className="bg-yellow-100 text-yellow-800 font-extrabold text-center flex flex-col items-center justify-center text-lg px-2 py-6">
          <div className="mb-5 text-3xl">
            Please wait, the exam details will be visible soon!
          </div>
          <div className="text-gray-600">Exam details will be visible from</div>
          <div className="bg-white rounded-full px-3 py-1 text-gray-700">
            {DATE(tomorrow.toString())}
          </div>
        </div>
      ) : claim === false ? (
        <div className="mb-20">
          <AnswersDetails
            exam_id={props.exam_id}
            showAnswers={props.showAnswers}
          />
          {/* <div
            onClick={() => setClaim(true)}
            className="bg-blue-100 flex flex-row items-center px-3 py-2 -mt-10"
          >
            <div className="btn bg-green-600 text-white text-center w-max px-6 text-2xl flex flex-row item-center justify-center gap-3 cursor-pointer">
              <IoHandLeftSharp className="text-3xl " />{" "}
              <span>Exam marks Claim</span>
            </div>
          </div> */}
        </div>
      ) : (
        <div className="bg-gray-100 mt-4 pt-2 rounded">
          <ClaimFormComponent
            setFormState={setClaim}
            claim_type={PlansTypeKeys.EXAMINATION}
            file_id={props.exam_id.exam_id}
          />
        </div>
      )}
    </div>
  );
};

export default CandidateAnswersDetails;
