import React from "react";
import { IoArrowBackOutline } from "react-icons/io5";
import { RiListSettingsLine } from "react-icons/ri";
import { Link } from "react-router-dom";
import {
  ApplicationStates,
  GetApplicationsAPI,
} from "../../../interfaces/application";
import { DATE } from "../../../utils/functions";

interface ApplicationsListProps {
  list: GetApplicationsAPI[];
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
}

const ApplicationsList = (props: ApplicationsListProps) => {
  return (
    <div className="pr-2 -ml-2 lg:ml-0">
      <div className="bg-gray-200 rounded py-4 mt-2">
        <div className="flex flex-row items-center gap-2">
          <div>
            <Link
              to="/apps-page"
              title="Back to question bank"
              className="bg-white rounded-full h-10 w-10 flex items-center justify-center text-blue-600 hover:bg-blue-600 hover:text-white"
            >
              <IoArrowBackOutline className=" text-4xl" />
            </Link>
          </div>
          <div className="flex flex-col">
            <span className="text-xl md:text-3xl font-extrabold text-blue-600">
              Placement Agreements
            </span>
            <span className="text-gray-600">
              Choose application you want to modify placement proposed locations
            </span>
          </div>
        </div>
      </div>
      {props.list.filter(
        (itm) =>
          itm.application_status === ApplicationStates.Shortlisted ||
          itm.application_status === ApplicationStates.Pass
      ).length === 0 ? (
        <div className="text-4xl text-gray-400 font-extrabold text-center py-6 px-4 bg-gray-50 mt-2">
          No applications available
        </div>
      ) : (
        props.list
          .filter(
            (itm) =>
              itm.application_status === ApplicationStates.Shortlisted ||
              itm.application_status === ApplicationStates.Pass
          )
          .map((item, i) => (
            <div
              key={i + 1}
              onClick={() => props.SelectUserApplication(item)}
              className="group bg-white hover:bg-blue-50 rounded-md px-4 py-2 mt-2 flex flex-row items-center gap-3 border-2 border-white hover:border-blue-600 cursor-pointer"
            >
              <RiListSettingsLine className="text-5xl text-gray-400 group-hover:text-blue-600" />
              <div className="flex flex-col">
                <span className="text-base group-hover:text-blue-600 font-extrabold">
                  {item.position.position_title}
                </span>
                <span>
                  <div className="flex flex-col text-xs text-gray-600 mt-1">
                    <div className="flex flex-row gap-2">
                      <span
                        className={`${
                          item.application_status !==
                          ApplicationStates.Initiated
                            ? item.application_status ===
                              ApplicationStates.Rejected
                              ? "text-gray-500"
                              : item.application_status ===
                                ApplicationStates.Shortlisted
                              ? "text-gray-500"
                              : "text-gray-500"
                            : "text-gray-500"
                        }`}
                      >
                        Date:
                      </span>
                      <span
                        className={`${
                          item.application_status !==
                          ApplicationStates.Initiated
                            ? item.application_status ===
                              ApplicationStates.Rejected
                              ? "text-gray-800"
                              : item.application_status ===
                                ApplicationStates.Shortlisted
                              ? "text-gray-800"
                              : "text-gray-800"
                            : "text-black"
                        } font-bold`}
                      >
                        {DATE(item.createdAt, "DD/MM/YYYY")}
                      </span>
                    </div>
                    <div className="flex flex-row gap-2">
                      <span
                        className={`${
                          item.application_status !==
                          ApplicationStates.Initiated
                            ? item.application_status ===
                              ApplicationStates.Rejected
                              ? "text-gray-500"
                              : item.application_status ===
                                ApplicationStates.Shortlisted
                              ? "text-gray-500"
                              : "text-gray-500"
                            : "text-gray-500"
                        }`}
                      >
                        Application period:
                      </span>
                      <span
                        className={`${
                          item.application_status !==
                          ApplicationStates.Initiated
                            ? item.application_status ===
                              ApplicationStates.Rejected
                              ? "text-gray-800"
                              : item.application_status ===
                                ApplicationStates.Shortlisted
                              ? "text-gray-800"
                              : "text-gray-800"
                            : "text-black"
                        } font-bold`}
                      >
                        {item.application_session &&
                          item.application_session.session_plan_name}{" "}
                        -{" "}
                        {item.application_session &&
                          item.application_session.period}
                      </span>
                    </div>
                  </div>
                </span>
              </div>
            </div>
          ))
      )}
    </div>
  );
};

export default ApplicationsList;
