import axios from "axios";
import React, { Component } from "react";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  candidateApplyStoreInterface,
  schoolLevelsStoreInterface,
} from "../../../../actions";
import { PopupAlertData } from "../../../../components/Alert/Popup";
import { GetApplicationsAPI } from "../../../../interfaces/application";
import { PopupAlertTheme } from "../../../../interfaces/popupAlert";
import { API } from "../../../../utils/api";
import { PlacementContract } from "../../../PlacementContract/PlacementContract";
import { ApplicationStepsTypes } from "../CandidateApplicationSteps/CandidateApplicationSteps";
import Congratulation from "./Congratulation";

interface AgreementProps {
  auth: Auth;
  candidateApplications: CandidateApplicationsStoreInterface;
  candidateApply: candidateApplyStoreInterface;
  SelectUserApplication: (application: GetApplicationsAPI | null) => void;
  setCandidateApplicationStep: (step: ApplicationStepsTypes) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  RemoveUserApplication: (id: string) => void;
  setApplyCompletionStatus: (status: boolean) => void;
  cleanCandidateApply: () => void;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  schoolLevels: schoolLevelsStoreInterface;
  requestedApplications: GetApplicationsAPI[] | null;
  addCandidateApplications: (applications: GetApplicationsAPI[]) => void;
}

interface AgreementState {
  loading: boolean;
  changed: boolean;
}

class Agreement extends Component<AgreementProps, AgreementState> {
  constructor(props: AgreementProps) {
    super(props);

    this.state = {
      loading: false,
      changed: false,
    };
  }

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  deleteApplication = async () => {
    let test = this.props.candidateApplications.candidate_applications?.find(
      (itm) =>
        this.props.schoolLevels.selectedEducationCategory !== null &&
        itm.user_id === this.props.auth.user.user_id &&
        itm.position.position_id ===
          this.props.candidateApply.selectedPosition?.position_id &&
        itm.application_session._id ===
          this.props.schoolLevels.selectedEducationCategory.session_plan?._id
    );
    if (test !== undefined) {
      this.setLoading(true);
      try {
        await axios.delete(`${API.APPLICATION}/apply/${test.application_id}`);
        this.props.RemoveUserApplication(test.application_id);
        this.props.SelectUserApplication(null);
        this.setLoading(false);
        this.props.PushPopupAlert({
          title:
            this.props.candidateApply.currentStep !==
            ApplicationStepsTypes.Step2
              ? `Application process canceled successfully!`
              : `Now you are on step 2`,
          description:
            this.props.candidateApply.currentStep !==
            ApplicationStepsTypes.Step2
              ? "Now you can choose another position from the list"
              : `Now you can continue to modify your application`,
          theme: PopupAlertTheme.info,
        });
      } catch (error: any) {
        this.setLoading(false);
        this.props.PushPopupAlert({
          title: `Failed to cancel application process!`,
          theme: PopupAlertTheme.warning,
        });
      }
    }
  };

  componentDidMount = () => {
    let selectedApplication =
      this.props.candidateApplications.candidate_applications?.find(
        (itm) =>
          itm.position.position_id ===
            this.props.candidateApply.selectedPosition?.position_id &&
          itm.application_session._id ===
            this.props.schoolLevels.selectedEducationCategory?.session_plan?._id
      );
    console.log(
      "requestedApplications appl: ",
      this.props.requestedApplications
    );
    console.log("Selected appl: ", selectedApplication);
    if (selectedApplication === undefined) {
      if (this.props.requestedApplications !== null) {
        // Add requested applications
        this.props.addCandidateApplications(this.props.requestedApplications);
      } else {
        this.setLoading(true);
        // Load new applications
        this.props.LoadCandidateApplications(
          this.props.auth.user.user_id,
          (status: boolean) => {
            this.setLoading(status);
            if (status === false) {
            }
          }
        );
      }
    }
    if (selectedApplication !== undefined) {
      this.props.SelectUserApplication(selectedApplication);
    }
    if (this.props.candidateApplications.selected_application !== null) {
      this.setState({ changed: true });
    }
  };
  render() {
    if (this.props.candidateApply.completed === true) {
      let selectedApplication =
        this.props.candidateApplications.candidate_applications?.find(
          (itm) =>
            itm.position.position_id ===
              this.props.candidateApply.selectedPosition?.position_id &&
            itm.application_session._id ===
              this.props.schoolLevels.selectedEducationCategory?.session_plan
                ?._id
        );
      return (
        <Congratulation
          auth={this.props.auth}
          candidateApply={this.props.candidateApply}
          cleanCandidateApply={this.props.cleanCandidateApply}
          selectedApplication={selectedApplication}
        />
      );
    }
    return (
      <div className="max-w-7xl mx-auto px-2 md:px-12 lg:px-24 mt-5 bg-white">
        <div className="">
          {this.state.loading === false &&
            this.props.candidateApplications.selected_application !== null && (
              <PlacementContract
                SubmitApplication={() => {
                  this.props.setApplyCompletionStatus(true);
                }}
                applicationGoBack={() => {
                  // if (loading === false) {
                  this.deleteApplication(); //add loading
                  this.props.setCandidateApplicationStep(
                    ApplicationStepsTypes.Step2
                  );
                  // }
                }}
              />
            )}
        </div>
      </div>
    );
  }
}

export default Agreement;
