import React from "react";
import { IoCheckmarkCircleSharp } from "react-icons/io5";
import { RiErrorWarningLine } from "react-icons/ri";
import { Auth } from "../../../../actions";
import {
  PositionAPI,
  PositionRequirements,
  PositionSubcategoryData,
} from "../../../../interfaces";

interface SuccessFileProps {
  item: PositionRequirements;
  data: PositionSubcategoryData | null;
  currentPosition: PositionAPI | null;
  auth: Auth;
  validateRequirementItem: (
    item: PositionRequirements,
    itm: PositionSubcategoryData | null
  ) => boolean;
  validateRequirementItemOptional: (
    item: PositionRequirements,
    itm: PositionSubcategoryData | null
  ) => boolean;
  addFile: () => void;
}

const OtherDocs = (props: SuccessFileProps) => {
  const data = null;
  const colorName =
    props.validateRequirementItem(props.item, data) === true &&
    props.validateRequirementItemOptional(props.item, data) === false &&
    props.item.required === false
      ? "-gray-300"
      : "-green-500";
  return (
    <div className="col-span-1">
      <div className="absolute bg-transparent pl-3">
        <div
          className={`h-28 w-24 bg-white border-2 border${colorName} p-2 pr-3 rounded-md`}
        >
          <div className={`w-full h-2 bg${colorName} rounded-full mb-3`}></div>
          <div
            className={`h-2 bg${colorName} rounded-full mb-3 mr-5`}
            style={{ width: "90%" }}
          ></div>
          <div
            className={`h-2 bg${colorName} rounded-full mb-3 mr-10`}
            style={{ width: "70%" }}
          ></div>
          <div className="flex items-center justify-end w-full -mr-3">
            <div>
              {props.validateRequirementItem(props.item, props.data) === true &&
              props.validateRequirementItemOptional(props.item, props.data) ===
                false &&
              props.item.required === false ? (
                <RiErrorWarningLine className="text-5xl text-gray-300 -mr-2 -mt-3" />
              ) : (
                <IoCheckmarkCircleSharp
                  className={`text-5xl text${colorName} -mr-2 -mt-3`}
                />
              )}
            </div>
          </div>
        </div>
      </div>
      <div
        className={`bg-gray-200 rounded-lg flex flex-row gap-28 mt-8 border`}
        style={{ minHeight: "90px" }}
      >
        <div className="w-0"></div>
        <div
          className="pl-2 pr-2 text-sm flex flex-col justify-center pb-3"
          style={{ height: "auto" }}
        >
          <div className="text-base leading-tight mt-2 mb-1 font-normal">
            <span className="text-sm">
              {props.data === null || props.data?.subcategory_id === null
                ? props.item.subcategory.title
                : props.currentPosition?.requirements.find(
                    (it) =>
                      props.data !== null &&
                      it.subcategory.user_detail_subcategory_id ===
                        props.data.subcategory_id
                  )?.subcategory.title}
            </span>
          </div>
          <div>
            {props.validateRequirementItem(props.item, data) === false ? (
              props.auth.user.user_id ? (
                <div className="bg-green-100 text-green-900 rounded-full px-1 text-xs w-max border border-green-300">
                  Not found!
                </div>
              ) : (
                <div className="bg-gray-100 text-gray-900 rounded-full px-1 text-xs w-max border border-green-300">
                  Required
                </div>
              )
            ) : props.item.required === true ? (
              <div className="bg-blue-100 text-blue-900 rounded-full px-1 text-xs w-max border border-green-300">
                Found!
              </div>
            ) : props.validateRequirementItemOptional(
                props.item,
                props.data
              ) === true ? (
              <div className="bg-gray-100 text-gray-900 rounded-full px-1 text-xs w-max border border-gray-300">
                Optional
              </div>
            ) : (
              <div
                onClick={() => props.addFile()}
                className="bg-white hover:border-blue-500 hover:bg-blue-500 cursor-pointer text-gray-900 hover:text-white rounded-md px-3 py-2 text-xs w-max border border-gray-300"
              >
                Optional, Click to add
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default OtherDocs;
