export const validateDocCategory = (
  requirement: PositionRequirements,
  userDocuments: DocumentUser[]
) => {
  if (
    userDocuments.find(
      (item) =>
        item.category_id === requirement.category.user_detail_category_id &&
        item.sub_category_id ===
          requirement.subcategory.user_detail_subcategory_id &&
        (item.validation_status === DocumentStatus.Approved ||
          item.validation_status === DocumentStatus.Waiting)
    )?.category_id === undefined &&
    requirement.required === true
  ) {
    return requirement.category.user_detail_category_id;
  } else {
    return null;
  }
};

