export const returnValidatePosition = (
  requirements: PositionRequirements[],
  documents: DocumentUser[]
) => {
  let status = true;

  const SingleOptionalDocuments = requirements.filter(
    (item) => item.position_requirement.length === 0 && item.required === false
  );
  const SingleRequiredDocuments = requirements.filter(
    (item) => item.position_requirement.length === 0 && item.required === true
  );
  const DoubleRequirements = requirements.filter(
    (item) => item.position_requirement.length > 0
  );

  // Check SingleOptionalDocuments
  if (
    SingleOptionalDocuments.find(
      (singleDoc) =>
        documents.find(
          (itm) =>
            itm.category_id === singleDoc.category.user_detail_category_id &&
            itm.sub_category_id ===
              singleDoc.subcategory.user_detail_subcategory_id &&
            (itm.validation_status === DocumentStatus.Approved ||
              itm.validation_status === DocumentStatus.Waiting) &&
            singleDoc.required === false
        ) !== undefined
    ) !== undefined
  ) {
    status = true;
  }

  // Check single required document
  if (
    SingleRequiredDocuments.find(
      (singleRequired) =>
        validateDocCategory(singleRequired, documents) !== null
    ) !== undefined
  ) {
    status = false;
    return status;
  }

  // Check filters DoubleRequirements
  let OptionalD: PositionSubcategoryData[] = [];
  let RequiredD: PositionSubcategoryData[] = [];
  for (let oneDoc of DoubleRequirements) {
    // when length > 0: chec optional

    const TestOptional = oneDoc.position_requirement.filter(
      (item) => item.required === false
    );
    const TestRequired = oneDoc.position_requirement.filter(
      (item) => item.required === true
    );
    OptionalD = [...OptionalD, ...TestOptional];
    RequiredD = [...RequiredD, ...TestRequired];
  }
  for (let opt of OptionalD) {
    const getCat = DoubleRequirements.find(
      (itm) =>
        itm.position_requirement.find((itm) => itm._id === opt._id) !==
        undefined
    );
    if (
      documents.find(
        (item) =>
          item.category_subcategory_data &&
          item.category_subcategory_data.category_subcategory_Data_id ===
            opt._id &&
          getCat !== undefined &&
          item.category_id === getCat.category.user_detail_category_id &&
          item.sub_category_id ===
            getCat.subcategory.user_detail_subcategory_id &&
          (item.validation_status === DocumentStatus.Approved ||
            item.validation_status === DocumentStatus.Waiting)
      ) !== undefined
    ) {
      status = true;
    }
  }

  for (let req of RequiredD) {
    const getCat = DoubleRequirements.find(
      (itm) =>
        itm.position_requirement.find((itm) => itm._id === req._id) !==
        undefined
    );
    if (
      documents.find(
        (item) =>
          item.category_subcategory_data &&
          item.category_subcategory_data.category_subcategory_Data_id ===
            req._id &&
          getCat !== undefined &&
          item.category_id === getCat.category.user_detail_category_id &&
          (item.sub_category_id === null ||
            item.sub_category_id ===
              getCat.subcategory.user_detail_subcategory_id) &&
          (item.validation_status === DocumentStatus.Approved ||
            item.validation_status === DocumentStatus.Waiting)
      ) === undefined
    ) {
      status = false;
      return status;
    }
  }
  return status;
};

export const returnValidatePositionOptional = (
  requirements: PositionRequirements[],
  documents: DocumentUser[]
) => {
  let status = true;
  for (let i in requirements) {
    if (requirements[i].position_requirement.length === 0) {
      if (
        (requirements[i] !== null &&
          documents !== null &&
          validateDocCategory(requirements[i], documents) !== null &&
          validateDocCategory(requirements[i], documents) !== undefined) ||
        validateDocCategoryRequired(requirements[i], documents) !== null
      ) {
        status = false;
      }
    } else {
      if (
        validateDocSubcategoryOptional(requirements[i], documents).length !==
          0 &&
        validateDocSubcategoryOptional(requirements[i], documents).length ===
          requirements[i].position_requirement.filter(
            (exc) => exc.required === false
          ).length
      ) {
        status = false;
      }
    }
  }
  return status;
};
