import React, { useState } from "react";
import { MdCheckCircle, MdRadioButtonUnchecked } from "react-icons/md";
import {
  ApplicationPeriodGet,
  PlanItem,
  SESSION_PLAN_KEYS,
  SESSION_PLAN_STATUS,
} from "../../interfaces/application";
import Loading from "../Loading/Loading";
import searchData from "../../utils/search";
import { DATE } from "../../utils/functions";

interface ApplicationPeriodsProps {
  applicationPeriods: ApplicationPeriodGet[] | null;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  showPeriodFilter: (status: boolean) => void;
}

export const FindPlan = (plans: PlanItem[], searchKey: SESSION_PLAN_KEYS) => {
  let temp = plans.find((itm) => itm.type_key === searchKey);
  return temp !== undefined ? temp : null;
};

export const FindActiveInPlans = (plans: PlanItem[]): PlanItem[] => {
  let today = new Date();
  let activePlans: PlanItem[] = [];
  for (let plan of plans) {
    let startDate = new Date(plan.start_date);
    let endDate = new Date(plan.end_date);
    if (
      startDate.getTime() <= today.getTime() &&
      endDate.getTime() >= today.getTime() &&
      plan.type_status === SESSION_PLAN_STATUS.OPEN
    ) {
      activePlans = [...activePlans, plan];
    }
  }
  return activePlans;
};

const ApplicationPeriods = (props: ApplicationPeriodsProps) => {
  const [selectedPlan, setSelectedPlan] = useState<ApplicationPeriodGet | null>(
    null
  );
  const [search, setSearch] = useState<string>("");
  const getSearch = (
    period: ApplicationPeriodGet[],
    searched: string
  ): ApplicationPeriodGet[] => {
    return searchData(period, searched) as ApplicationPeriodGet[];
  };

  return (
    <div className="">
      <div className="px-3">
        <input
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setSearch(e.target.value)
          }
          type="text"
          className="py-3 px-4 w-full rounded border border-gray-400 mb-3"
          placeholder="Search by year, name"
        />
      </div>

      <div className="bg-gray-100 px-3 pt-3 pb-2 rounded-b">
        {props.applicationPeriods === null ? (
          <Loading />
        ) : (
          getSearch(props.applicationPeriods, search).map((item, i) => (
            <div
              key={i + 1}
              className={`rounded border ${
                item.is_active === true
                  ? "bg-white border-white"
                  : "bg-white border-white "
              } group hover:border-blue-500 px-2 py-2 mb-2`}
            >
              <div className="flex flex-col md:flex-row items-start md:items-center md:justify-between ">
                <div className="flex flex-row items-center gap-3">
                  {item.is_active === true ? (
                    <MdCheckCircle className={`text-blue-500 text-4xl`} />
                  ) : (
                    <MdRadioButtonUnchecked
                      className={`text-gray-300 text-4xl`}
                    />
                  )}
                  <div className="flex flex-col">
                    <div
                      className={`${
                        item.is_active === true
                          ? "text-blue-600"
                          : "text-gray-600"
                      } font-bold`}
                    >
                      {item.session_plan_name}
                    </div>
                    <div className="text-sm text-gray-500">
                      <span>Status: </span>
                      <span
                        className={`${
                          item.is_active === true
                            ? "text-gray-900 font-bold underline"
                            : "text-gray-500 italic"
                        }`}
                      >
                        {item.is_active === true ? "Active" : "Expired"}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="flex flex-row items-center gap-2">
                  {(selectedPlan === null || selectedPlan._id !== item._id) && (
                    <div
                      onClick={() => setSelectedPlan(item)}
                      className="bg-gray-50 group-hover:bg-blue-100 font-semibold text-gray-400 group-hover:text-blue-600 rounded px-3 py-2 cursor-pointer"
                    >
                      Details
                    </div>
                  )}
                  <div
                    onClick={() => {
                      props.setSelectedPeriod(item);
                      props.showPeriodFilter(false);
                    }}
                    className="bg-gray-50 group-hover:bg-blue-600 font-semibold text-gray-400 group-hover:text-white rounded px-3 py-2 cursor-pointer"
                  >
                    Select
                  </div>
                  {selectedPlan !== null && selectedPlan._id === item._id && (
                    <div
                      onClick={() => setSelectedPlan(null)}
                      className="bg-red-100 group-hover:bg-red-200 font-semibold text-red-600 group-hover:text-red-800 rounded px-3 py-2 cursor-pointer"
                    >
                      Close
                    </div>
                  )}
                </div>
              </div>
              {selectedPlan !== null && selectedPlan._id === item._id && (
                <div className="bg-white rounded border border-blue-50 mt-2 animate__animated animate__fadeIn">
                  <table className="w-full">
                    <thead className="bg-blue-50">
                      <tr className="text-left">
                        <th className="px-3 py-3">#</th>
                        <th>Plan</th>
                        <th>Start date</th>
                        <th>Ending date</th>
                        <th>Status</th>
                      </tr>
                    </thead>
                    <tbody className="text-sm">
                      {selectedPlan !== null &&
                        selectedPlan._id === item._id &&
                        selectedPlan.plans.length > 0 &&
                        item.plans.map((plan, i) => (
                          <tr
                            key={i + 1}
                            className={`${
                              FindActiveInPlans(item.plans).find(
                                (itm) => itm.type_key === plan.type_key
                              ) !== undefined
                                ? "bg-blue-100 text-blue-600 font-bold"
                                : ""
                            } text-left border-b border-blue-50`}
                          >
                            <td className="px-3 py-1">{i + 1}</td>
                            <td>{plan.type_title}</td>
                            <td>{DATE(plan.start_date, "DD/MM/YYYY")}</td>
                            <td>{DATE(plan.end_date, "DD/MM/YYYY")}</td>
                            <td>{plan.type_status}</td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default ApplicationPeriods;
